<template>
  <el-row>
    <el-col :span="24" :offset="0">
      <el-form-item label="">
        {{ DeviceName || dname }}
      </el-form-item>
    </el-col>
    <el-col :span="12" :offset="0">
      <el-form-item label="ip">
        {{ DeviceNum || dnum }}
      </el-form-item>
    </el-col>
    <el-col :span="12" :offset="0">
      <el-form-item label="">
        {{ getMapByNum(snum).sname }}
      </el-form-item>
    </el-col>
    <el-col :span="12" :offset="0">
      <el-form-item label="">
        {{ UserName }}
      </el-form-item>
    </el-col>
    <el-col :span="12" :offset="0">
      <el-form-item label="">
        ******
      </el-form-item>
    </el-col>
    <el-col :span="12" :offset="0">
      <el-form-item label="">
        {{ ChanelNum }}
      </el-form-item>
    </el-col>
    <el-col :span="12" :offset="0">
      <el-form-item label="">
        {{ IsYunTai ? "" : "" }}
      </el-form-item>
    </el-col>
    <el-col :span="12" :offset="0">
      <el-form-item label="x">
        {{ x }}
      </el-form-item>
    </el-col>
    <el-col :span="12" :offset="0">
      <el-form-item label="y">
        {{ y }}
      </el-form-item>
    </el-col>
  </el-row>
</template>

<script lang="ts">
import { defineComponent, reactive, toRefs, watch } from "vue";
import { getMapByNum } from "@/utils/auth";
export default defineComponent({
  name: "CameraItem",
  props: {
    deviceData: {
      type: Object
    }
  },
  setup(props) {
    const form = reactive({
      DeviceName: "",
      DeviceNum: "",
      dname: "",
      dnum: "",
      bnum: "",
      snum: "",
      UserName: "",
      Password: "",
      ChanelNum: 0,
      IsYunTai: false,
      IsCameraPhoto: "",
      x: "",
      y: ""
    });

    watch(
      () => props.deviceData,
      newVal => {
        Object.assign(form, newVal);
      },
      {
        immediate: true,
        deep: true
      }
    );

    return {
      ...toRefs(form),
      getMapByNum
    };
  }
});
</script>
