import { Group } from "konva/types/Group";
import Konva from "konva";
import { DEFAULT_COLORS } from "../constVariables";

/**
 * @description: shape Image
 * @param {*}
 * @return {*}
 */
export function generatePersonGroup(
  group: Group,
  imageDom: HTMLImageElement,
  id: number | string,
  name: string
) {
  const image = new Konva.Image({
    width: imageDom.width,
    height: imageDom.height,
    image: imageDom,
    shadowColor: DEFAULT_COLORS.TRANSPARENT,
    shadowBlur: 20,
    id: String(id) //   shapegroup  parent
  });
  const fontSize = 14;
  const rectHeight = imageDom.height * 0.4;
  const textRect = new Konva.Rect({
    width: imageDom.width,
    height: rectHeight,
    y: -rectHeight - 5,
    fill: "rgba(0,0,0,.7)",
    cornerRadius: rectHeight
  });
  const text = new Konva.Text({
    // width:imageDom.width,
    // height:rectHeight,
    x: imageDom.width / 2,
    y: -fontSize - 5,
    text: name,
    fontSize,
    fill: "#fff",
    id: String(id)
  });
  text.offsetX(text.width() / 2);
  text.offsetY(text.height() / 2 - 5 / 2);

  group
    .add(image)
    .add(textRect)
    .add(text);
  return image;
}
