<template>
  <div>
    <el-form :model="params" label-width="80px" :inline="true" size="small">
      <el-form-item label="">
        <el-input v-model.trim="params.dnum" clearable @change="search" />
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="search"></el-button>
      </el-form-item>
    </el-form>
    <hd-table :data="tableData" v-loading="loading">
      <hd-table-column label="" prop="dnum" width="110" />
      <hd-table-column label="" prop="dname" />
      <hd-table-column label="" prop="dtype" />
      <el-table-column label="" show-overflow-tooltip align="center">
        <template #default="{ row }">
          <div v-show="row.voltage < 3.55 || !row.voltage" style="color:#f00;">
            <span class="icon iconfont">&#xe62a;</span>
          </div>
          <div
            v-show="row.voltage >= 3.55 && row.voltage < 3.65"
            style="color:#0f0;"
          >
            <img src="/images/power.png" style="width:16px" alt="" />
          </div>
          <div v-show="row.voltage >= 3.65" style="color:#0f0;">
            <span class="icon iconfont">&#xe62f;</span>
          </div>
        </template>
      </el-table-column>
      <el-table-column
        width="180"
        label=""
        show-overflow-tooltip
        align="center"
      >
        <template #default="{ row }">
          <span v-if="row.snum" style="color: #3dd1ff">
            {{ getMapByNum(row.snum).sname }}
          </span>
          <span v-if="!row.snum">
            
          </span>
        </template>
      </el-table-column>
      <el-table-column width="120" label="" align="center">
        <template #default="{ row }">
          <el-button size="mini" type="primary" @click="handleDetail(row)"
            ></el-button
          >
        </template>
      </el-table-column>
    </hd-table>
    <el-pagination
      v-show="total && tableData && tableData.length"
      background
      v-model:current-page="params.page"
      layout="total, prev, pager, next, jumper"
      :total="total"
      @current-change="changePage"
    />
  </div>
</template>

<script lang="ts">
import {
  onMounted,
  reactive,
  ref,
  defineComponent,
  shallowRef,
  PropType
} from "vue";
import commonApi from "@/api/common";
import { TYPES } from "../constVariables";
import { IDeviceItem } from "../types";
import { IPersonArrItem } from "../types/index";
import { getMapByNum } from "@/utils/auth";
import hdTableColumn from "@/components/hd/hd-table-column.vue";
const CLICKED = "clicked";

export default defineComponent({
  components: { hdTableColumn },
  name: "Label",
  props: {
    wsAllLabel: {
      type: Array as PropType<IPersonArrItem[]>
    }
  },
  emits: ["showEquipDetails", CLICKED],
  setup(props, { emit }) {
    const params = reactive({
      page: 1,
      pagesize: 10,
      dtype: "",
      dnum: ""
    });
    const loading = shallowRef<boolean>(false);

    const tableData = ref<IDeviceItem[]>([]);
    const total = shallowRef<number>(0);
    // 
    async function getList() {
      loading.value = true;
      const param = {
        act: "mKQQKGa49xhcOWaBjDI1612407197459",
        curpage: params.page,
        // eslint-disable-next-line @typescript-eslint/camelcase
        sys_showpagesize: params.pagesize,
        dtype: params.dtype,
        dnum: params.dnum
      };
      const res = await commonApi.getCommonApi(param);
      const { data } = res as any;
      data.content.forEach((item: any) => {
        if (props.wsAllLabel) {
          props.wsAllLabel.forEach(element => {
            if (element.TagId === item.dnum) {
              item.snum = element.LocationSceneId;
            }
          });
        }

        if (item.eattribute) {
          const attrs = item.eattribute.split(",");
          attrs.forEach((elem: any) => {
            const attrName = elem.split("|");
            const arr = [
              "status", // 
              "hearttime", // 
              "currentalarm", // 
              "voltage", // 
              "bracelet_voltage" // 
            ];
            arr.forEach(prop => {
              if (!item[prop] && attrName[0] === prop) {
                item[prop] = attrName[1].split(":")[1];
              }
            });
          });
        }
      });
      tableData.value = data.content;
      console.log("labelList:", data.content);
      total.value = data.count;
      loading.value = false;
    }
    // 
    function handleDetail(row: IDeviceItem) {
      emit("showEquipDetails", row, TYPES.labelList);
      emit(CLICKED);
    }
    // 
    async function changePage(page: number) {
      params.page = page;
      getList();
    }
    // 
    async function search() {
      params.page = 1;
      getList();
    }

    onMounted(() => {
      getList();
    });

    return {
      params,
      loading,
      tableData,
      total,
      changePage,
      search,
      getMapByNum,
      handleDetail
    };
  }
});
</script>
