
import { defineComponent, shallowRef } from "vue";
import { NAV_INFO, FENCE, TYPES, ALARM, DATAV } from "../constVariables";
import { INavItem } from "../types/mainNav";
export default defineComponent({
  name: "MainNav",
  emits: ["navClick"],
  setup(props, { emit }) {
    const navList: INavItem[] = [
      { name: "", icon: "&#xe61d;", code: "index" },
      // {
      //   name: '',
      //   icon: '&#xe638;',
      //   path: '/home/attendance',
      //   class: ''
      // },
      {
        icon: "&#xe61a;",
        ...FENCE
      },
      // {
      //   name: '',
      //   icon: '&#xe667;',
      //   path: '/home/arealist',
      //   class: ''
      // },
      // 
      {
        icon: "&#xe7e8;",
        ...NAV_INFO[TYPES.camera]
      },
      // 
      {
        icon: "&#xe603;",
        ...NAV_INFO[TYPES.basestation]
      },
      // {
      //   name: '',
      //   icon: '&#xe624;',
      //   path: '/home/alarmlist',
      //   class: ''
      // },
      // 
      {
        icon: "&#xe6d8;",
        ...NAV_INFO[TYPES.labelList]
      },
      // 
      {
        icon: "&#xe60d;",
        ...NAV_INFO[TYPES.person]
      },

      // 
      {
        icon: "&#xe624;",
        ...ALARM
      },
      // 
      {
        icon: "&#xe61e;",
        ...DATAV
      }

      //
      // {name: "", icon: "&#xe619;", path: "/home/gzyj", class: ""},
      // {
      //   name: '',
      //   icon: '&#xe61e;',
      //   path: '/home/tracklist',
      //   class: ''
      // },
      // {
      //   name: '',
      //   icon: '&#xe62b;',
      //   path: '/home/tool',
      //   class: ''
      // },
      // {
      //   name: '',
      //   icon: '&#xe629;',
      //   path: '/home/car',
      //   class: ''
      // },
      // {
      //   name: "",
      //   icon: "&#xe62e;",
      //   path: "/home/analysis",
      //   class: ""
      // },
      // {
      //   name: "",
      //   icon: "&#xe627;",
      //   code: "warn"
      // }
    ];

    const navIndex = shallowRef(0);

    function handleNavClick(navItem: INavItem, index: number) {
      if (navIndex.value !== index) {
        navIndex.value = index;
      }
      const { code } = navItem;
      if (!code) return;
      emit("navClick", code);
    }
    function resetIndex() {
      navIndex.value = 0;
    }
    return {
      navList,
      navIndex,
      handleNavClick,
      resetIndex
    };
  }
});
