<template>
  <el-row>
    <el-col :span="12" :offset="0">
      <el-form-item label="">
        {{ Username }}
      </el-form-item>
    </el-col>
    <el-col :span="12" :offset="0"
      ><el-form-item label="">
        {{ DeviceName || "" }}
      </el-form-item></el-col
    >

    <el-col :span="12" :offset="0">
      <el-form-item label="">
        {{ Personnel_category }}
      </el-form-item>
    </el-col>
    <el-col :span="12" :offset="0">
      <el-form-item label="">
        {{ Position }}
      </el-form-item>
    </el-col>
    <el-col :span="12" :offset="0">
      <el-form-item label="">
        {{ Fettle === 0 ? "" : "" }}
      </el-form-item>
    </el-col>
  </el-row>
</template>

<script lang="ts">
import { defineComponent, reactive, toRefs, watch } from "vue";
export default defineComponent({
  name: "PersonItem",
  props: {
    deviceData: {
      type: Object
    }
  },
  setup(props) {
    const form = reactive({
      Username: "",
      DeviceName: "",
      Photo: "",
      src: "",
      // eslint-disable-next-line @typescript-eslint/camelcase
      Personnel_category: "",
      Position: "",
      Fettle: "",
      BraceletVoltage: ""
    });

    watch(
      () => props.deviceData,
      newVal => {
        Object.assign(form, newVal);
      },
      {
        immediate: true,
        deep: true
      }
    );

    return {
      ...toRefs(form)
    };
  }
});
</script>
