<template>
  <ul class="fence-box">
    <li
      v-for="(item, index) in list"
      :key="index"
      @click="handleClick(item, index)"
    >
      {{ item.label }}
    </li>
  </ul>
</template>

<script>
export default {
  name: "FenceBox",
  data() {
    return {
      list: [
        {
          label: ""
        },
        {
          label: ""
        }
      ]
    };
  },
  methods: {
    handleClick(item, index) {
      const emitObj = {
        0: "cancel",
        1: "finish"
      };

      this.$emit(emitObj[index]);
    }
  }
};
</script>
