
import { defineComponent, reactive, toRefs, watch } from "vue";
import { getMapByNum } from "@/utils/auth";
export default defineComponent({
  name: "CameraItem",
  props: {
    deviceData: {
      type: Object
    }
  },
  setup(props) {
    const form = reactive({
      DeviceName: "",
      DeviceNum: "",
      dname: "",
      dnum: "",
      bnum: "",
      snum: "",
      UserName: "",
      Password: "",
      ChanelNum: 0,
      IsYunTai: false,
      IsCameraPhoto: "",
      x: "",
      y: ""
    });

    watch(
      () => props.deviceData,
      newVal => {
        Object.assign(form, newVal);
      },
      {
        immediate: true,
        deep: true
      }
    );

    return {
      ...toRefs(form),
      getMapByNum
    };
  }
});
