
import { TYPES } from "../constVariables";
import { defineComponent, PropType, reactive, ref, watch } from "vue";
import { TShapeType } from "../types";
import { labelItem, personItem, basestationItem, cameraItem } from "./formItem";
import { cloneDeep } from "lodash";

export default defineComponent({
  name: "FormContent",
  props: {
    formType: {
      type: String as PropType<TShapeType>,
      default: TYPES.person
    },
    x: { type: Number, default: 0 },
    y: { type: Number, default: 0 },
    formData: {
      type: Object,
      default: () => ({})
    }
  },
  components: { labelItem, personItem, basestationItem, cameraItem },
  setup(props) {
    const formContentRef = ref();

    const form = reactive({
      // 
      roomNum: "1",
      src: "/images/staff_big.png",
      // 
      dname: "",
      dnum: "",
      x: "",
      y: "",
      dtype: "",
      status: "",
      hardwarever: "",
      version: "",
      starttype: 0,
      checkState: 0,
      uwbr4checking: 0,
      uwbt3checking: 0,
      rfidchecking: 0,
      pattern: "",
      // 
      currentalarm: "", // 
      voltage: 0, // 
      // eslint-disable-next-line @typescript-eslint/camelcase
      bracelet_voltage: 0, // 
      // 
      UserName: "",
      Password: "",
      ChanelNum: 0,
      IsYunTai: false,
      IsCameraPhoto: "",

      AlarmMappingLayerIdS: "",
      BrandCode: "",
      BrandName: "",
      BuildingName: "",
      BuildingNum: "",
      CreateTime: "",
      DeviceName: "",
      DeviceNum: "",
      DeviceObjProperty: "",
      DeviceSelfPointRelation: "",
      DeviceType: "",
      DeviceTypeCode: "",
      InstallSceneNum: "",
      IsUse: true,
      OwnAlarmSourceCode: "",
      PDeviceIds: "",
      SceneName: "",

      // 
      shapeName: "",
      desc: ""
    });
    const formDatas = cloneDeep(form);
    const types = TYPES;

    watch(
      () => props.formData,
      newVal => {
        if (newVal) {
          console.log("newVal", newVal);
          Object.assign(form, formDatas);
          Object.assign(form, newVal);
          form.x = String(props.x) !== "0" ? String(props.x) : form.x;
          form.y = String(props.y) !== "0" ? String(props.y) : form.y;
        }
      },
      {
        immediate: true,
        deep: true
      }
    );

    watch(
      () => props.x,
      newX => {
        console.log("x", newX);
        if (!isNaN(newX)) {
          form.x = String(newX);
        }
      }
    );
    watch(
      () => props.y,
      newY => {
        if (!isNaN(newY)) {
          form.y = String(newY);
        }
      }
    );

    return {
      formContentRef,
      form,
      types
    };
  }
});
