import http from "@/utils/http3";

export default {
  //  
  getAllAlarmType(data: any) {
    return http({
      url: "/tbs/getalarmtype",
      method: "post",
      data
    });
  },
  // 
  dealAlarmItem(data: any) {
    return http({
      url: "/alarm/deal",
      method: "post",
      data
    });
  },
  // 
  dealAlarms(data: any) {
    return http({
      url: "/alarm/dealall",
      method: "post",
      data
    });
  }
};
