    // 
    let deviceAll = reactive<IDeviceItem[]>([]);

    async function addLayer() {
      (stage as Stage).add(layer).add(bgLayer).add(operationLayer);

      Object.keys(allGroup).forEach((key) => {
        layer.add(allGroup[key]);
      });

      bgLayer.moveToBottom();
      allGroup.fenceGroup.moveToBottom();
      operationLayer.moveToTop();
    }

    async function initAlarmIcon(attrs: { x: number; y: number; id: number }) {
      const { x, y, id } = attrs;
      const alarmIcon = new Konva.Circle({
        x: x,
        y: y,
        radius: 70,
        fill: "rgba(255,0,0,.7)",
      });
      cacheAlarmObj[id] = alarmIcon;
      const period = 5000;
      allGroup.alarmGroup.add(alarmIcon);
      let anim: null | Animation = new Konva.Animation((frame) => {
        const scale = Math.sin(((frame as IFrame).time * 2 * Math.PI) / period) + 0.001;
        alarmIcon.scale({ x: scale, y: scale });
      }, layer);
      (anim as Animation).start();
      setTimeout(() => {
        (anim as Animation)?.stop();
        anim = null;
        cacheAlarmObj[id].destroy();
        delete cacheAlarmObj[id];
        batchDraw();
      }, 3000);
    }

    async function initBasestation(attrs: TLayerItem, show = controlButtonList[0].show) {
      const { x, y, lids } = attrs;
      // todo deviceitem 
      // const deviceArr = JSON.parse(deviceitem) as ISetDeviceDataItem[];
      console.log("base attrs", attrs);
      const imageDom = await generateImageDomByType(attrs);

      const generate = () => {
        const shape = new Konva.Image({
          x,
          y,
          width: imageDom.width,
          height: imageDom.height,
          image: imageDom,
          shadowColor: DEFAULT_COLORS.TRANSPARENT,
          // shadowBlur: imageDom.width,
          shadowBlur: 20,
          name: lids,
        });

        shape.on("contextmenu", (e) => {
          console.log(e);
          if (status.isDrawFence) {
            return false;
          }

          hideDetails();

          activeTarget.attrs = cloneDeep(e.target.attrs);
          currentContextmenu = shape;

          contextmenuTypeRef.value = TYPES.basestation;
          showMenuList(e.evt);

          shape.shadowColor(DEFAULT_COLORS.BLUE);
          batchDraw();
        });

        allGroup.basestationGroup.add(shape);
        cacheBasestationObj[lids] = shape;
        shape.visible(show);
        batchDraw();
      };

      loadImage(imageDom, generate);
    }

    async function initCamera(attrs: TLayerItem, show = controlButtonList[1].show) {
      const { x, y, lids, deviceitem } = attrs;
      const imageDom = await generateImageDomByType(attrs);

      const deviceArr = JSON.parse(deviceitem) as ISetDeviceDataItem[];

      const shape = new Konva.Image({
        x,
        y,
        width: imageDom.width,
        height: imageDom.height,
        image: imageDom,
        shadowColor: DEFAULT_COLORS.TRANSPARENT,
        // shadowBlur: imageDom.width,
        shadowBlur: 20,
        // ididdeviceAll
        name: deviceArr[0].DeviceIdS,
      });

      shape.on("contextmenu", (e) => {
        console.log(e);
        if (status.isDrawFence) {
          return false;
        }

        hideDetails();

        activeTarget.attrs = cloneDeep(e.target.attrs);

        contextmenuTypeRef.value = TYPES.camera;
        currentContextmenu = shape;

        showMenuList(e.evt);
        visibleObj.contextmenu = true;

        shape.shadowColor(DEFAULT_COLORS.BLUE);
        batchDraw();
      });

      allGroup.cameraGroup.add(shape);
      cacheCameraObj[lids] = shape;
      shape.visible(show);
      batchDraw();
    }

    async function initBubble(attrs: TVectorgraph) {
      const offsetX = 24;
      const offsetY = 16;
      const OffsetX = 2 * offsetX;
      const OffsetY = 2 * offsetY;
      const { x, y, sname, lids } = attrs;

      const { W, H, layinfo, Color, IsHaveCount, CountItem } = attrs.defdata;
      const w = W - OffsetX;
      const h = H - OffsetY;
      const group = new Konva.Group({
        x,
        y,
      });

      const config: ISvgToImageConfig = {
        pathSrc: layinfo,
        width: W,
        height: H,
        color: Color,
      };

      const imageDom = await svgToImage(config, attrs.ltype === "");
      const image = new Konva.Image({
        image: imageDom,
        name: lids,
        // listening:false,
        // stroke: "rgba(255,0,0,.5)",
      });

      if (!IsHaveCount) {
        const label = new Konva.Text({
          x: W / 2 - offsetX,
          y: (h - 10) / 2,
          text: sname,
          fontSize: 14,
          fill: "#fff",
          listening: false,
        });
        label.offsetX(label.width() / 2);
        label.offsetY(label.height() / 2);
        group.add(image).add(label);
      } else {
        const label = new Konva.Text({
          x: W / 2 - offsetX,
          y: H / 8,
          text: sname,
          fontSize: 14,
          fill: "#fff",
          listening: false,
        });
        label.offsetX(label.width() / 2);

        const line = new Konva.Line({
          points: [w * 0.1, h / 2, w * 0.9, h / 2],
          stroke: "#fff",
          strokeWidth: 0.5,
          listening: false,
        });

        const bottomGroup = new Konva.Group({
          x: w / 2,
          y: (h - 10) * 0.7,
          listening: false,
        });

        cacheBubbleObj[lids] = {
          text: [],
          tagList: [],
        };

        const len = CountItem.length;
        const gap = 10;

        CountItem.forEach((item, index) => {
          const cornerGroup = new Konva.Group({
            x: gap * index,
            listening: false,
          });

          const cornerH = ((h - 10) / 2) * 0.5;
          const cornerRect = new Konva.Rect({
            width: w / 3 / len,
            height: cornerH,
            fill: "rgba(255,255,255,.6)",
            cornerRadius: cornerH,
            listening: false,
          });
          cornerRect.offsetX(cornerRect.width() / 2);

          const cornerText = new Konva.Text({
            x: 0,
            y: cornerH / 2,
            fill: Color,
            text: "?",
            name: item,
            listening: false,
          });
          cornerText.offsetX(cornerText.width() / 2);
          cornerText.offsetY(cornerText.height() / 2);

          cornerGroup.add(cornerRect).add(cornerText);

          cacheBubbleObj[lids].text.push(cornerText);

          bottomGroup.add(cornerGroup);
        });

        group.add(image).add(label).add(line).add(bottomGroup);

        group.on("dblclick", (e) => {
          console.log(e);
          const { clientX, clientY } = e.evt;
          Object.assign(layerPosition, {
            left: clientX,
            top: clientY - titleHeight,
          });
          //   
          layerPerson.value = cacheBubbleObj[e.target.attrs.name].tagList[0];
          console.log("layerPerson.value", layerPerson.value);

          visibleObj.layerPerson = true;
        });
      }

      allGroup.locateBubbleGroup.add(group);
    }

    // 
    function initArea(attrs: IAreaItem) {
      const { areaids, points, areaname, show } = attrs;
      const posArr = JSON.parse(points) as IUpperPos[];
      const group = new Konva.Group({
        visible: show,
      });
      const startPoint = posArr[0];
      const text = new Konva.Text({
        x: startPoint.X + 10,
        y: startPoint.Y + 10,
        text: areaname,
        fill: "#fff",
        fontSize: 24,
      });
      const areaItem = new Konva.Shape({
        ...areaColorConfig,
        sceneFunc: function (ctx, shape) {
          ctx.beginPath();
          ctx.moveTo(posArr[0].X, posArr[0].Y);
          for (let i = 1; i < posArr.length; i++) {
            const { X, Y } = posArr[i];
            ctx.lineTo(X, Y);
          }
          ctx.stroke();
          ctx.closePath();
          ctx.fillStrokeShape(shape);
        },
      });
      group.add(areaItem).add(text);
      //   
      if (cacheAreaObj[areaids]) {
        cacheAreaObj[areaids].destroy();
        delete cacheAreaObj[areaids];
      }
      allGroup.fenceGroup.add(group);
      cacheAreaObj[areaids] = group;

      batchDraw();
    }

