    async function initPerson(attrs: IPersonArrItem, initType = 0) {
      const { x, y, name, TagId } = attrs;

      if (cachePersonObj[TagId] && initType === 0) {
        return;
      }

      const imageDom = new window.Image();
      imageDom.src = `/images/staff_unchecked${initType + 1}.png`;
      imageDom.onload = () => {
        const group = new Konva.Group({
          x: x + imageDom.width / 2,
          y: y + imageDom.height,
        });
        if (personImageWH.width === 0) {
          personImageWH.width = imageDom.width;
          personImageWH.height = imageDom.height;
        }
        const image = generatePersonGroup(group, imageDom, TagId, name);
        if (initType === 0) {
          cachePersonObj[TagId] = group;
        } else {
          cachePlaybackPerson = group;
        }

        // 
        if (allGroup.personGroup && initType === 0) {
          allGroup.personGroup.add(group);
        } else if (operationLayer && initType) {
          operationLayer.add(group);
          group.moveToTop();
        }

        if (initType === 0) {
          group.on("contextmenu", (e) => {
            if (status.isPlayBack) {
              ElMessage.info("");
              return;
            }
            if (status.isFollowing) {
              ElMessage.info("");
              return;
            }
            console.log(e);
            console.log(e.target.attrs.id);
            if (status.isDrawFence) {
              return false;
            }
            activeTarget.attrs = cloneDeep(e.target.attrs);
            hideDetails();

            currentContextmenu = group;
            contextmenuTypeRef.value = TYPES.labelList;
            showMenuList(e.evt);
            visibleObj.contextmenu = true;
            image.shadowColor(DEFAULT_COLORS.BLUE);
            batchDraw();
          });
        } else {
          status.isPlayBack = true;
          trackPlayback.message = ElMessage({
            // showClose: true,
            message: "...",
            type: "warning",
            duration: 0,
          });
          // eslint-disable-next-line @typescript-eslint/no-use-before-define
          trackPlaybackTween(group, trackPlayback.steps);
        }
        batchDraw();
      };
    }

