    // 
    async function alarmHandleSubmit() {
      const handleAlarmId = alarmInfo.handleAlarmId;
      const handleAlarmIdArr = alarmInfo.handleAlarmIdArr;
      const handleType = alarmInfo.form.handleType;
      const handleContent = alarmInfo.form.handleContent;
      if (handleAlarmId) {
        // 
        const params = {
          sIDS: handleAlarmId,
          sDealAccount: getUsername(),
          sDealResult: handleContent,
          sDealType: handleType,
          sDealIp: "127.0.0.1",
        };
        // 
        const res: unknown = await alarmApi.dealAlarmItem(params);
        const { success, msg } = res as any;
        if (success) {
          alarmInfo.handleVisible = false;
          ElMessage.success(msg);
          // 
          alarmListInfo.handleIds = [handleAlarmId];
          alarmListInfo.tableData.forEach((item: any) => {
            if (item.IDS === handleAlarmId) {
              item.alarmProgress = "";
            }
          });
          getAllAlarm();
        } else {
          ElMessage.error(msg);
        }
      } else {
        // 
        const params = {
          sIDSChar: handleAlarmIdArr,
          sDealAccount: getUsername(),
          sDealResult: handleContent,
          sDealType: handleType,
          sDealIp: "127.0.0.1",
        };
        // 
        const res: unknown = await alarmApi.dealAlarms(params);
        const { success, msg } = res as any;
        if (success) {
          alarmInfo.handleVisible = false;
          ElMessage.success(msg);
          // 
          alarmListInfo.handleIds = handleAlarmIdArr.split(",");
          alarmListInfo.tableData.forEach((item: any) => {
            alarmListInfo.handleIds.forEach((val: any) => {
              if (item.IDS === val) {
                item.alarmProgress = "";
              }
            });
          });
          getAllAlarm();
        } else {
          ElMessage.error(msg);
        }
      }
    }
