    //  
    function updatePoint(index: number) {
      trackPlaybackLinePos = [];
      const steps = cloneDeep(trackPlayback.steps).splice(0, index);

      steps.forEach((item) => {
        const { x, y } = strToPos(item.LocatePoint);
        trackPlaybackLinePos.push(x + personImageWH.width / 2, y + personImageWH.height);
      });
    }
    /**  */
    function changeProcess(index: number) {
      playbackObj.currentIndex = index;
      // 
      // 
      trackPlaybackLinePos = [];

      // 
      if (index !== 0) {
        updatePoint(index);
      }

      togglePlay(true);
    }

    // 
    function playbackClose() {
      trackPlayback.tween = null;
      trackPlayback.message?.close();
      trackPlayback.steps = [];
      trackPlayback.lineNode?.destroy();
      trackPlaybackLinePos = [];
      cachePlaybackPerson?.destroy();
      status.isPlayBack = false;
    }

    // 
    function initMoveLine() {
      trackPlayback.lineNode?.destroy();
      trackPlayback.lineNode = new Konva.Line({
        points: trackPlaybackLinePos,
        stroke: "rgba(0,0,0,.8)",
        strokeWidth: 3,
        lineCap: "round",
        lineJoin: "round",
        name: "moveLine",
      });
      operationLayer.add(trackPlayback.lineNode);
      trackPlayback.lineNode.moveToTop();
      trackPlayback.lineNode.moveDown();
      batchDraw();
    }

    // 
    async function trackPlaybackTween(node: any, steps: ITrackItem[]) {
      // 
      if (!status.isPlayBack) {
        return;
      }
      // 
      if (steps.length - 1 === playbackObj.currentIndex) {
        playbackObj.isPlay = false;
        ElMessage.success("");
        return;
      }

      const { SceneNum } = steps[0];

      const animate = () => {
        const { LocatePoint, LocateTriggerTime } = steps[
          playbackObj.currentIndex
        ] as ITrackItem;
        playbackObj.currentTime = LocateTriggerTime.replace("T", " ");
        playbackObj.currentIndex++;
        const { x, y } = strToPos(LocatePoint);
        trackPlayback.tween = new Konva.Tween({
          node,
          duration: 0.2 / playRate,
          x,
          y,
          onUpdate: () => {
            const moveX = node.x() + personImageWH.width / 2;
            const moveY = node.y() + personImageWH.height;
            trackPlaybackLinePos.push(moveX, moveY);
            // trackPlaybackLinePos.push(node.x(), node.y());
            // 5 204 / 2 
            initMoveLine();
          },
          onFinish: () => {
            updatePoint(playbackObj.currentIndex);
            trackPlaybackTween(node, steps);
            node.moveToTop();
          },
        });
        trackPlayback.tween.play();
      };

      if (SceneNum === mapIdRef.value) {
        animate();
      } else {
        ElMessage.info("...");
        // eslint-disable-next-line @typescript-eslint/no-use-before-define
        await changeMap(SceneNum);
        await trackPlaybackTween(node, steps);
      }
    }

