    //    shape
    function destroyLayer(type: TDestoryType = "all") {
      const destroy = {
        layer: () => {
          // 
          allGroup.cameraGroup.destroyChildren();
          for (const key in cacheCameraObj) {
            delete cacheCameraObj[key];
          }
          // 
          allGroup.basestationGroup.destroyChildren();
          for (const key in cacheBasestationObj) {
            delete cacheCameraObj[key];
          }
          // 
          allGroup.locateBubbleGroup.destroyChildren();
          for (const key in cacheBubbleObj) {
            delete cacheBubbleObj[key];
          }
        },
        area: () => {
          allGroup.fenceGroup.destroyChildren();
          for (const key in cacheAreaObj) {
            delete cacheAreaObj[key];
          }
        },
        person: () => {
          allGroup.personGroup.destroyChildren();
          for (const key in cachePersonObj) {
            delete cachePersonObj[key];
          }
        },
      };
      if (type === "all") {
        Object.keys(destroy).map((func) => {
          destroy[func]();
        });
      } else {
        destroy[type]();
      }
    }

