
import { defineComponent, reactive, toRefs, watch } from "vue";
export default defineComponent({
  name: "PersonItem",
  props: {
    deviceData: {
      type: Object
    }
  },
  setup(props) {
    const form = reactive({
      Username: "",
      DeviceName: "",
      Photo: "",
      src: "",
      // eslint-disable-next-line @typescript-eslint/camelcase
      Personnel_category: "",
      Position: "",
      Fettle: "",
      BraceletVoltage: ""
    });

    watch(
      () => props.deviceData,
      newVal => {
        Object.assign(form, newVal);
      },
      {
        immediate: true,
        deep: true
      }
    );

    return {
      ...toRefs(form)
    };
  }
});
