import { ICommonRule, ICustomFuncRule } from "@/types";
import { cloneDeep } from "lodash";
// 
import dayjs from "dayjs";

export function computedRem() {
  const docEl = document.documentElement;
  let width = document.body.clientWidth || document.documentElement.clientWidth;
  width = width > 2400 ? 2400 : width;
  docEl.style.fontSize = width / 190 + "px";
}

/**
 * @description: rule
 * @param {string} msg 
 * @return {object} IRule
 */
export function commonRuleItem(msg: string): ICommonRule {
  return {
    required: true,
    message: msg,
    trigger: ["blur", "change"]
  };
}

/**
 * @description: 
 * @param {function} func 
 * @return {IRule} IRule
 */
export function customFuncRule(func: Function): ICustomFuncRule {
  return {
    required: true,
    validator: func,
    trigger: "blur"
  };
}
/**
 * @description: 
 */
export function checkPositiveInteger(
  rule: any,
  value: string,
  callback: Function
) {
  if (value === "0" || value === "") {
    return callback(new Error("0"));
  }
  if (!/(^[1-9]\d*$)/.test(value)) {
    callback(new Error(""));
  } else {
    callback();
  }
}
/**
 * @description: 
 * @param allArr 
 * @param page 
 * @param prop 
 * @param key 
 * @param size 
 * @return arr 
 */
export function getPagination(
  allArr: any[],
  page: number,
  prop = "",
  key = "",
  size = 10
) {
  // const len = arguments.length;
  if (prop || key) {
    allArr = cloneDeep(allArr.filter((item: any) => item[prop].includes(key)));
  }
  const arr = allArr.slice((page - 1) * size, (page - 1) * size + size);

  return arr;
}
/**
 * @description: 
 * @param allArr 
 * @param prop 
 * @param key 
 * @return count 
 */
export function getPagintionCount(allArr: any[], prop = "", key = "") {
  const len = arguments.length;
  if (len > 1) {
    allArr = cloneDeep(allArr.filter((item: any) => item[prop].includes(key)));
  }

  return allArr.length;
}
/**
 * @description: 
 * @param createtime 
 * @return diffTime 
 */
export function getIntervalTime(createtime: string) {
  const year = dayjs(new Date()).diff(dayjs(createtime), "year");
  const month = dayjs(new Date()).diff(dayjs(createtime), "month");
  const week = dayjs(new Date()).diff(dayjs(createtime), "week");
  const day = dayjs(new Date()).diff(dayjs(createtime), "day");
  const hour = dayjs(new Date()).diff(dayjs(createtime), "hour");
  const minute = dayjs(new Date()).diff(dayjs(createtime), "minute");
  const second = dayjs(new Date()).diff(dayjs(createtime), "second");
  let diffTime = "";
  if (year > 0) {
    diffTime = `${year}`;
  } else {
    if (month > 0 && month <= 12) {
      diffTime = `${month}`;
    } else {
      if (week > 0 && week <= 4) {
        diffTime = `${week}`;
      } else {
        if (day > 0 && day <= 7) {
          diffTime = `${day}`;
        } else {
          if (hour > 0 && hour <= 24) {
            diffTime = `${hour}`;
          } else {
            if (minute > 0 && minute <= 60) {
              diffTime = `${minute}`;
            } else {
              if (second > 0 && second <= 60) {
                diffTime = `${second}`;
              } else {
                diffTime = "";
              }
            }
          }
        }
      }
    }
  }
  return diffTime;
}
