<template>
  <nav>
    <ul>
      <li
        v-for="(navItem, index) in navList"
        :key="navItem.name"
        :class="{ active: index === navIndex }"
        @click="handleNavClick(navItem, index)"
      >
        <span class="icon iconfont" v-html="navItem.icon" />
        <div>{{ navItem.name }}</div>
      </li>
    </ul>
  </nav>
</template>

<script lang="ts">
import { defineComponent, shallowRef } from "vue";
import { NAV_INFO, FENCE, TYPES, ALARM, DATAV } from "../constVariables";
import { INavItem } from "../types/mainNav";
export default defineComponent({
  name: "MainNav",
  emits: ["navClick"],
  setup(props, { emit }) {
    const navList: INavItem[] = [
      { name: "", icon: "&#xe61d;", code: "index" },
      // {
      //   name: '',
      //   icon: '&#xe638;',
      //   path: '/home/attendance',
      //   class: ''
      // },
      {
        icon: "&#xe61a;",
        ...FENCE
      },
      // {
      //   name: '',
      //   icon: '&#xe667;',
      //   path: '/home/arealist',
      //   class: ''
      // },
      // 
      {
        icon: "&#xe7e8;",
        ...NAV_INFO[TYPES.camera]
      },
      // 
      {
        icon: "&#xe603;",
        ...NAV_INFO[TYPES.basestation]
      },
      // {
      //   name: '',
      //   icon: '&#xe624;',
      //   path: '/home/alarmlist',
      //   class: ''
      // },
      // 
      {
        icon: "&#xe6d8;",
        ...NAV_INFO[TYPES.labelList]
      },
      // 
      {
        icon: "&#xe60d;",
        ...NAV_INFO[TYPES.person]
      },

      // 
      {
        icon: "&#xe624;",
        ...ALARM
      },
      // 
      {
        icon: "&#xe61e;",
        ...DATAV
      }

      //
      // {name: "", icon: "&#xe619;", path: "/home/gzyj", class: ""},
      // {
      //   name: '',
      //   icon: '&#xe61e;',
      //   path: '/home/tracklist',
      //   class: ''
      // },
      // {
      //   name: '',
      //   icon: '&#xe62b;',
      //   path: '/home/tool',
      //   class: ''
      // },
      // {
      //   name: '',
      //   icon: '&#xe629;',
      //   path: '/home/car',
      //   class: ''
      // },
      // {
      //   name: "",
      //   icon: "&#xe62e;",
      //   path: "/home/analysis",
      //   class: ""
      // },
      // {
      //   name: "",
      //   icon: "&#xe627;",
      //   code: "warn"
      // }
    ];

    const navIndex = shallowRef(0);

    function handleNavClick(navItem: INavItem, index: number) {
      if (navIndex.value !== index) {
        navIndex.value = index;
      }
      const { code } = navItem;
      if (!code) return;
      emit("navClick", code);
    }
    function resetIndex() {
      navIndex.value = 0;
    }
    return {
      navList,
      navIndex,
      handleNavClick,
      resetIndex
    };
  }
});
</script>

<style lang="scss" scoped>
nav {
  width: 84rem;
  position: absolute;
  bottom: 2rem;
  left: 50%;
  transform: translateX(-50%);
  border: 1px solid rgba(51, 119, 233, 0.13);
  border-left: none;
  border-right: none;
  z-index: 1000;
}
ul {
  display: flex;
  justify-content: space-evenly;
  padding: 0 4rem;
  color: rgba(91, 169, 233, 0.8);
  background: url("../../../../assets/dashboard/nav_bg.png") repeat center
    center/100% 100%;
  li {
    /* flex: 1; */
    min-width: 8rem;
    padding: 1rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    transition: all 0.3s ease;

    &:hover,
    &.active {
      color: #33eaff;
      background: url("../../../../assets/dashboard/active.png") repeat center
        center/100% 100%;
    }

    span {
      font-size: 2.4rem;
    }
    div {
      margin-top: 1rem;
      font-size: 1.4rem;
    }
  }
}
</style>
