<template>
  <div>
    <svg-icon
      @click="click"
      :data="isFullscreen ? exitFullscreen : fullscreen"
    />
  </div>
</template>

<script lang="ts">
import { onMounted, onUnmounted, shallowRef, defineComponent } from "vue";
import screenfull from "screenfull";
import fullscreen from "@/icons/svg/fullscreen.svg";
import exitFullscreen from "@/icons/svg/exit-fullscreen.svg";
import { ElMessage } from "element-plus";

export default defineComponent({
  name: "screenfull",
  setup() {
    const isFullscreen = shallowRef<boolean>(false);
    const change = () => {
      isFullscreen.value = (screenfull as any).isFullscreen;
    };
    const init = () => {
      if (screenfull.isEnabled) {
        screenfull.on("change", change);
      }
    };

    const destroy = () => {
      if (screenfull.isEnabled) {
        screenfull.off("change", change);
      }
    };

    const click = () => {
      if (!screenfull.isEnabled) {
        ElMessage.warning("");
        return false;
      }
      screenfull.toggle();
    };

    onMounted(() => {
      init();
    });

    onUnmounted(() => {
      destroy();
    });

    return {
      isFullscreen,
      click,
      fullscreen,
      exitFullscreen
    };
  }
});
</script>
