<!--
 * @Description: 
 * @Author: liufuqiang
 * @Date: 2021-01-25 13:50:04
 * @LastEditors: liufuqiang
 * @LastEditTime: 2021-02-20 11:37:04
-->
<template>
  <div class="handle" @scroll.capture="lazyLoad($event)">
    <div class="handle-class">
      <el-button
        :class="flag == '' ? 'active' : ''"
        @click="handleStatus('')"
      >
        
      </el-button>
      <el-button
        :class="flag == '' ? 'active' : ''"
        @click="handleStatus('')"
      >
        
      </el-button>
    </div>
    <div class="danger-level">
      <el-button :class="status[3]" @click="handleLevel(3)">
        
      </el-button>
      <el-button :class="status[2]" @click="handleLevel(2)">
        
      </el-button>
      <el-button :class="status[1]" @click="handleLevel(1)">
        
      </el-button>
      <el-button :class="status[0]" @click="handleLevel(0)">
        
      </el-button>
    </div>

    <ul ref="alarmBarRef" name="warn-items" class="alarm-list">
      <li
        v-for="item in showAlarms"
        :key="item.Id"
        ref="itemsRef"
        class="active"
        :class="item.isSelected ? 'checked' : 'item'"
        @click="checkAlarm(item)"
      >
        <div
          class="top-bar"
          :style="item.status == true ? 'filter:grayscale(0.5)' : ''"
        >
          <span :style="styleArr[item.alarmLevel]" class="alarm-level">{{
            alarmLevel(item.alarmLevel)
          }}</span>
          <p>
            <span style="line-height:2.6rem">
              {{ item.CreateTime }}
            </span>
            &nbsp;-
            <strong :style="colorStyleArr[item.alarmLevel]">
              {{ item.SourceType }}
            </strong>
            <br />
            <span :style="colorStyleArr[item.alarmLevel]">{{
              item.alarmContent
            }}</span>
          </p>
        </div>
        <div class="bot-bar">
          <span>{{ item.diffTime }}</span>
          <el-button
            size="mini"
            v-show="item.map"
            @click.stop="viewToAlarm(item)"
          >
            
          </el-button>
          <el-button size="mini" @click.stop="alarmDetail(item)">
            
          </el-button>
          <el-button
            size="mini"
            v-show="flag == ''"
            @click.stop="showHandlePannel(item)"
          >
            
          </el-button>
        </div>
      </li>
      <li v-show="!loading" class="tips">
        <p></p>
      </li>
    </ul>
    <div class="set-bar">
      <span v-show="checkedNum > 0">{{ checkedNum }}</span
      >&nbsp;

      <ElCheckbox
        v-show="flag == ''"
        label=""
        v-model="checkAll"
        @change="handleCheckAllChange"
      ></ElCheckbox
      >&nbsp;
      <el-popconfirm
        title=" "
        @confirm="handleAll"
        effect="dark"
      >
        <template #reference>
          <el-button size="mini" v-show="flag != ''" class="single-line">
            
          </el-button>
        </template>
      </el-popconfirm>
      <span>{{ total }}</span>
      <!-- --------------- -->
      <div>
        <span v-show="loading" class="loader loader--style2" title="1">
          <svg
            version="1.1"
            id="loader-1"
            xmlns="http://www.w3.org/2000/svg"
            xmlns:xlink="http://www.w3.org/1999/xlink"
            x="0px"
            y="0px"
            width="40px"
            height="40px"
            viewBox="0 0 50 50"
            style="enable-background:new 0 0 50 50;"
            xml:space="preserve"
          >
            <path
              fill="#000"
              d="M25.251,6.461c-10.318,0-18.683,8.365-18.683,18.683h4.068c0-8.071,6.543-14.615,14.615-14.615V6.461z"
            >
              <animateTransform
                attributeType="xml"
                attributeName="transform"
                type="rotate"
                from="0 25 25"
                to="360 25 25"
                dur="0.6s"
                repeatCount="indefinite"
              />
            </path>
          </svg>
        </span>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import {
  defineComponent,
  reactive,
  ref,
  toRefs,
  onMounted,
  watch,
  computed,
  nextTick,
  PropType
} from "vue";
import commonApi from "@/api/common";
import { ElMessage } from "element-plus";
// 
import dayjs from "dayjs";
import { getIntervalTime } from "@/utils";
import { timeFormateStr } from "../constVariables";
import { IGetAlarmItem } from "../types/ws";

export default defineComponent({
  name: "Handle",
  props: {
    alarmType: {
      required: false,
      default: ""
    },
    /** ws  */
    wsAlarmItem: {
      type: Object as PropType<IGetAlarmItem>
    },
    /** id */
    handleIds: {
      required: false,
      default: []
    },
    /** ws  id */
    cancelId: {
      type: String
    }
  },
  emits: [
    "showAlarmDetail",
    "showHandlePannel",
    "getAlarmTotal",
    "getAlarmUnHandle"
  ],
  setup(props, { emit }) {
    const params = reactive({
      status: ["", "", "", "active"],
      showAlarms: [] as any,
      alarms: [],
      styleArr: [
        { background: "#00A67B" },
        { background: "#FF7352" },
        { background: "#D9354E" }
      ],
      colorStyleArr: [
        { color: "#52f3c8" },
        { color: "#FF7352" },
        { color: "#D9354E" }
      ],
      flag: "",
      size: 10,
      page: 1,
      loading: false,
      oldY: undefined,
      disposeStatus: "", // 
      typeid: "", // id
      alarmLevel: "" as string | number,
      alarmTypeArr: [], // 
      alarmTypeName: "",
      total: 0,
      static: {
        total: 0,
        unHandle: 0
      },
      checkAll: false
    });

    const itemsRef = ref();
    const alarmBarRef = ref();
    const dataRef = toRefs(params);
    // 
    async function getAllAlarm() {
      const param = {
        act: "6Z6wTPGkfMVF9eLTOXA1611040899987",
        curpage: 1,
        // eslint-disable-next-line @typescript-eslint/camelcase
        sys_showpagesize: 10,
        ShowSceneNum: "",
        SourceType: "",
        alarmLevel: "",
        sTime: "",
        eTime: "",
        alarmProgress: ""
      };
      const res = await commonApi.getCommonApi(param);
      const { data } = res as any;
      params.static.total = data.count;
      emit("getAlarmTotal", params.static.total);
    }
    // 
    async function getAllUnHandleAlarm() {
      const param = {
        act: "6Z6wTPGkfMVF9eLTOXA1611040899987",
        curpage: 1,
        // eslint-disable-next-line @typescript-eslint/camelcase
        sys_showpagesize: 10,
        ShowSceneNum: "",
        SourceType: "",
        alarmLevel: "",
        sTime: "",
        eTime: "",
        alarmProgress: ""
      };
      const res = await commonApi.getCommonApi(param);
      const { data } = res as any;
      params.static.unHandle = data.count;
      emit("getAlarmUnHandle", params.static.unHandle);
    }
    // 
    async function getAlarmList() {
      const param = {
        act: "6Z6wTPGkfMVF9eLTOXA1611040899987",
        curpage: params.page,
        // eslint-disable-next-line @typescript-eslint/camelcase
        sys_showpagesize: params.size,
        ShowSceneNum: "",
        SourceType: props.alarmType,
        alarmLevel: params.alarmLevel,
        sTime: "",
        eTime: "",
        alarmProgress: params.disposeStatus
      };
      const res = await commonApi.getCommonApi(param);
      const { result, data } = res as any;

      if (result.success) {
        data.content.forEach((element: any) => {
          // 
          const createtime = dayjs(element.CreateTime).format(timeFormateStr);
          element.CreateTime = createtime;
          // 
          element.diffTime = getIntervalTime(createtime);
          // 
          const dealTime = dayjs(element.dealTime).format(timeFormateStr);
          element.dealTime = dealTime;
          // 
          element.isSelected = false;
        });
        params.showAlarms = [...params.showAlarms, ...data.content];
        params.total = data.count;
      }
      params.loading = false;
    }

    // 
    function alarmLevel(n: number) {
      const arr = ["", "", ""];
      return arr[n];
    }

    // 
    function handleStatus(status: string) {
      params.checkAll = false;
      params.loading = true;
      if (params.disposeStatus == status) {
        params.loading = false;
        return;
      }
      params.page = 1;
      params.flag = status;
      params.disposeStatus = status;
      params.showAlarms = [];
      getAlarmList();
    }
    // 
    function lazyLoad(e: any) {
      const itemLength = itemsRef.value && itemsRef.value.offsetHeight;
      const ulLength = alarmBarRef.value.offsetHeight;
      if (
        e.target.scrollTop >
        itemLength * params.page * params.size - ulLength
      ) {
        // if (params.showAlarms.length < params.page * params.size) {
        //   return;
        // }

        params.page += 1;
        params.loading = true;
        getAlarmList();
      }
    }
    // 
    function handleLevel(count: number) {
      params.checkAll = false;
      params.loading = true;
      if (
        params.alarmLevel === count ||
        (params.alarmLevel === "" && count === 3)
      ) {
        params.loading = false;
        return;
      }
      params.status = ["", "", "", ""];
      params.status[count] = "active";
      if (count === 3) {
        params.alarmLevel = "";
      } else {
        params.alarmLevel = count;
      }
      params.page = 1;
      params.showAlarms = [];
      getAlarmList();
    }

    // 
    function alarmDetail(row: any) {
      emit("showAlarmDetail", row);
    }

    //
    function handleAll() {
      const checkedAlarm = params.showAlarms.filter(
        (item: any) => item.isSelected
      );
      if (checkedAlarm.length === 0) {
        ElMessage.error("");
        return;
      }
      emit("showHandlePannel", checkedAlarm);
    }

    // 
    function showHandlePannel(row: any) {
      emit("showHandlePannel", row);
    }

    // 
    function checkAlarm(row: any) {
      params.showAlarms.forEach((item: any) => {
        if (item.Id === row.Id) {
          item.isSelected = !row.isSelected;
          return;
        }
      });
    }

    // /
    function handleCheckAllChange() {
      params.showAlarms.forEach((item: any) => {
        item.isSelected = params.checkAll;
      });
    }

    watch(
      () => props.wsAlarmItem,
      newVal => {
        console.log("newVal", newVal);
        if (newVal) {
          const wsAlarm = {
            isSelected: false,
            status: false,
            alarmLevel: newVal.AlarmLevel,
            CreateTime: dayjs(new Date()).format(timeFormateStr),
            diffTime: getIntervalTime(dayjs(new Date()).format(timeFormateStr)),
            SourceType: newVal.AlarmSourceType,
            alarmContent: newVal.AlarmContent,
            SourceName: newVal.AlarmSourceName,
            alarmProgress: ""
          };

          params.showAlarms = [wsAlarm, ...params.showAlarms];
          params.static.unHandle++;
          emit("getAlarmUnHandle", params.static.unHandle);
          params.static.total++;
          emit("getAlarmTotal", params.static.total);
        }
      }
    );
    // 
    watch(
      () => props.handleIds,
      newArr => {
        params.showAlarms.slice().forEach((item: any, index: number) => {
          newArr.forEach((id: string | number) => {
            if (item.IDS == id) {
              params.static.unHandle--;
              emit("getAlarmUnHandle", params.static.unHandle);
              // 
              if (params.showAlarms.length < params.size) {
                params.page = 1;
                params.showAlarms = [];
                getAlarmList();
              }
              params.showAlarms.splice(index, 1);
              params.total--;
            }
          });
        });
      }
    );
    const checkedNum: any = computed(() => {
      const num = params.showAlarms.filter((item: any) => item.isSelected);
      return num.length;
    });

    onMounted(() => {
      getAlarmList();
      // 
      getAllAlarm();
      // 
      getAllUnHandleAlarm();
    });

    watch(
      () => props.alarmType,
      () => {
        nextTick(() => {
          params.page = 1;
          params.showAlarms = [];
          getAlarmList();
        });
      }
    );
    return {
      ...dataRef,
      itemsRef,
      alarmBarRef,
      alarmLevel,
      handleStatus,
      lazyLoad,
      handleLevel,
      alarmDetail,
      handleAll,
      showHandlePannel,
      checkAlarm,
      handleCheckAllChange,
      checkedNum
    };
  }
});
</script>

<style lang="scss" scoped>
button {
  border: 0;
  background: 0;
  cursor: pointer;
  outline: none;
}
.handle {
  width: 98%;
  min-width: 220px;
  height: 100vh;
  color: #fff;
  position: relative;
  background: rgb(9, 29, 75, 0.5);
  opacity: 0.95;
  .handle-class {
    display: flex;
    justify-content: space-around;
    button {
      flex-grow: 1;
      line-height: 4rem;
      font-size: 1.6rem;
      background: #23406f;
      color: #fff;
      padding: 0 0.6rem;
      cursor: pointer;
      border-radius: 0;
      width: 100%;
    }
    .active {
      background: 0;
      font-size: 1.6rem;
      font-family: Microsoft YaHei;
      font-weight: 400;
      color: rgba(61, 209, 255, 1);
      line-height: 2rem;
    }
  }
  .danger-level {
    display: flex;
    justify-content: space-between;
    padding: 0rem 0rem 0rem 1rem;
    /* width: 98%; */
    button {
      line-height: 3rem;
      margin: 1rem 0;
      color: #ffffff;
      background: #3672c3;
      border-radius: 0.3rem;
      padding: 0 1.9rem;
      font-size: 1.4rem;
      font-family: Microsoft YaHei;
      font-weight: 400;
      color: rgba(255, 255, 255, 1);
      span {
        margin-left: 1.5rem;
      }
    }
    .active {
      background: #3dd1ff;
    }
  }
  .alarm-list {
    .item {
      background-size: 100% 100%;
      &:hover {
        box-shadow: 0 0 5px rgba(0, 113, 241, 1);
        -webkit-box-shadow: 0 0 5px rgba(0, 113, 241, 1);
      }
    }
    .checked {
      background: url("../../../../assets/dashboard/important_monitor.png")
        repeat center center/100% 110%;
    }
  }
  .alarm-type {
    display: flex;
    font-size: 1.4rem;
    align-items: center;
    padding-left: 1rem;
    padding-bottom: 1rem;
    .spinner-item {
      width: 17rem;
      min-width: 120px;
    }
  }
  ul {
    // border-bottom: 1px solid rgba(134, 208, 255, 0.2);
    height: 70%;
    overflow: auto;
    &::-webkit-scrollbar {
      /**/
      width: 0.7rem; /**/
    }
    &::-webkit-scrollbar-thumb {
      /**/
      border-radius: 0.3rem;
      background: rgba(134, 208, 255, 0.3);
    }
    li.tips {
      p {
        margin: 0 auto;
        text-align: center;
        width: 100px;
      }
    }
    li.active {
      margin: 0 1rem 0.6rem 1rem;
      color: rgba(255, 255, 255, 1);
      font-size: 1.4rem;
      height: 11rem;
      min-height: 70px;
      background: rgba(39, 74, 127, 1);
      border: 1px solid rgba(51, 119, 233, 1);
      border-radius: 0.3rem;
      box-sizing: border-box;
      .top-bar {
        display: flex;
        justify-content: space-between;
        align-items: center;
        .alarm-level {
          display: inline-block;
          width: 5rem;
          min-width: 20px;
          min-height: 35px;
          line-height: 5rem;
          font-size: 1.6rem;
          text-align: center;
          margin: 0.5rem;
          border-radius: 0.4rem;
        }
        p {
          margin: 0;
          width: 80%;
          min-width: 150px;
          padding-right: 0.6rem;
          font-size: 1.3rem;
          line-height: 1.6rem;
        }
      }
      .bot-bar {
        text-align: right;
        line-height: 2.6rem;
        margin-top: 0.6rem;
        span {
          font-size: 1rem;
          padding-top: 0.6rem;
          float: left;
          margin-left: 1rem;
        }
        button {
          font-size: 1.2rem;
          color: #fff;
          background: rgba(54, 114, 195, 1);
          border-radius: 0.3rem;
          margin-right: 1rem;
        }
      }
    }
  }
  .set-bar {
    width: 100%;
    padding: 0.6rem 2rem;
    text-align: center;
    font-size: 1.2rem;
    bottom: 1rem;
    button {
      color: #fff;
      width: 10rem;
      background: rgba(54, 114, 195, 1);
      margin-right: 1rem;
      border-radius: 0.3rem;
    }
  }
}
</style>
