<template>
  <div>
    <div ref="monitorListPanelRef" class="historical-mon">
      <div class="top-title">
        <span>{{ cameraName }}</span>
        <div style="width:10%">
          <el-switch
            v-model="cameraHigh"
            class="switchStyle"
            active-text=""
            inactive-text=""
            :active-value="true"
            :inactive-value="false"
            active-color="#13ce66"
            inactive-color="rgba(67, 67, 122, 1)"
            @change="changSwitch($event, cameraHigh)"
            >></el-switch
          >
        </div>
        <button @click="hidden" @touchstart.stop.prevent="hidden" />
      </div>
      <!--  -->
      <div class="h5videowrapper h5container" :style="getStyle()">
        <span v-show="isLoading">...</span>
        <video
          ref="playerRef"
          class="live-video"
          muted
          autoplay
          controls="true"
          style="object-fit: fill"
        />
      </div>
    </div>
  </div>
</template>
<script>
import flvjs from "hd-flv";
import { httpFlvServer } from "@/utils/urls";
import dragDialog from "../directive/drag-dialog";
import {
  defineComponent,
  onUnmounted,
  ref,
  nextTick,
  reactive,
  toRefs,
  onMounted
} from "vue";
import { ElMessage } from "element-plus";
const CHANGE_MONITORLISTPANEL_STATE = "changeMonitorListPanelState";
export default defineComponent({
  name: "MonitorList",
  directives: { dragDialog },
  props: {
    cameraData: {
      type: Object,
      required: true
    },
    livePlayCount: {
      type: Number,
      required: true
    }
  },
  emits: [CHANGE_MONITORLISTPANEL_STATE],
  setup(props, { emit }) {
    const players = ref(); // player
    const data = reactive({
      playList: {}, // 4
      monitorPanelState: false, // 
      serverHost: "", // Http_flv
      cameraId: 0,
      cameraName: "",
      cameraHigh: false,
      subtype: 2,
      isLoading: false
    });
    const dataRef = toRefs(data);
    const playerRef = ref();
    function initPlayer(playList, subtype) {
      // 
      if (flvjs.isSupported()) {
        console.log("playerRef", playerRef);
        const video = playerRef.value; //video
        if (video) {
          data.isLoading = true;
          const id = playList.camera.id; // id
          data.cameraId = id;
          data.cameraName = playList.camera.name;
          let rtsp = `rtsp://${playList.camera.username}:${playList.camera.password}@${playList.camera.ip}?channel=1&subtype=${subtype}`; // rtsp
          if (
            playList.camera.brand === "" ||
            playList.camera.brand === ""
          ) {
            rtsp = `rtsp://${playList.camera.username}:${playList.camera.password}@${playList.camera.ip}/Streaming/Channels/10${subtype}`; // rtsp
          } else if (playList.camera.brand === "") {
            rtsp = `rtsp://${playList.camera.username}:${playList.camera.password}@${playList.camera.ip}?channel=1&subtype=${subtype}`; // rtsp
          }
          console.log("rtsp", rtsp);
          players.value = playList.camera.player; // player
          const Url = `${httpFlvServer}/${id}/?url=${encodeURIComponent(
            rtsp
          )}}&brand=${playList.camera.brand}`;
          players.value = flvjs.createPlayer({
            type: "flv",
            isLive: true, // isLive:true,false
            enableStashBuffer: false,
            autoCleanupMaxBackwardDuration: 60,
            autoCleanupMinBackwardDuration: 30,
            statisticsInfoReportInterval: 2000,
            stashInitialSize: 128 * 1024,
            // AndroidenableDurationMonitorfalse
            enableDurationMonitor: true, // true
            enableVideoFrozenMonitor: true, // VIDEO_FROZEN
            // SafarienableConstVideoViewSizefalse
            enableConstVideoViewSize: true,
            url: Url
          });
          const player = players.value;
          player.on(flvjs.Events.METADATA_ARRIVED, () => {
            // console.log('--== meta data arrived ==--')
            data.isLoading = false;
            console.log("--==  ==--");
          });
          player.enableVideoStateMonitor(true); // { + }

          player.on(flvjs.Events.VIDEO_RESOLUTION_CHANGED, () => {
            // ElMessage.error('')
            // 
            initPlayer(data.playList, data.subtype);
            // console.log('--== video resolution changed, reload ==--')
            console.log("");
          });
          player.on(flvjs.Events.MEDIA_SOURCE_ENDED, () => {
            // ElMessage.error('')
            // 
            initPlayer(data.playList, data.subtype);
            // console.log('--== media source ended, normally means server stop push stream ==--')
            console.log("");
          });
          player.on(flvjs.Events.MEDIA_SOURCE_CLOSE, () => {
            // ElMessage.error('')
            // console.log('--== media source close, show no source view ==--')
            console.log("");
          });

          let paused = false;
          video.addEventListener("pause", function() {
            // 
            paused = true;
          });
          video.addEventListener("play", function() {
            // 
            paused = false;
          });
          player.on(flvjs.Events.NETWORK_ERROR, () => {
            ElMessage.error("");
          });
          player.on(flvjs.Events.MEDIA_ERROR, () => {
            // ElMessage.error('')
            console.log("");
          });

          player.on(flvjs.Events.VIDEO_FROZEN, () => {
            // console.log('--== video frozen ==--')
            if (!paused) {
              // 
              initPlayer(data.playList, data.subtype);
            }
          });

          player.on(flvjs.Events.ERROR, (errType, errDetail) => {
            // ElMessage.error('...')
            if (!paused) {
              // 
              initPlayer(data.playList, data.subtype);
            }
            console.log(
              "--== flvjs error, type:" +
                errType +
                ", detail:" +
                errDetail +
                " ==--"
            );
            console.log(
              "--== should set a timer(for example, 3 seconds) to reload current flv ==--"
            );
          });

          players.value.attachMediaElement(video);
          try {
            players.value.load();
            players.value.play();
          } catch (error) {
            console.log(error);
          }
        }
      }
    }
    //
    onUnmounted(() => {
      // 
      if (typeof players.value !== "undefined" && players.value.e != null) {
        if (players.value != null) {
          players.value.pause();
          players.value.unload();
          players.value.detachMediaElement();
          players.value.destroy();
        }
      }
    });
    //
    function getStyle() {
      if (props.livePlayCount === 1) {
        return "width:100%; height:100%";
      } else {
        return "width:100%; height:26rem";
      }
    }

    // 
    function hidden() {
      if (typeof players.value !== "undefined" && players.value.e != null) {
        if (players.value != null) {
          players.value.pause();
          players.value.unload();
          players.value.detachMediaElement();
          players.value.destroy();
        }
      }
      // 
      emit(CHANGE_MONITORLISTPANEL_STATE, data.cameraId);
    }

    // 
    function changSwitch($event, value) {
      data.cameraHigh = value;
      if (data.cameraHigh) {
        data.subtype = 1;
      } else {
        data.subtype = 2;
      }
      // 
      if (typeof players.value !== "undefined" && players.value.e != null) {
        if (players.value != null) {
          players.value.pause();
          players.value.unload();
          players.value.detachMediaElement();
          players.value.destroy();
        }
      }
      nextTick(() => {
        console.log(data.playList);
        initPlayer(data.playList, data.subtype);
      });
    }

    function initCameraData(newValue) {
      if (newValue) {
        console.log("playList", data.playList);
        console.log("newValue", newValue);
        data.playList = newValue;
        nextTick(() => {
          if (!newValue.active) {
            // 
            // 
            if (
              typeof players.value !== "undefined" &&
              players.value.e != null
            ) {
              if (players.value != null) {
                players.value.pause();
                players.value.unload();
                players.value.detachMediaElement();
                players.value.destroy();
              }
            }
          } else {
            // 
            initPlayer(newValue, data.subtype);
          }
        });
      }
    }
    onMounted(() => {
      initCameraData(props.cameraData);
    });
    return {
      ...dataRef,
      getStyle,
      hidden,
      changSwitch,
      playerRef
    };
  }
});
</script>
<style lang="scss">
.live-video {
  width: 100%;
  height: 100%;
  background: black;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  -moz-background-size: 100% 100%;
  background-image: url("/images/connecting.jpg");
}
.historical-mon {
  /* background: rgba(30, 42, 66, 0.88); */
  width: 100%;
  height: 100%;
  color: #ffffff;
}
.historical-mon .top-title {
  width: 100%;
  line-height: 30px;
  color: #ffffff;
  border-bottom: 1px solid rgba(178, 223, 255, 0.2);
  /* background: #274a7f; */
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 1;
}
.historical-mon .top-title span {
  margin-left: 10px;
}
.historical-mon .top-title button {
  width: 20px;
  height: 20px;
  border: 0;
  cursor: pointer;
  background: url("/images/x.png") no-repeat;
  background-size: 100% 100%;
  background-position: center;
}
.historical-mon ::-webkit-scrollbar {
  /**/
  width: 6px; /**/
}
.historical-mon ::-webkit-scrollbar-thumb {
  /**/
  border-radius: 3px;
  background: rgba(67, 97, 122, 1);
}
.switchStyle {
  font-size: 8px !important;
}
.el-switch__label * {
  line-height: 1;
  font-size: 10px;
  display: inline-block;
}
.switchStyle .el-switch__label {
  position: absolute;
  display: none;
  color: #fff;
}
/**/
.switchStyle .el-switch__label--right {
  z-index: 1;
  right: 5px;
}
/**/
.switchStyle .el-switch__label--left {
  z-index: 1;
  left: 8px;
}
/**/
.switchStyle .el-switch__label.is-active {
  display: block;
}
.switchStyle.el-switch .el-switch__core,
.el-switch .el-switch__label {
  width: 48px !important;
  height: 18px !important;
}
</style>
