<!--
 * @Description: 
 * @Author: huxianc
 * @Date: 2020-12-24 14:56:24
 * @LastEditors: huxianc
 * @LastEditTime: 2021-02-23 10:06:15
-->
<template>
  <main class="home-wrapper">
    <div id="container"></div>

    <!--  start -->
    <el-collapse-transition>
      <main-nav
        v-show="visibleObj.nav && showMenu"
        ref="mainNavRef"
        @navClick="navClick"
      />
    </el-collapse-transition>
    <!--  end -->

    <!--  start -->
    <el-drawer
      v-model="drawerInfo.showList"
      direction="rtl"
      size="50%"
      :with-header="false"
      @close="resetNavIndex"
      append-to-body
    >
      <div class="drawer-container">
        <!-- drawer - title -->
        <div class="drawer-header">
          <div class="drawer-title">{{ drawerInfo.drawerTitle }}</div>
          <i
            class="el-icon-close close-btn"
            @click="drawerInfo.showList = false"
          />
        </div>
        <!-- drawer content -->
        <el-tabs
          v-model="drawerInfo.activeName"
          type="card"
          @tab-click="equipSwitchClick"
        >
          <el-tab-pane label="" :name="types.camera">
            <!-- v-if tabsapi -->
            <camera-list
              v-if="drawerInfo.activeName === types.camera"
              :form-data="detailsForm.formData"
              @seeNowVideo="seeNowVideo"
              @seeHistoryVideo="seeHistoryVideo"
              @showEquipDetails="showEquipDetails"
            />
          </el-tab-pane>
          <el-tab-pane label="" :name="types.basestation">
            <basestation-list
              v-if="drawerInfo.activeName === types.basestation"
              :form-data="detailsForm.formData"
              @showEquipDetails="showEquipDetails"
            />
          </el-tab-pane>
          <el-tab-pane label="" :name="types.labelList">
            <label-list
              v-if="drawerInfo.activeName === types.labelList"
              :form-data="detailsForm.formData"
              :wsAllLabel="person.all"
              @showEquipDetails="showEquipDetails"
            />
          </el-tab-pane>
          <el-tab-pane label="" :name="types.person">
            <person-list
              v-if="drawerInfo.activeName === types.person"
              :form-data="detailsForm.formData"
              :wsAllLabel="person.all"
              @showEquipDetails="showEquipDetails"
            />
          </el-tab-pane>
        </el-tabs>
      </div>
    </el-drawer>
    <!--  end -->

    <!-- table dialog start -->
    <el-dialog
      title=""
      v-model="fenceListInfo.tableVisible"
      width="50%"
    >
      <div style="margin-bottom: 10px">
        <el-button type="primary" @click="handleAddFence"></el-button>
        <!-- https://vue3js.cn/docs/zh/guide/conditional.html#v-show -->
        <!--  -->
        <template v-if="fenceListInfo.tableData.length > 0">
          <el-button type="primary" @click="toggleAllFenceVisible(true)"
            ></el-button
          >
          <el-button type="primary" @click="toggleAllFenceVisible(false)"
            ></el-button
          >
        </template>
      </div>
      <hd-table
        v-loading="fenceListInfo.loading"
        :data="fenceListInfo.tableData"
      >
        <hd-table-column prop="areaname" label="" />
        <el-table-column label="" width align="center">
          <template #default="{ row }">
            <el-switch
              v-model="row.show"
              @change="changeAreaVisible($event, row.areaids)"
            />
          </template>
        </el-table-column>
        <el-table-column label="" width="250" align="center">
          <template #default="{ row }">
            <el-button type="primary" size="mini" @click="handleEditFence(row)"
              ></el-button
            >
            <el-button type="primary" size="mini" @click="handleSeeRules(row)"
              ></el-button
            >
            <el-popconfirm
              :title="` ${row.areaname} `"
              @confirm="handleDelFence(row)"
              effect="dark"
            >
              <template #reference>
                <el-button type="danger" size="mini" style="margin-left: 10px"
                  ></el-button
                >
              </template>
            </el-popconfirm>
          </template>
        </el-table-column>
      </hd-table>
      <el-pagination
        v-show="fenceListInfo.total"
        v-model:current-page="fenceListInfo.params.curpage"
        layout="total, prev, pager, next, jumper"
        :total="fenceListInfo.total"
        background
        @current-change="fenceCurrentChange"
      />
    </el-dialog>
    <!-- table dialog end -->

    <!--   dialog start -->
    <hd-dialog
      :title="`${fenceInfo.dialogType === 0 ? '' : ''}`"
      v-model="fenceInfo.visible"
      width="800px"
      showFooterButtons
      :close-on-click-modal="false"
      :confirmButtonDisabled="!drawFenceInfo.clonePoints.length"
      @confirm="handleSubmitFence"
      @cancel="fenceInfo.visible = false"
    >
      <el-form
        ref="fenceInfoFormRef"
        :model="fenceInfo.form"
        :rules="fenceInfo.rules"
        label-width="120px"
      >
        <el-row :gutter="0">
          <el-col :span="12" :offset="0">
            <el-form-item label="" prop="name">
              <el-input
                v-model.trim="fenceInfo.form.name"
                clearable
                style="max-width: 200px"
                placeholder=""
              />
            </el-form-item>
          </el-col>
        </el-row>

        <el-form-item label="">
          <el-transfer
            v-model="fenceInfo.form.chosenCamera"
            filterable
            filter-placeholder=""
            :data="fenceInfo.allMapCamera"
            :titles="['', '']"
            :props="transferProps"
          >
            <template #default="{ option }">
              <span>{{ option.dname }}</span>
            </template>
          </el-transfer>
        </el-form-item>

        <el-form-item label="" required>
          <el-button type="success" size="mini" @click="handleFenceDraw">
            {{
              `${drawFenceInfo.clonePoints.length === 0 ? "" : ""}`
            }}
          </el-button>
        </el-form-item>
      </el-form>
    </hd-dialog>
    <!--   dialog end -->

    <!--  buttons start -->
    <el-collapse-transition>
      <fence-box
        v-if="status.isDrawFence"
        @revoke="handleRevokeFence"
        @redraw="handleRedrawFence"
        @cancel="handleCancelDrawFence"
        @finish="handleFinishDrawFence"
      />
    </el-collapse-transition>
    <!--  buttons end -->

    <!--  table start -->
    <hd-dialog title="" v-model="ruleListInfo.visiable">
      <div style="margin-bottom: 10px">
        <el-button type="primary" @click="handleAddRule"></el-button>
      </div>
      <hd-table v-loading="ruleListInfo.loading" :data="ruleListInfo.tableData">
        <hd-table-column label="" prop="labelType" />
        <hd-table-column label="" prop="loop" />
        <hd-table-column label="" width align="center" prop="stime" />
        <hd-table-column label="" width align="center" prop="etime" />

        <el-table-column label="" width="210" align="center">
          <template #default="{ row }">
            <el-button type="primary" size="mini" @click="handleEditRule(row)"
              ></el-button
            >
            <el-popconfirm
              :title="``"
              @confirm="handleDelRule(row)"
              effect="dark"
            >
              <template #reference>
                <el-button type="danger" size="mini" style="margin-left: 10px"
                  ></el-button
                >
              </template>
            </el-popconfirm>
          </template>
        </el-table-column>
      </hd-table>
      <el-pagination
        v-show="ruleListInfo.total"
        v-model:current-page="ruleListInfo.curpage"
        layout="total, prev, pager, next, jumper"
        :total="ruleListInfo.total"
        background
        @current-change="ruleCurrentChange"
      />
    </hd-dialog>
    <!--  table end -->

    <!--  start -->
    <hd-dialog
      :title="(ruleItemDetail.dialogType === 0 ? '' : '') + ''"
      v-model="ruleItemDetail.visiable"
      width="60%"
      :close-on-click-modal="false"
      showFooterButtons
      @confirm="handleSubmitRule"
      @cancel="ruleItemDetail.visiable = false"
    >
      <el-form
        ref="ruleFormRef"
        :model="ruleItemDetail.form"
        :rules="ruleItemDetailRule"
        label-width="100px"
      >
        <el-row :gutter="20">
          <el-col :span="12" :offset="0">
            <el-form-item label="">
              <el-switch v-model="ruleItemDetail.form.isLoop"> </el-switch>
            </el-form-item>
          </el-col>

          <el-col :span="12" :offset="0">
            <el-form-item label="" prop="permitType">
              <el-select
                v-model="ruleItemDetail.form.permitType"
                placeholder=""
                clearable
              >
                <el-option
                  v-for="item in areaRulesOption"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                >
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row
          v-if="
            ruleItemDetail.form.permitType === '6' ||
              ruleItemDetail.form.permitType === '7'
          "
          :gutter="20"
        >
          <!--  ||   -->
          <el-col :span="12" :offset="0">
            <el-form-item label="" prop="limitedTime">
              <el-input
                v-model.trim="ruleItemDetail.form.limitedTime"
                placeholder="0"
                clearable
                style="max-width: 200px"
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <!-- IsLoop=true,false -->
        <el-row v-if="ruleItemDetail.form.isLoop" :gutter="20">
          <el-col :span="12" :offset="0">
            <el-form-item label="" prop="startTime">
              <el-time-picker
                v-model="ruleItemDetail.form.startTime"
                placeholder=""
                @change="timerPickerChange"
              >
              </el-time-picker>
            </el-form-item>
          </el-col>
          <el-col :span="12" :offset="0">
            <el-form-item label="" prop="endTime">
              <el-time-picker
                v-model="ruleItemDetail.form.endTime"
                placeholder=""
              >
              </el-time-picker>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row v-else :gutter="20">
          <el-col :span="12" :offset="0">
            <el-form-item label="" prop="startTime">
              <el-date-picker
                v-model="ruleItemDetail.form.startTime"
                type="datetime"
                placeholder=""
                @change="ruleItemDetail.form.endTime = ''"
              >
              </el-date-picker>
            </el-form-item>
          </el-col>
          <el-col :span="12" :offset="0">
            <el-form-item label="" prop="endTime">
              <el-date-picker
                v-model="ruleItemDetail.form.endTime"
                type="datetime"
                placeholder=""
                :disabled="!ruleItemDetail.form.startTime"
                :disabledDate="ruleDisabledDate"
              >
              </el-date-picker>
            </el-form-item>
          </el-col>
        </el-row>

        <el-row :gutter="0">
          <el-col :span="24" :offset="0">
            <el-form-item label="">
              <el-input v-model="ruleItemDetail.form.memo" type="textarea" />
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
    </hd-dialog>
    <!--  end -->

    <!-- table dialog start -->
    <el-dialog
      title=""
      v-model="alarmListInfo.tableVisible"
      width="65%"
    >
      <el-form :model="alarmListInfo.params" label-width="80px" :inline="true">
        <el-form-item label>
          <el-select
            v-model="alarmListInfo.SourceType"
            placeholder=""
            style="width: 160px"
            clearable
            filterable
            @change="getAlarmListByCondition"
          >
            <el-option
              v-for="item in alarmType"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label>
          <el-date-picker
            v-model="alarmListInfo.timeRange"
            type="datetimerange"
            range-separator=""
            start-placeholder=""
            end-placeholder=""
            @change="getAlarmListByCondition"
          >
          </el-date-picker>
        </el-form-item>
        <el-form-item label>
          <el-select
            v-model="alarmListInfo.alarmLevel"
            placeholder=""
            style="width: 120px"
            clearable
            filterable
            @change="getAlarmListByCondition"
          >
            <el-option label="" value="0"> </el-option>
            <el-option label="" value="1"> </el-option>
            <el-option label="" value="2"> </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label>
          <el-select
            v-model="alarmListInfo.alarmProgress"
            placeholder=""
            style="width: 120px"
            clearable
            filterable
            @change="getAlarmListByCondition"
          >
            <el-option label="" value=""> </el-option>
            <el-option label="" value=""> </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <hd-table
        v-loading="alarmListInfo.loading"
        :data="alarmListInfo.tableData"
        :header-cell-style="{ 'text-align': 'center' }"
      >
        <hd-table-column label="" prop="SourceType" width="100" />
        <el-table-column
          label=""
          width="90"
          align="center"
          show-overflow-tooltip
        >
          <template #default="{ row }">
            <el-tag v-if="row.alarmLevel === 0"></el-tag>
            <el-tag v-if="row.alarmLevel === 1" type="warning"></el-tag>
            <el-tag v-if="row.alarmLevel === 2" type="danger"></el-tag>
          </template>
        </el-table-column>

        <el-table-column
          label=""
          width="90"
          align="center"
          show-overflow-tooltip
        >
          <template #default="{ row }">
            <el-tag v-if="row.alarmProgress === ''" type="danger">{{
              row.alarmProgress
            }}</el-tag>
            <el-tag v-if="row.alarmProgress === ''" type="success">{{
              row.alarmProgress
            }}</el-tag>
          </template>
        </el-table-column>
        <hd-table-column
          prop="alarmContent"
          label=""
          width
          show-overflow-tooltip
        />
        <hd-table-column prop="CreateTime" label="" width="155" />
        <el-table-column label="" width="240" align="center">
          <template #default="{ row }">
            <el-button
              type="primary"
              v-if="row.alarmProgress === ''"
              size="mini"
              @click="handleHandleAlarm(row)"
              ></el-button
            >
            <el-button type="primary" size="mini" @click="showAlarmDetail(row)"
              ></el-button
            >
            <el-popconfirm title=" " effect="dark">
              <template #reference>
                <el-button type="primary" size="mini"></el-button>
              </template>
            </el-popconfirm>
          </template>
        </el-table-column>
      </hd-table>
      <el-pagination
        v-show="alarmListInfo.total"
        v-model:current-page="alarmListInfo.params.curpage"
        :page-size="alarmListInfo.params.size"
        layout="total, prev, pager, next, jumper"
        :total="alarmListInfo.total"
        background
        @current-change="alarmCurrentChange"
      />
    </el-dialog>
    <!-- table dialog end -->

    <!--  start -->
    <el-dialog title="" v-model="alarmInfo.visible" width="1000px">
      <div class="alarm-detail">
        <div class="monitor-content">
          <div class="left-content">
            <div class="content-controller">
              <!---->
              <ul class="alarm-info-data">
                <li>
                  <span>
                    
                    <span style="color: #ff7352">
                      {{ alarmInfo.form.SourceType }}
                    </span>
                  </span>

                  <span>
                    
                    <span style="color: #ff7352">{{
                      alarmInfo.form.CreateTime
                    }}</span>
                  </span>
                </li>
                <li>
                  <span
                    >
                    <el-tag v-if="alarmInfo.form.alarmLevel === 0"></el-tag>
                    <el-tag
                      v-if="alarmInfo.form.alarmLevel === 1"
                      type="warning"
                      ></el-tag
                    >
                    <el-tag v-if="alarmInfo.form.alarmLevel === 2" type="danger"
                      ></el-tag
                    >
                  </span>
                  <span>
                    
                    <span style="color: #d9354e">
                      {{ alarmInfo.form.SourceName }}
                    </span>
                  </span>
                </li>
                <li>
                  <span>
                    {{
                      getMapByNum(alarmInfo.form.ShowSceneNum).sname
                    }}
                  </span>
                  <span> {{ alarmInfo.form.SourcePointNum }} </span>
                </li>
              </ul>
            </div>

            <!--  -->
            <div class="alarm-area">
              <img
                :src="
                  `${imgUrl}/${getMapByNum(alarmInfo.form.ShowSceneNum).surl}`
                "
                alt
                ref="mapImg"
              />
              <span
                class="icon iconfont pointImg"
                ref="pointImg"
                :style="alarmInfo.iconStyle"
                >&#xe615;</span
              >
            </div>
          </div>
          <ul class="right-content" ref="listBar">
            <li class="description">
              <div>
                
                <el-tag
                  v-if="alarmInfo.form.alarmProgress === ''"
                  type="danger"
                  style="width: fit-content"
                  >{{ alarmInfo.form.alarmProgress }}</el-tag
                >
                <el-tag
                  v-if="alarmInfo.form.alarmProgress === ''"
                  type="success"
                  >{{ alarmInfo.form.alarmProgress }}</el-tag
                >
              </div>
              <div v-show="alarmInfo.form.dealAccount">
                {{ alarmInfo.form.dealAccount }}
              </div>
              <div v-show="alarmInfo.form.dealAccount">
                {{ alarmInfo.form.dealTime }}
              </div>
            </li>
            <li class="alarm-ps" v-show="alarmInfo.form.SourceName">
              <p style="font-size: 1.6rem; margin-bottom: 0.4rem">:</p>
              <p style="text-indent: 1rem"></p>
            </li>
            <li class="right-content-head">
              <el-button size="mini" type="info">  </el-button>
              <el-popconfirm title=" " effect="dark">
                <template #reference>
                  <el-button type="primary" size="mini"></el-button>
                </template>
              </el-popconfirm>
            </li>
            <li class="right-content-head">
              <el-popconfirm
                title=" "
                effect="dark"
                @confirm="handleLocate"
              >
                <template #reference>
                  <el-button type="primary" size="mini"></el-button>
                </template>
              </el-popconfirm>
            </li>
            <li class="right-content-head">
              <div>
                <p></p>
                <p>{{ alarmInfo.form.alarmContent }}</p>
              </div>
            </li>
          </ul>
        </div>
      </div>
    </el-dialog>
    <!--  end -->

    <!--  start -->
    <el-dialog
      title=""
      v-model="alarmInfo.handleVisible"
      width="30%"
      :close-on-click-modal="false"
    >
      <el-form abel-width="100px" :rules="alarmInfo.rules">
        <el-form-item label="">
          <el-select
            v-model="alarmInfo.form.handleType"
            placeholder=""
            clearable
            filterable
          >
            <el-option
              v-for="item in handleTypeList"
              :key="item.id"
              :label="item.content"
              :value="item.content"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <div>
            <el-input
              type="textarea"
              style="width: 80%"
              :rows="4"
              placeholder=""
              v-model="alarmInfo.form.handleContent"
              @focus="alarmInfo.handleExampleVisible = true"
            >
            </el-input>
            <i
              v-if="alarmInfo.handleExampleVisible"
              class="el-icon el-icon-document"
              ></i
            >
            <el-table
              v-if="alarmInfo.handleExampleVisible"
              :data="handleExample"
              @row-click="getHandleExample"
              :show-header="false"
            >
              <el-table-column width="60" property="id"></el-table-column>
              <el-table-column property="content"></el-table-column>
            </el-table>
          </div>
        </el-form-item>
      </el-form>

      <template #footer>
        <span class="dialog-footer">
          <el-button @click="alarmInfo.handleVisible = false"> </el-button>
          <el-button type="primary" @click="alarmHandleSubmit"> </el-button>
        </span>
      </template>
    </el-dialog>
    <!--  end -->

    <!--  start -->
    <contextmenu-content
      v-if="visibleObj.contextmenu"
      :type="contextmenuTypeRef"
      :contxtmenu-position="contxtmenuPosition"
      :contextmenu-list="contextmenuList"
      @clicked="visibleObj.contextmenu = false"
      @seeNowVideo="seeNowVideo"
      @seeHistoryVideo="seeHistoryVideo"
      @showDetails="showDetails"
      @trackPlayback="handleClickTrackPlayback"
      @follow="personFollow"
    />
    <!--  end -->

    <!--  form start -->
    <dragable-container
      v-model="visibleObj.detailsForm"
      :title="detailsForm.formTitle"
      @close="hideDetails"
    >
      <form-content
        :form-data="detailsForm.formData"
        :form-type="contextmenuTypeRef"
        :x="detailsForm.x"
        :y="detailsForm.y"
      />
    </dragable-container>
    <!--  form end -->
    <!-- lfq start -->
    <dragable-container
      v-model="visibleObj.monitorListFlag"
      :width="768"
      @close="handleCloseAllVideo"
      title=""
    >
      <div
        v-for="(value, index) in monitorListInfo.newMonitorList"
        v-show="value.active"
        :key="index"
        :class="monitorListInfo.monitorInner"
      >
        <monitor-list
          ref="monitorListPanelRef"
          :camera-data="value"
          @changeMonitorListPanelState="
            watchMonitorListPanelState(value.camera.id)
          "
          :cameraData="value"
          :livePlayCount="monitorListInfo.livePlayCount"
        />
      </div>
    </dragable-container>
    <!--  end -->
    <!--lfq start-->
    <dragable-container
      v-model="visibleObj.historyMonitorListFlag"
      :width="768"
      title=""
      @close="handleCloseAllHistoryVideo"
    >
      <div
        v-for="(value, index) in monitorListInfo.newHistoryMonitorList"
        v-show="value.active"
        :key="index"
        :class="monitorListInfo.monitorHistoryInner"
      >
        <history-monitor
          ref="hitoryMonitorListPanelRef"
          :history-camera-data="value"
          @changeHistoryMonitorListPanelState="
            watchHistoryMonitorListPanelState(value.camera.id)
          "
          :historyCameraData="value"
          :historyPlayCount="monitorListInfo.historyPlayCount"
        />
      </div>
    </dragable-container>
    <!--lfq end-->
    <!--  start -->
    <hd-dialog
      title=""
      v-model="trackPlayback.show"
      width="600px"
      :close-on-click-modal="false"
      center
      showFooterButtons
      @opened="handleOpenContextmenu"
      @confirm="handleClickSeeTrackPlayback"
      @cancel="trackPlayback.show = false"
    >
      <el-form
        ref="trackPlaybackFormRef"
        :model="trackPlayback.form"
        :rules="trackPlayback.rules"
        label-width="100px"
        size="small"
      >
        <el-form-item label="" prop="date">
          <el-date-picker
            v-model="trackPlayback.form.date"
            type="date"
            placeholder=""
            :disabledDate="trackPlayback.disabledDate"
          />
        </el-form-item>
        <!--  -->
        <el-form-item label="" prop="time">
          <el-time-picker
            is-range
            v-model="trackPlayback.form.time"
            range-separator=""
            start-placeholder=""
            end-placeholder=""
            placeholder=""
          >
          </el-time-picker>
        </el-form-item>
      </el-form>
    </hd-dialog>
    <!--  end -->

    <!--  -->
    <playback-control
      v-if="status.isPlayBack"
      :sTime="playbackObj.sTime"
      :eTime="playbackObj.eTime"
      :currentTime="playbackObj.currentTime"
      :currentIndex="playbackObj.currentIndex"
      :len="playbackObj.len"
      :isPlay="playbackObj.isPlay"
      @changeRate="changeRate"
      @togglePlay="togglePlay"
      @close="playbackClose"
      @changeProcess="changeProcess"
    />

    <!--  start-->
    <div class="warning-container" :style="{ right: warningWidth }">
      <div class="warning-wrap">
        <div class="warning-show-button" @click="showWarningList"></div>
        <div class="warning-content">
          <el-form
            :model="fenceListInfo.params"
            label-width="80px"
            :inline="true"
          >
            <el-select
              v-model="alarmForm.type"
              placeholder=""
              clearable
              filterable
              style="width: 98%"
              @change="() => {}"
            >
              <el-option
                v-for="item in alarmType"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
            <br />
            <transition name="slide-fade">
              <handle
                :alarmType="alarmForm.type"
                :wsAlarmItem="wsAlarmItem"
                :handleIds="alarmListInfo.handleIds"
                @getAlarmTotal="getAlarmTotal"
                @getAlarmUnHandle="getAlarmUnHandle"
                @showAlarmDetail="showAlarmDetail"
                @showHandlePannel="handleHandleAlarm"
                :cancelId="cancelAlarmId"
              ></handle>
            </transition>
          </el-form>
        </div>
