    </div>
    <!--  end-->

    <!--  start -->
    <layer-person-list
      v-if="visibleObj.layerPerson"
      :person-list="layerPerson"
      :mouse-position="layerPosition"
    />
    <!--  end -->

    <!--  start -->
    <div
      v-show="!visibleObj.controlPannel && showMenu"
      class="control-menu"
      @click="visibleObj.controlPannel = !visibleObj.controlPannel"
    >
      <span class="icon iconfont">&#xe613;</span>
    </div>
    <!--  end -->

    <!--  start -->
    <div class="control-panel" v-show="visibleObj.controlPannel && showMenu">
      <div class="control-wrap">
        <i
          class="el-icon-close close-btn"
          @click="visibleObj.controlPannel = fasle"
        ></i>
        <div class="control-title o-h">
          {{ mapNameRef }}
        </div>
        <el-select
          v-model.trim="leftPannelSearch"
          :remote-method="querySearch"
          filterable
          default-first-option
          remote
          size="small"
          placeholder=""
          class="search-select"
          @change="searchChange"
          clearable
        >
          <el-option
            v-for="item in userSearchRes"
            :key="item.lids"
            :value="item.lids"
            :label="item.ltype"
          />
        </el-select>
        <div class="data-display-item">
          <div class="title">
            <span class="icon iconfont">&#xe6d8;</span>
            <span></span>
          </div>
          <div class="display-content">
            <div class="data-item">
              <div class="count">{{ person.all.length }}</div>
              <div class="name"></div>
            </div>
            <div class="data-item">
              <div class="count">{{ person.currentMap.length }}</div>
              <div class="name"></div>
            </div>
          </div>
        </div>
        <div class="data-display-item">
          <div class="title">
            <span class="icon iconfont big">&#xe69c;</span>
            <span></span>
          </div>
          <div class="display-content">
            <div class="data-item">
              <div class="count">{{ alarmInfo.alarmTotal }}</div>
              <div class="name"></div>
            </div>
            <div class="data-item warn">
              <div class="count">{{ alarmInfo.alarmUnHandle }}</div>
              <div class="name"></div>
            </div>
          </div>
        </div>
        <div class="control-buttons-container">
          <div
            class="control-button-item"
            v-for="(item, index) in controlButtonList"
            :key="`button-${index}`"
          >
            <div class="button-label">{{ item.label }}</div>
            <el-switch
              v-model="item.show"
              @change="controlButtonChange($event, item.code)"
              size="mini"
            >
            </el-switch>
          </div>
        </div>
      </div>
    </div>
    <!--  end -->

    <!--  -->
    <dragable-container
      v-model="visibleObj.map"
      title=""
      :width="800"
      @close="closeMapChoosen"
      ><map-choosen
        :map-list="mapListRef"
        :map-id="mapIdRef"
        @changeMap="changeMap"
    /></dragable-container>

    <!--  -->
    <el-button
      v-if="status.isFollowing"
      class="close-follow"
      type="primary"
      @click="handleCloseFollow"
      ></el-button
    >
  </main>
</template>

<script lang="ts">
import {
  defineComponent,
  onMounted,
  onUnmounted,
  reactive,
  ref,
  shallowRef,
  computed,
  watch,
  nextTick,
} from "vue";
import stageApi from "@/api/stage";
import alarmApi from "@/api/alarm";
import commonApi from "@/api/common";

import { imgUrl } from "@/utils/urls";
import { IResponseCommon, ISelectOption } from "@/types/index";

import {
  IMapArrItem,
  IDeviceItem,
  IDeviceInfo,
  Nullable,
  IPersonArrItem,
  IMessageClose,
  TLayerItem,
  TVectorgraph,
  IBuildItem,
  ISvgToImageConfig,
  IAreaItem,
  IPos,
  ITrackItem,
  ITrackErrorItem,
  TTrackRes,
} from "./types/index";
import { IBubbleDataItem, TDestoryType } from "./types/cacheObj";
import { IAlarmType } from "./types/alarm";
import { IGetAlarmItem } from "./types/ws";
import {
  IAddEditFenceParams,
  ISetDeviceData,
  ISetDeviceDataItem,
  IRuleItem,
  IUpperPos,
} from "./types/fence";
// todo konva   https://www.npmjs.com/package/konva Minimal bundle
import Konva from "konva";
import { Stage } from "konva/types/Stage";
import { ElMessage, ElMessageBox } from "element-plus";
import type { Pane } from "element-plus/lib/el-tabs/src/tabs.vue";
import {
  getOffset,
  screenTocanvas,
  svgToImage,
  guid,
  generateImageDomByType,
  loadImage,
  locateIcon,
  getDateTime,
  getTrackTime,
  strToPos,
  checkTimeGap,
} from "./utils";
import { generatePersonGroup } from "./utils/shape";
import { generatePersonItem } from "./utils/ws";
import { cloneDeep, debounce } from "lodash";
import {
  mainNav,
  personList,
  cameraList,
  basestationList,
  fenceBox,
  contextmenuContent,
  formContent,
  monitorList,
  historyMonitor,
  labelList,
  handle,
  layerPersonList,
  mapChoosen,
  dragableContainer,
  playbackControl,
} from "./components";

// 
import {
  NAV_INFO,
  FENCE,
  ALARM,
  DATAV,
  TITLE_HEIGHT as titleHeight,
  TYPES,
  areaRulesOption,
  formateRuleType,
  bgImageConfig,
  areaColorConfig,
  Hms,
  timeFormateStr,
  warningListWidth,
  getFormTitle,
  DEFAULT_COLORS,
  handleTypeList,
  handleExample,
} from "./constVariables";

import { computedRem, commonRuleItem, getPagination, customFuncRule } from "@/utils";
import { Shape } from "konva/types/Shape";
import { IELForm } from "./types/element";
import { Image } from "konva/types/shapes/Image";
import {
  setAllDevice,
  setAllScene,
  setAllBuild,
  getBuildByNum,
  getMapByNum,
  getUsername,
  setLoadSNum,
} from "@/utils/auth";
import initData from "./module/initData";
import dayjs from "dayjs";
import { Group } from "konva/types/Group";
import { Animation } from "konva/types/Animation";
import { IFrame } from "konva/types/types";

export default defineComponent({
  name: "Content",
  components: {
    mainNav,
    personList,
    cameraList,
    basestationList,
    fenceBox,
    contextmenuContent,
    formContent,
    monitorList,
    historyMonitor,
    labelList,
    handle,
    layerPersonList,
    mapChoosen,
    dragableContainer,
    playbackControl,
  },
  setup() {
    enum EpauletActionCode {
      /**  */
      HTagInit = 1000,
      /**  */
      HTagOnline = 1001,
      /**  */
      HTagOffline = 1002,
      /**  */
      HTagMove = 1003,
      /**  */
      HLayerGroupStatistics = 1004,
      /**  */
      HTagLogOff = 1005,
      /**  */
      HWaterAlarm = 1006,
      /**  */
      HSceneStatistics = 1007,
    }

    const {
      router,
      wsUrl, // ws 
      mapObj, //  obj
      mapListRef, //  arr
      mapIdRef,
      scaleBoundary, //  mminScale maxScale
      state,
      status,
      showMenu,
      mapNameRef,
      warningWidth, // 
      contextmenuTypeRef, //  TYPES
      contxtmenuPosition, //  
      contextmenuList, // shape
      detailsForm,
      visibleObj,
      monitorListInfo,
      drawerInfo,
      fenceInfo,
      fenceListInfo,
      drawFenceInfo,
      ruleListInfo, //  table
      ruleFormRef,
      ruleItemDetail, //  
      ruleItemDetailRule,
      ruleDisabledDate, // 
      alarmInfo,
      alarmListInfo,
      allGroup,
      activeTarget,
      basestation,
      bubble,
      camera,
      area,
      person,
      cacheAlarmObj,
      cacheBasestationObj,
      cacheCameraObj,
      layerPerson,
      layerPosition,
      cacheBubbleObj,
      cacheAreaObj,
      cachePersonObj,
      transferProps,
      allLayer,
      controlButtonList,
      wsAlarmType,
      alarmType,
      alarmForm,
    } = initData();

    let clickTimer: Nullable<number> = null;
    let currentContextmenu: Nullable<Shape | Group> = null; // shapeshadow

    const trackPlayback = reactive({
      show: false, // 
      // form
      form: {
        date: "" as any,
        time: [],
      },
      rules: {
        date: commonRuleItem(""),
        time: [commonRuleItem(""), customFuncRule(checkTimeGap)],
      },
      disabledDate(time: Date) {
        return time.getTime() > Date.now();
      },
      message: null as any,
      tween: null as any,
      steps: [] as ITrackItem[],
      lineNode: null as any,
    });

    const playbackObj = reactive({
      sTime: "",
      eTime: "",
      currentTime: "",
      currentIndex: 0,
      len: 0,
      isPlay: true,
    });

    let trackPlaybackLinePos: number[] = [];

    // 
    function timerPickerChange() {
      if (ruleItemDetail.form.endTime) {
        ruleItemDetail.form.endTime = "";
      }
    }

    const mousePos: IPos = { x: 0, y: 0 };

    let stage: Nullable<Stage> = null;
    function batchDraw() {
      (stage as Stage).batchDraw();
    }

    const layer = new Konva.Layer();
    const bgLayer = new Konva.Layer();
    const operationLayer = new Konva.Layer();

    watch(monitorListInfo.newMonitorList, (val) => {
      let count = 0;
      val.forEach((item: { active: any }) => {
        const active = item.active;
        if (active) {
          count++;
        }
      });
      if (count === 0) {
        visibleObj.monitorListFlag = false;
      }
      monitorListInfo.livePlayCount = count;
      if (count === 1) {
        monitorListInfo.monitorInner = "monitor-inner-one";
      } else {
        monitorListInfo.monitorInner = "monitor-inner-pannel";
      }

      if (val.length > monitorListInfo.cameraCount) {
        if (val.length > monitorListInfo.cameraCount * 1000) {
          val.splice(0, val.length - monitorListInfo.cameraCount);
        }
        val.forEach((item: { active: any }, index: number) => {
          if (index < val.length - monitorListInfo.cameraCount) {
            item.active = false;
          }
        });
      }
    });
    watch(monitorListInfo.newHistoryMonitorList, (val) => {
      let count = 0;
      val.forEach((value: { active: any }) => {
        const active = value.active;
        if (active) {
          count++;
        }
      });
      if (count === 0) {
        visibleObj.historyMonitorListFlag = false;
      }
      monitorListInfo.historyPlayCount = count;
      if (count === 1) {
        monitorListInfo.monitorHistoryInner = "monitor-inner-one";
      } else {
        monitorListInfo.monitorHistoryInner = "monitor-inner-pannel";
      }

      if (val.length > monitorListInfo.cameraCount) {
        if (val.length > monitorListInfo.cameraCount * 1000) {
          val.splice(0, val.length - monitorListInfo.cameraCount);
        }
        val.forEach((value: { active: any }, index: number) => {
          if (index < val.length - monitorListInfo.cameraCount) {
            value.active = false;
          }
        });
      }
    });

    let cacheDrawFence: Nullable<Shape> = null;
    /**
     * @description: 
     * @param {boolean} isFinished 
     * @return {*}
     */
    function handleStartDrawFence(isFinished = false) {
      cacheDrawFence?.destroy();

      const pointsArr = drawFenceInfo.points;
      if (pointsArr.length === 0) {
        batchDraw();
        return;
      }

      const fenceArea = new Konva.Shape({
        ...areaColorConfig,
        sceneFunc: function (ctx, shape) {
          ctx.beginPath();
          ctx.moveTo(pointsArr[0].x, pointsArr[0].y);
          for (let i = 1; i < pointsArr.length; i++) {
            const { x, y } = pointsArr[i];
            ctx.lineTo(x, y);
          }
          ctx.stroke();
          if (isFinished) {
            ctx.closePath();
          }
          ctx.fillStrokeShape(shape);
        },
      });
      cacheDrawFence = fenceArea;
      allGroup.fenceGroup.add(fenceArea);
      batchDraw();
    }

    // 
    function handleRedrawFence() {
      drawFenceInfo.points = [];
      handleStartDrawFence();
    }

    // 
    function handleRevokeFence() {
      const { points } = drawFenceInfo;
      const len = points.length;
      if (len === 0) {
        ElMessage.info("");
      } else if (len === 1 || len === 2) {
        handleRedrawFence();
      } else {
        points.splice(len - 2, 2);
        handleStartDrawFence();
      }
    }

    // 
    function handleCancelDrawFence() {
      handleRedrawFence();
      status.isDrawFence = false;
      (drawFenceInfo.message as IMessageClose)?.close();
      drawFenceInfo.points = [];
      drawFenceInfo.clonePoints = [];
      fenceInfo.visible = true;
    }

    // 
    function handleFinishDrawFence() {
      const { points } = drawFenceInfo;
      const len = points.length;
      if (len < 4) {
        ElMessage.info("");
      } else {
        (drawFenceInfo.message as IMessageClose).close();
        status.isDrawFence = false;
        points.pop();
        drawFenceInfo.clonePoints = cloneDeep(points);
        handleStartDrawFence(true);
        setTimeout(() => {
          fenceInfo.visible = true;
          handleRedrawFence();
        }, 500);
      }
    }

    // 
    function clearShadowColor() {
      const personOrLabel = [TYPES.labelList, TYPES.person];

      if (personOrLabel.includes(contextmenuTypeRef.value)) {
        ((currentContextmenu as Group)?.children[0] as Shape)?.shadowColor(
          DEFAULT_COLORS.TRANSPARENT
        );
      } else {
        (currentContextmenu as Shape)?.shadowColor?.(DEFAULT_COLORS.TRANSPARENT);
      }

      batchDraw();
    }

    /**
     * @description: 
     * @param {Event} e windowe
     * @return {*}
     */
    function showMenuList(e: MouseEvent) {
      const { clientX: x, clientY: y } = e;
      Object.assign(contxtmenuPosition, {
        left: x,
        top: y - titleHeight,
      });
      visibleObj.contextmenu = true;
    }

    // 
    function hideMenuList() {
      if (visibleObj.contextmenu) {
        visibleObj.contextmenu = false;
        clearShadowColor();
        currentContextmenu = null;
      }
    }

    function handleStageWheel() {
      (stage as Stage).on("wheel", (e) => {
        const ev: WheelEvent = e.evt;

        // ts   wheelDelta 
        // const wheelDelta = ev.wheelDelta ? ev.wheelDelta : ev.deltaY * -40;
        const wheelDelta = ev.deltaY * -40;

        const rateX = (ev.clientX - state.sx) / state.scale;
        const rateY = (ev.clientY - state.sy) / state.scale;

        if (wheelDelta > 0) {
          state.scale += 0.05;
          state.scale = Number((state.scale + 0.05).toFixed(2));
          if (state.scale >= scaleBoundary.maxScale) {
            state.scale = scaleBoundary.maxScale;
          }
        } else {
          state.scale -= 0.05;
          state.scale = Number((state.scale - 0.05).toFixed(2));
          if (state.scale <= scaleBoundary.minScale) {
            state.scale = scaleBoundary.minScale;
          }
        }

        state.sx = ev.clientX - state.scale * rateX;
        state.sy = ev.clientY - state.scale * rateY;

        (stage as Stage).setAttrs({
          x: state.sx,
          y: state.sy,
          scaleX: state.scale,
          scaleY: state.scale,
        });

        if (status.isDrawFence) {
          handleStartDrawFence();
        }

        batchDraw();
      });
    }

    function handleStageMouseDown() {
      (stage as Stage).on("mousedown", (e) => {
        const ev = e.evt;
        if (status.isDrawFence) {
          clickTimer = window.setTimeout(() => {
            const pos = screenTocanvas({ x: ev.clientX, y: ev.clientY }, state);
            drawFenceInfo.points.push(pos);
            handleStartDrawFence();
          }, 300);

          return;
        }
        status.draggable = true;
        Object.assign(mousePos, {
          x: ev.clientX,
          y: ev.clientY,
        });
        (stage as Stage).container().style.cursor = "move";
        hideMenuList();
      });
    }

    function handleStageMouseUp() {
      (stage as Stage).on("mouseup mouseout", () => {
        if (status.draggable) {
          status.draggable = false;
          (stage as Stage).container().style.cursor = "default";
        }
      });
    }

    function handleStageMouseMove() {
      (stage as Stage).on(
        "mousemove",
        debounce((e) => {
          const mouseMoveFunc = () => {
            const ev: MouseEvent = e.evt;

            if (status.isDrawFence) {
              const { points } = drawFenceInfo;

              if (points.length >= 1) {
                if (points.length > 1) {
                  points.pop();
                }
                const pos = screenTocanvas({ x: ev.clientX, y: ev.clientY }, state);
                points.push(pos);
                handleStartDrawFence();
              }
              return;
            }
            if (!status.draggable) return;

            state.sx += ev.clientX - mousePos.x;
            state.sy += ev.clientY - mousePos.y;

            const maxSx = getOffset(
              {
                x: (stage as Stage).width() * 0.25,
                y: 0,
              },
              {
                x: 0,
                y: 0,
              },
              state.scale
            ).x;
            const minSx = getOffset(
              {
                x: (stage as Stage).width() * 0.75,
                y: 0,
              },
              {
                x: bgImageConfig.width,
                y: 0,
              },
              state.scale
            ).x;
            const maxSy = getOffset(
              {
                x: 0,
                y: titleHeight,
              },
              { x: 0, y: 0 },
              state.scale
            ).y;
            const minSy = getOffset(
              {
                x: 0,
                y: (stage as Stage).height(),
              },
              { x: 0, y: bgImageConfig.height },
              state.scale
            ).y;

            const midSy = getOffset(
              {
                x: 0,
                y: (stage as Stage).height() / 2,
              },
              {
                x: 0,
                y: bgImageConfig.height / 2,
              },
              state.scale
            ).y;

            if (maxSx <= minSx) {
              state.sx = maxSx;
            } else if (state.sx > maxSx) {
              state.sx = maxSx;
            } else if (state.sx < minSx) {
              state.sx = minSx;
            }

            if (maxSy <= minSy) {
              state.sy = maxSy;
            } else if (state.sy > maxSy) {
              state.sy = maxSy;
            } else if (state.sy < minSy) {
              state.sy = minSy;
            }

            if (state.scale === scaleBoundary.minScale) {
              state.sy = midSy;
            }

            Object.assign(mousePos, {
              x: ev.clientX,
              y: ev.clientY,
            });

            (stage as Stage).setAttrs({
              x: state.sx,
              y: state.sy,
            });

            batchDraw();
          };
          mouseMoveFunc();
        }, 10)
      );
    }

    function handleStageContextMenu() {
      (stage as Stage).on("contextmenu", (e) => {
        const ev: MouseEvent = e.evt;
        ev.preventDefault();
        clickTimer && clearTimeout(clickTimer);
        if (status.isDrawFence) {
          handleFinishDrawFence();
          return;
        }
      });
    }

    function stageEventsInit() {
      handleStageWheel();
      handleStageMouseDown();
      handleStageMouseUp();
      handleStageMouseMove();
      handleStageContextMenu();
    }

    function initStage() {
      stage = new Konva.Stage({
        width: window.innerWidth,
        height: window.innerHeight - titleHeight,
        container: "container",
        scale: {
          x: state.scale,
          y: state.scale,
        },
      });
    }

    function hideDetails() {
      clearShadowColor();
      visibleObj.detailsForm = false;
    }

    // 
    let deviceAll = reactive<IDeviceItem[]>([]);

    async function addLayer() {
      (stage as Stage).add(layer).add(bgLayer).add(operationLayer);

      Object.keys(allGroup).forEach((key) => {
        layer.add(allGroup[key]);
      });

      bgLayer.moveToBottom();
      allGroup.fenceGroup.moveToBottom();
      operationLayer.moveToTop();
    }

    async function initAlarmIcon(attrs: { x: number; y: number; id: number }) {
      const { x, y, id } = attrs;
      const alarmIcon = new Konva.Circle({
        x: x,
        y: y,
        radius: 70,
        fill: "rgba(255,0,0,.7)",
      });
      cacheAlarmObj[id] = alarmIcon;
      const period = 5000;
      allGroup.alarmGroup.add(alarmIcon);
      let anim: null | Animation = new Konva.Animation((frame) => {
        const scale = Math.sin(((frame as IFrame).time * 2 * Math.PI) / period) + 0.001;
        alarmIcon.scale({ x: scale, y: scale });
      }, layer);
      (anim as Animation).start();
      setTimeout(() => {
        (anim as Animation)?.stop();
        anim = null;
        cacheAlarmObj[id].destroy();
        delete cacheAlarmObj[id];
        batchDraw();
      }, 3000);
    }

    async function initBasestation(attrs: TLayerItem, show = controlButtonList[0].show) {
      const { x, y, lids } = attrs;
      // todo deviceitem 
      // const deviceArr = JSON.parse(deviceitem) as ISetDeviceDataItem[];
      console.log("base attrs", attrs);
      const imageDom = await generateImageDomByType(attrs);

      const generate = () => {
        const shape = new Konva.Image({
          x,
          y,
          width: imageDom.width,
          height: imageDom.height,
          image: imageDom,
          shadowColor: DEFAULT_COLORS.TRANSPARENT,
          // shadowBlur: imageDom.width,
          shadowBlur: 20,
          name: lids,
        });

        shape.on("contextmenu", (e) => {
          console.log(e);
          if (status.isDrawFence) {
            return false;
          }

          hideDetails();

          activeTarget.attrs = cloneDeep(e.target.attrs);
          currentContextmenu = shape;

          contextmenuTypeRef.value = TYPES.basestation;
          showMenuList(e.evt);

          shape.shadowColor(DEFAULT_COLORS.BLUE);
          batchDraw();
        });

        allGroup.basestationGroup.add(shape);
        cacheBasestationObj[lids] = shape;
        shape.visible(show);
        batchDraw();
      };

      loadImage(imageDom, generate);
    }

    async function initCamera(attrs: TLayerItem, show = controlButtonList[1].show) {
      const { x, y, lids, deviceitem } = attrs;
      const imageDom = await generateImageDomByType(attrs);

