    // 
    function handleHandleAlarm(row: any) {
      console;
      if (row.length > 0) {
        // 
        let str = "";
        alarmInfo.handleAlarmId = "";
        row.forEach((item: any) => {
          str += item.IDS + ",";
        });
        // 
        if (str.length > 0) {
          str = str.substr(0, str.length - 1);
        }
        alarmInfo.handleAlarmIdArr = str;
      } else {
        alarmInfo.handleAlarmId = row.IDS;
        alarmInfo.handleAlarmIdArr = "";
      }
      alarmInfo.handleVisible = true;
      alarmInfo.form.handleContent = "";
      alarmInfo.handleExampleVisible = false;
    }
