    async function initBgImage(src: string = imgUrl + mapObj[mapIdRef.value].surl) {
      console.log("src:", src);

      const imageDom = new window.Image();
      imageDom.src = src;
      imageDom.onload = async () => {
        const image = new Konva.Image({
          image: imageDom,
          width: imageDom.width,
          height: imageDom.height,
          // https://konvajs.org/docs/performance/All_Performance_Tips.html 13
          listening: false,
        });

        state.scale = Number((window.innerWidth / image.width()).toFixed(2));
        scaleBoundary.minScale = state.scale;

        await bgLayer.add(image);
        (stage as Stage).setAttrs({
          scale: {
            x: state.scale,
            y: state.scale,
          },
        });

        cacheBgImage = image;

        image.moveToBottom();
        await batchDraw();

        //   stage 
