    // /  
    function handleSubmitRule() {
      (ruleFormRef.value as IELForm).validate(async (valid: boolean) => {
        if (valid) {
          const {
            memo,
            isLoop,
            limitedTime,
            startTime,
            endTime,
            permitType,
          } = ruleItemDetail.form;

          const formateTime = (loop: boolean, time1: Date, time2: Date) => {
            const formateStr = loop ? Hms : timeFormateStr;

            return {
              StartTime: dayjs(time1).format(formateStr),
              EndTime: dayjs(time2).format(formateStr),
            };
          };

          const { StartTime, EndTime } = formateTime(
            isLoop,
            startTime as Date,
            endTime as Date
          );

          const params = {
            act: "QRZNk8BItBSJTT0ikWT1586422170736",
            LayerIdS: layerId,
            SceneNum: mapIdRef.value,
            ID: ruleItemDetail.dialogType === 0 ? 0 : ruleId,
            CheckSourceUnitName: layerName,
            CheckSourceID: editPoints,
            Memo: memo,
            RuleLocateType: 2,
            PermitType: permitType,
            IsLoopEveryDay: isLoop,
            OwnUserType: "0,8",
            OwnParam: "",
            LimitedTime: limitedTime || "0",
            StartTime,
            EndTime,
          };
          const res: unknown = await stageApi.saveRule(params);
          const { result } = res as IResponseCommon<string>;
          if (result.msg) {
            ElMessage.success(result.msg);
            ruleItemDetail.visiable = false;
            getRules(layerId);
          }
        }
      });
    }

