    // ws 
    let locationWs: Nullable<WebSocket> = null;
    function initLocationWs() {
      const url = wsUrl.location;
      locationWs = new WebSocket(url);
      locationWs.onmessage = async (event) => {
        const data = JSON.parse(event.data);
        if (data.ActionCode === EpauletActionCode.HTagInit) {
          console.log("init", data.Data);
          person.all = (data.Data as IPersonArrItem[]).map((item) =>
            generatePersonItem(item)
          );

          getCurrentMapPerson(mapIdRef.value);
        }

        if (data.ActionCode === EpauletActionCode.HTagOnline) {
          const personData = data.Data as IPersonArrItem;
          // 
          const isIn = person.all.some((item) => item.TagId === personData.TagId);
          if (isIn) return;
          const posItem = generatePersonItem(personData);
          person.all.push(posItem);
          if (mapIdRef.value === personData.LocationSceneId) {
            person.currentMap.push(posItem);
            initPerson(posItem);
          }
          ElMessage.info(`${posItem.name}`);
        }

        if (
          data.ActionCode === EpauletActionCode.HTagOffline ||
          data.ActionCode === EpauletActionCode.HTagLogOff
        ) {
          //  TagId 
          const personItem = data.Data as IPersonArrItem;
          // console.log("data=>", personItem);

          const index = person.all.findIndex((item) => item.TagId === personItem.TagId);
          if (index > -1) {
            if (person.all[index].map === mapIdRef.value) {
              const currntMapIndex = person.currentMap.findIndex(
                (item) => item.TagId === personItem.TagId
              );
              if (currntMapIndex > -1) {
                person.currentMap.splice(currntMapIndex, 1);
                const node = cachePersonObj[personItem.TagId];
                if (
                  personItem.TagId ===
                  ((cacheFollowingPerson as Group)?.children[0] as Shape).attrs.id
                ) {
                  status.isFollowing = false;
                  ElMessage.info("");
                }
                node?.destroy();
                delete cachePersonObj[personItem.TagId];
                batchDraw();
              }
            }
            const name = person.all[index].name;
            person.all.splice(index, 1);
            if (data.ActionCode === EpauletActionCode.HTagOffline) {
              ElMessage.info(`${name}`);
            }
          }
        }

        if (data.ActionCode === EpauletActionCode.HTagMove) {
          const personItem = data.Data as IPersonArrItem;
          const posObj = generatePersonItem(personItem);
          // 
          const index = person.all.findIndex((item) => item.TagId === posObj.TagId);

          // const { x: bx, y: by } = person.all[index];
          if (index > -1) {
            // x,y 
            const item = person.all[index];
            if (item.x === posObj.x && item.y === posObj.y) {
              return;
            }
            Object.assign(item, posObj);
          }

          // 
          if (personItem.LocationSceneId === mapIdRef.value) {
            const node = cachePersonObj[personItem.TagId];
            node?.to({
              x: posObj.x,
              y: posObj.y,
              duration: 0.2,
            });
            // 
            if (status.isFollowing && followPersonId === posObj.TagId) {
              const gx = -(posObj.x * state.scale - window.innerWidth / 2);
              const gy = -(
                posObj.y * state.scale -
                (window.innerHeight - titleHeight) / 2
              );
              (stage as Stage).setAttrs({
                x: gx,
                y: gy,
              });
            }
            batchDraw();
          }
        }

        if (data.ActionCode === EpauletActionCode.HLayerGroupStatistics) {
          // console.log("HLayerGroupStatistics", data.Data);
          const list = (data.Data as IBubbleDataItem[]) || [];
          list.forEach((item, index: number) => {
            cacheBubbleObj[item.LayerIdS].text[index].text(
              String(item.TagDataList.length || 0)
            );
            cacheBubbleObj[item.LayerIdS].tagList[index] = item.TagDataList || [];
          });
          batchDraw();
        }

        if (data.ActionCode === EpauletActionCode.HWaterAlarm) {
          console.log("", data.Data);
        }
        if (data.ActionCode === EpauletActionCode.HSceneStatistics) {
          // console.log("", data.Data);
          data.Data.forEach((item: any, index: number) => {
            mapListRef.value[index].pnum = item.TagDataList.length || 0;
          });
        }
      };

      locationWs.onclose = () => {
        //
      };
    }
    let alarmWs: Nullable<WebSocket> = null;
    const wsAlarmItem = shallowRef<Nullable<IGetAlarmItem>>(null);
    const cancelAlarmId = shallowRef<string>("");
    function initAlarmWs() {
      const url = wsUrl.alarm;
      alarmWs = new WebSocket(url);
      alarmWs.onmessage = async (event) => {
        const data = JSON.parse(event.data);
        // console.log("=>", data);
        const getData = JSON.parse(data.data);
        // 
        if (data.actcode === wsAlarmType.getAlarm) {
          const res = getData as IGetAlarmItem;
          // console.log("", res);
          wsAlarmItem.value = res;
          ElMessage.info("");
          if (warningWidth.value === warningListWidth) {
            warningWidth.value = "0";
          }
        }

        // 
        if (data.actcode === wsAlarmType.cancelAlarm) {
          const res = getData as string;
          // console.log("", res);
          cancelAlarmId.value = res;
        }
      };
    }

    function showWarningList() {
      warningWidth.value =
        warningWidth.value !== warningListWidth ? warningListWidth : "0";
    }

    async function getAllAlarmType() {
      const params = {
        act: "E83yVQOUUMYKt6jRYRI1586428010174",
      };
      const res: unknown = await alarmApi.getAllAlarmType(params);
      const { data } = res as IResponseCommon<IAlarmType[]>;
      alarmType.value = data.map((item) => ({
        label: item.alarmtype,
        value: item.alarmtype,
      })) as ISelectOption[];
    }

    /**
     * @description:    
     * @param {*} show 
     * @param {*} areaids id
     * @return {*}
     */
    function changeAreaVisible(show: boolean, areaids: string) {
      //  
      const group = cacheAreaObj[areaids];
      group.visible(show);
      //  
      batchDraw();
      ElMessage.success("");
    }
    //  |  
    function toggleAllFenceVisible(show: boolean) {
      fenceListInfo.allData.forEach((item) => {
        if (item.show !== show) {
          item.show = show;
          cacheAreaObj[item.areaids].visible(show);
        }
      });
      batchDraw();
      ElMessage.success("");
    }

    //    shape
    function destroyLayer(type: TDestoryType = "all") {
      const destroy = {
        layer: () => {
          // 
          allGroup.cameraGroup.destroyChildren();
          for (const key in cacheCameraObj) {
            delete cacheCameraObj[key];
          }
          // 
          allGroup.basestationGroup.destroyChildren();
          for (const key in cacheBasestationObj) {
            delete cacheCameraObj[key];
          }
          // 
          allGroup.locateBubbleGroup.destroyChildren();
          for (const key in cacheBubbleObj) {
            delete cacheBubbleObj[key];
          }
        },
        area: () => {
          allGroup.fenceGroup.destroyChildren();
          for (const key in cacheAreaObj) {
            delete cacheAreaObj[key];
          }
        },
        person: () => {
          allGroup.personGroup.destroyChildren();
          for (const key in cachePersonObj) {
            delete cachePersonObj[key];
          }
        },
      };
      if (type === "all") {
        Object.keys(destroy).map((func) => {
          destroy[func]();
        });
      } else {
        destroy[type]();
      }
    }

    function handleCloseFollow() {
      status.isFollowing = false;
      followObj.message?.close();
      ((cacheFollowingPerson as Group).children[0] as Shape)?.shadowColor(
        DEFAULT_COLORS.TRANSPARENT
      );
      batchDraw();
    }

    //    
    async function handleLocate() {
      console.log(alarmInfo.form);
      await changeMap(alarmInfo.form.ShowSceneNum);
      alarmInfo.visible = false;
      const { x, y } = strToPos(alarmInfo.form.SourcePointNum);
      (stage as Stage).setAttrs({
        x: -(x * state.scale - window.innerWidth / 2),
        y: -(y * state.scale - (window.innerHeight - titleHeight) / 2),
      });
      initAlarmIcon({ x, y, id: alarmInfo.form.Id });
      batchDraw();
    }

    // 
    // window resize
    function handleWindowResize() {
      if (!cacheBgImage) return;
      state.scale = Number((window.innerWidth / cacheBgImage.width()).toFixed(2));
      scaleBoundary.minScale = state.scale;
      state.sx = 0;
      state.sy = 0;
      const attrs = {
        x: state.sx,
        y: state.sy,
        width: window.innerWidth,
        height: window.innerHeight - titleHeight,
        scale: {
          x: state.scale,
          y: state.scale,
        },
      };
      (stage as Stage).setAttrs(attrs);
      batchDraw();
    }

    const debounceRem = debounce(computedRem, 100);
    const debounceResize = debounce(handleWindowResize, 100);

    onMounted(() => {
      getAllMap();
      getAllAlarmType();
      computedRem();
      window.addEventListener("resize", debounceRem);
      window.addEventListener("resize", debounceResize);
    });

    onUnmounted(() => {
      clickTimer && clearTimeout(clickTimer);
      locationWs?.close();
      alarmWs?.close();
      window.removeEventListener("resize", debounceRem);
      document.documentElement.style.fontSize = "";
      window.removeEventListener("resize", debounceResize);
    });

    return {
      warningWidth,
      showWarningList,
      mapIdRef,
      mapNameRef,
      mapListRef,
      showDetails,
      mainNavRef,
      visibleObj,
      status,
      showMenu,
      drawerInfo,
      fenceInfo,
      fenceListInfo,
      ruleListInfo,
      ruleFormRef,
      ruleItemDetail,
      types: TYPES,
      navClick,
      resetNavIndex,
      equipSwitchClick,
      fenceInfoFormRef,
      handleAddFence,
      handleFenceDraw,
      handleEditFence,
      handleSubmitFence,
      handleDelFence,
      fenceCurrentChange,
      drawFenceInfo,
      handleRevokeFence,
      handleRedrawFence,
      handleCancelDrawFence,
      handleFinishDrawFence,
      contextmenuTypeRef,
      contxtmenuPosition,
      contextmenuList,
      detailsForm,
      trackPlayback,
      hideDetails,
      handleClickTrackPlayback,
      handleOpenContextmenu,
      handleClickSeeTrackPlayback,
      seeNowVideo,
      seeHistoryVideo,
      handleCloseAllVideo,
      handleCloseAllHistoryVideo,
      popMonitorVideo,
      popHistoryMonitorVideo,
      watchMonitorListPanelState,
      watchHistoryMonitorListPanelState,
      monitorListInfo,
      showEquipDetails,
      alarmType,
      alarmForm,
      transferProps,
      transferData,
      handleSeeRules,
      handleAddRule,
      handleEditRule,
      handleSubmitRule,
      handleDelRule,
      areaRulesOption,
      ruleCurrentChange,
      ruleDisabledDate,
      timerPickerChange,
      ruleItemDetailRule,
      changeAreaVisible,
      toggleAllFenceVisible,
      layerPerson,
      layerPosition,
      controlButtonList,
      controlButtonChange,
      closeMapChoosen,
      changeMap,
      leftPannelSearch,
      querySearch,
      searchChange,
      searchDevice,
      person,
      userSearchRes,
      alarmInfo,
      alarmListInfo,
      alarmCurrentChange,
      getAlarmListByCondition,
      handleHandleAlarm,
      alarmHandleSubmit,
      getBuildByNum,
      showAlarmDetail,
      getHandleExample,
      wsAlarmItem,
      cancelAlarmId,
      getAlarmTotal,
      getAlarmUnHandle,
      handleTypeList,
      handleExample,
      trackPlaybackFormRef,
      playbackObj,
      changeRate,
      togglePlay,
      changeProcess,
      playbackClose,
      personFollow,
      getMapByNum,
      imgUrl,
      handleCloseFollow,
      handleLocate,
    };
  },
});
</script>

