
import { defineComponent, reactive, toRefs, watch } from "vue";
import { getMapByNum } from "@/utils/auth";
export default defineComponent({
  name: "LabelItem",
  props: {
    deviceData: {
      type: Object
    }
  },
  setup(props) {
    const form = reactive({
      dname: "",
      dnum: "",
      dtype: "",
      status: "",
      hearttime: "",
      currentalarm: "", // 
      voltage: 0, // 
      // eslint-disable-next-line @typescript-eslint/camelcase
      bracelet_voltage: 0 // 
    });

    watch(
      () => props.deviceData,
      newVal => {
        Object.assign(form, newVal);
      },
      {
        immediate: true,
        deep: true
      }
    );

    return {
      ...toRefs(form),
      getMapByNum
    };
  }
});
