<template>
  <el-dialog v-model="visibleDialog" v-bind="$attrs" :width="width">
    <slot />
    <template v-if="showFooterButtons" #footer>
      <el-button @click="handleCancel" plain> </el-button>
      <el-button
        type="primary"
        @click="handleConfirm"
        :disabled="confirmButtonDisabled"
        > </el-button
      >
    </template>
  </el-dialog>
</template>

<script lang="ts">
import { computed, defineComponent } from "vue";

export default defineComponent({
  name: "HdDialog",
  inheritAttrs: false,
  props: {
    visible: {
      type: Boolean,
      default: false
    },
    showFooterButtons: {
      type: Boolean,
      default: false
    },
    width: {
      type: String,
      default: "80%"
    },
    confirmButtonDisabled: {
      type: Boolean,
      default: false
    }
  },
  emits: ["update:visible", "confirm", "cancel"],
  setup(props, { emit }) {
    const visibleDialog = computed({
      get: () => props.visible,
      set: val => {
        emit("update:visible", val);
      }
    });

    function handleConfirm() {
      emit("confirm");
    }

    function handleCancel() {
      emit("cancel");
    }

    return {
      visibleDialog,
      handleConfirm,
      handleCancel
    };
  }
});
</script>

<style scoped></style>
