
import {
  defineComponent,
  reactive,
  ref,
  onMounted,
  shallowRef,
  toRefs
} from "vue";
import commonApi from "@/api/common";
import { TYPES } from "../constVariables";
import { IDeviceItem } from "../types";
import { getMapByNum } from "@/utils/auth";
const CLICKED = "clicked";

export default defineComponent({
  name: "BasestationList",
  emits: ["showEquipDetails", CLICKED],
  setup(props, { emit }) {
    const baseData = reactive({
      timeStyle: [
        {
          color: "#0066ff"
        },
        {
          color: "#707070"
        }
      ],
      style: [
        // {
        //   color: "#666600"
        // },
        {
          color: "#FF0000"
        },
        {
          color: "#00FF00"
        },
        {
          color: "#0000FF"
        },
        {
          color: "#FFFF00"
        },
        {
          color: "#00FFFF"
        },
        {
          color: "#FF00FF"
        }
      ],
      timeTipsStyle: [
        {
          color: "#0066ff"
        },
        {
          color: "#707070"
        }
      ]
    });
    const tips = ref([
      {
        icon: "&#xe628;",
        label: "",
        color: "#FF0000"
      },
      {
        icon: "&#xe628;",
        label: "ID",
        color: "#00FF00"
      },
      {
        icon: "&#xe628;",
        label: "",
        color: "#0000FF"
      },
      {
        icon: "&#xe628;",
        label: "",
        color: "#FFFF00"
      },
      {
        icon: "&#xe628;",
        label: "ID",
        color: "#00FFFF"
      },
      {
        icon: "&#xe666;",
        label: "",
        color: "#0066ff"
      },
      {
        icon: "&#xe666;",
        label: "",
        color: "#707070"
      }
    ]);
    const params = reactive({
      page: 1,
      pagesize: 10,
      dtype: "",
      dnum: ""
    });
    const loading = shallowRef<boolean>(false);
    const tableData = ref<IDeviceItem[]>([]);
    const total = shallowRef<number>(0);
    // 
    async function getList() {
      loading.value = true;
      const param = {
        act: "mKQQKGa49xhcOWaBjDI1612407197459",
        curpage: params.page,
        // eslint-disable-next-line @typescript-eslint/camelcase
        sys_showpagesize: params.pagesize,
        dtype: params.dtype,
        dnum: params.dnum
      };
      const res = await commonApi.getCommonApi(param);
      const { data } = res as any;

      data.content.forEach((item: any) => {
        if (item.eattribute) {
          const attrs = item.eattribute.split(",");
          attrs.forEach((elem: any) => {
            const attrName = elem.split("|");
            const arr = [
              "status", // 
              "hearttime", // 
              "softver", // 
              "hardwarever", // 
              "version", // 
              "starttype", // 
              "checkState", // 
              "maxstr", // 
              "uwbr4checking", // UWBR4
              "uwbt3checking", // UWBT3
              "rfidchecking", // RFID
              "pattern" // 
            ];
            arr.forEach(prop => {
              if (!item[prop] && attrName[0] === prop) {
                item[prop] = attrName[1].split(":")[1];
              }
            });
          });
        }
        let extinfo = item.extinfo;
        if (extinfo) {
          if (!extinfo.startsWith("{")) {
            extinfo = JSON.parse(JSON.stringify(extinfo));
          } else {
            extinfo = JSON.parse(extinfo);
          }

          item.x = extinfo.xcoordinate; // x
          item.y = extinfo.ycoordinate; // y
        }
      });
      tableData.value = data.content;
      console.log("tableData.value", tableData.value);
      total.value = data.count;
      loading.value = false;
    }
    // 
    function styleFilter(item: any) {
      return item.status == "" ? 0 : 1;
    }

    // 
    function handleDetail(row: IDeviceItem) {
      emit("showEquipDetails", row, TYPES.basestation);
      emit(CLICKED);
    }
    // 
    async function changePage(page: number) {
      params.page = page;
      getList();
    }
    // 
    async function search() {
      params.page = 1;
      getList();
    }

    onMounted(() => {
      getList();
    });

    return {
      ...toRefs(baseData),
      tips,
      params,
      loading,
      tableData,
      total,
      handleDetail,
      changePage,
      search,
      getMapByNum,
      styleFilter
    };
  }
});
