<template>
  <div ref="hsitoryMonitorListPanelRef" class="historical-mon">
    <div class="top-title">
      <span>{{ cameraName }}</span>
      <button @click="hidden" @touchstart.stop.prevent="hidden" />
    </div>
    <!--  -->
    <div class="h5videowrapper h5container" :style="getStyle()">
      <span v-show="isLoading">...</span>
      <video
        ref="playerRef"
        class="history-video"
        autoplay
        controls="true"
        style="width:100%; height:100%;object-fit: fill"
        @timeupdate="handleVideoProgress"
      />
    </div>
    <div v-show="players" class="video-controls">
      <div class>
        <span
          v-show="!playStatus"
          class="icon iconfont"
          @click="changePlayStatus(true)"
          >&#xe635;</span
        >
        <span
          v-show="playStatus"
          class="icon iconfont"
          @click="changePlayStatus(false)"
          >&#xe644;</span
        >
      </div>
      <span class="time">{{ currentTime != 0 ? currentTime : "" }}</span>
      <div ref="progressBarRef" class="progress" @click="handleProgress">
        <div ref="innerBarRef" class="inner-bar"></div>
      </div>
      <span class="time">{{ eTime }}</span>
      <div class="rate">
        <span>{{ currentRate }}</span>
        <ul class="rate-list">
          <li @click="changeRate(5.0)">5.0</li>
          <li @click="changeRate(4.0)">4.0</li>
          <li @click="changeRate(3.0)">3.0</li>
          <li @click="changeRate(2.0)">2.0</li>
          <li @click="changeRate(1.5)">1.5</li>
          <li @click="changeRate(1.2)">1.2</li>
          <li @click="changeRate(1.0)">1.0</li>
          <li @click="changeRate(0.5)">0.5</li>
          <li @click="changeRate(0.2)">0.2</li>
        </ul>
      </div>
    </div>
    <div v-if="timePicker" class="block" style="width:100%;">
      <div style="width:38%; float: left;">
        <el-date-picker
          v-model="day"
          type="date"
          value-format="timestamp"
          placeholder=""
        >
        </el-date-picker>
      </div>
      <div style="width:30%; float: left;">
        <el-time-picker
          v-model="sTime"
          value-format="HH:mm:ss"
          style="width:100%"
          placeholder=""
        >
        </el-time-picker>
      </div>
      <div style="width:30%; padding-left: 10px; float: left;">
        <el-time-picker
          v-model="eTime"
          value-format="HH:mm:ss"
          style="width:100%"
          placeholder=""
        >
        </el-time-picker>
      </div>
      <button
        class="el-button el-button--medium el-button--info"
        style="width:100%;background-color: #02163a"
        @click="searchVideo"
      >
        
      </button>
    </div>
  </div>
</template>
<script lang="ts">
import flvjs from "hd-flv";
import { httpFlvServer } from "@/utils/urls";
import {
  defineComponent,
  onUnmounted,
  ref,
  computed,
  watch,
  nextTick,
  reactive,
  toRefs
} from "vue";
import { ElMessage } from "element-plus";

const CHANGE_HISTORYMONITORLISTPANEL_STATE =
  "changeHistoryMonitorListPanelState";
export default defineComponent({
  name: "HistpryMonitor",
  props: {
    historyCameraData: {
      type: Object,
      required: true
    },
    historyPlayCount: {
      type: Number,
      required: true
    }
  },
  emits: [CHANGE_HISTORYMONITORLISTPANEL_STATE],
  setup(props, { emit }) {
    const innerBarRef = ref();
    const progressBarRef = ref();
    const playerRef = ref();
    const players = ref(); // player

    const data = reactive({
      playList: {} as Record<string, any>, // 4
      day: "",
      sTime: 0,
      eTime: 0,
      startTime: 0,
      endTime: 0,
      playStatus: false,
      timePicker: true,
      currentTime: 0,
      overTime: 0,
      currentRate: 1.0,
      currentId: "",
      cameraId: 0,
      cameraName: "",
      isLoading: false
    });
    const dataRef = toRefs(data);

    const allDuration: any = computed(() => {
      const time1: any = data.sTime.toString().split(":");
      const time2: any = data.eTime.toString().split(":");
      const time =
        (time2[0] - time1[0]) * 3600000 +
        (time2[1] - time1[1]) * 60000 +
        (time2[2] - time1[2]) * 1000;
      return time;
    });
    const progressLength: any = computed(() => {
      return progressBarRef.value[0].clientWidth;
    });

    const originTime: any = computed(() => {
      const time: any = data.sTime.toString().split(":");
      return data.day + time[0] * 3600000 + time[1] * 60000 + time[2] * 1000;
    });

    onUnmounted(() => {
      // 
      if (typeof players.value !== "undefined" && players.value.e != null) {
        if (players.value != null) {
          players.value.pause();
          players.value.unload();
          players.value.detachMediaElement();
          players.value.destroy();
        }
      }
    });

    // NVR 
    function formatHaiKangTime(timestamps: string) {
      const timestamp = parseFloat(timestamps);
      const year = new Date(timestamp).getFullYear();
      const month = new Date(timestamp).getMonth() + 1;
      const day = new Date(timestamp).getDate();
      const hour = new Date(timestamp).getHours();
      const minute = new Date(timestamp).getMinutes();
      const second = new Date(timestamp).getSeconds();
      return `${year}${month > 9 ? month : "0" + month}${
        day > 9 ? day : "0" + day
      }T${hour > 9 ? hour : "0" + hour}${minute > 9 ? minute : "0" + minute}${
        second > 9 ? second : "0" + second
      }Z`;
    }

    // NVR 
    function formatDahuaTime(timestamps: string) {
      const timestamp = parseFloat(timestamps);
      const year = new Date(timestamp).getFullYear();
      const month = new Date(timestamp).getMonth() + 1;
      const day = new Date(timestamp).getDate();
      const hour = new Date(timestamp).getHours();
      const minute = new Date(timestamp).getMinutes();
      const second = new Date(timestamp).getSeconds();
      return `${year}_${month > 9 ? month : "0" + month}_${
        day > 9 ? day : "0" + day
      }_${hour > 9 ? hour : "0" + hour}_${minute > 9 ? minute : "0" + minute}_${
        second > 9 ? second : "0" + second
      }`;
    }

    function initPlayer(playList: any) {
      // 
      if (flvjs.isSupported()) {
        const player = ref();
        const video = player.value; // video
        if (
          !data.startTime &&
          !data.endTime &&
          !playList.startTime &&
          !playList.endTime
        ) {
          return;
        }
        data.startTime = playList.startTime
          ? playList.startTime
          : data.startTime;
        data.endTime = playList.endTime ? playList.endTime : data.endTime;
        if (playList.startTime && playList.endTime) {
          data.timePicker = false;
        } else {
          data.timePicker = true;
        }
        if (video) {
          data.isLoading = true;
          const id = playList.camera.id; // id
          data.cameraId = id;
          data.cameraName = playList.camera.name;
          let rtsp = "";
          if (
            "" == playList.camera.brand ||
            "" == playList.camera.brand
          ) {
            const sTime = formatHaiKangTime(data.startTime.toString());
            const eTime = formatHaiKangTime(data.endTime.toString());
            rtsp = `rtsp://${playList.camera.nvr_username} : ${playList.camera.nvr_password}@${playList.camera.nvr_ip}/Streaming/tracks/${playList.camera.nvr_channel}01/?starttime=${sTime}&endtime=" +
              ${eTime}&codecType=h264&subtype=1`;
          } else if ("" == playList.camera.brand) {
            const sTime = formatDahuaTime(data.startTime.toString())
              .replace(/-/g, "_")
              .replace(/:/g, "_")
              .replace(/ /g, "_");
            const eTime = formatDahuaTime(data.endTime.toString())
              .replace(/-/g, "_")
              .replace(/:/g, "_")
              .replace(/ /g, "_");

            rtsp = `rtsp://${playList.camera.nvr_username} : ${playList.camera.nvr_password}@${playList.camera.nvr_ip}/cam/playback?channel=${playList.camera.nvr_channel}&subtype=1&starttime=${sTime}&endtime=" +
              ${eTime}`;
          }

          players.value = playList.camera.player; //player
          if (playList.camera.id != data.currentId) {
            data.currentTime = 0;
            data.eTime = 0;
            data.currentRate = 1.0;
          }
          data.currentId = playList.camera.id;
          const Url = `${httpFlvServer}/${id}/?url=${encodeURIComponent(
            rtsp
          )}}&brand=${playList.camera.brand}`;
          players.value = flvjs.createPlayer({
            type: "flv",
            isLive: false, // isLive:true,false
            hasVideo: true,
            url: Url
          });

          const player = players.value;
          player.on(flvjs.Events.METADATA_ARRIVED, () => {
            console.log("--== meta data arrived ==--");
            data.isLoading = false;
            data.playStatus = true;
          });

          video.addEventListener("pause", function() {
            // 
            data.playStatus = true;
          });
          video.addEventListener("play", function() {
            // 
            data.playStatus = false;
          });

          players.value.attachMediaElement(video);
          try {
            players.value.load();
            players.value.play();
          } catch (error) {
            console.log(error);
          }
        }
      }
    }

    watch(props.historyCameraData, newValue => {
      if (newValue) {
        data.playList = newValue;
        nextTick(() => {
          if (!newValue.active) {
            // 
            // 
            if (
              typeof players.value !== "undefined" &&
              players.value.e != null
            ) {
              if (players.value != null) {
                players.value.pause();
                players.value.unload();
                players.value.detachMediaElement();
                players.value.destroy();
              }
            }
          } else {
            // 
            initPlayer(newValue);
          }
        });
      }
    });

    function getStyle() {
      if (props.historyPlayCount === 1) {
        return "width:100%; height:100%";
      } else {
        return "width:100%; height:26rem";
      }
    }

    // 
    function hidden() {
      if (typeof players.value !== "undefined" && players.value.e != null) {
        if (players.value != null) {
          players.value.pause();
          players.value.unload();
          players.value.detachMediaElement();
          players.value.destroy();
        }
      }
      // 
      emit(CHANGE_HISTORYMONITORLISTPANEL_STATE, data.cameraId);
    }

    function formatDay(timestamps: string) {
      const timestamp = parseFloat(timestamps);
      const year = new Date(timestamp).getFullYear();
      const month = new Date(timestamp).getMonth() + 1;
      const day = new Date(timestamp).getDate();
      return `${year}-${month > 9 ? month : "0" + month}-${
        day > 9 ? day : "0" + day
      }`;
    }

    // 
    function searchVideo() {
      if (!data.sTime || !data.eTime || !data.day) {
        ElMessage.error("");
        return;
      }
      data.startTime = new Date(
        Date.parse(formatDay(data.day) + " " + data.sTime)
      ).getTime();
      data.endTime = new Date(
        Date.parse(formatDay(data.day) + " " + data.eTime)
      ).getTime();

      // 
      initPlayer(data.playList);
    }

    function changePlayStatus(flag: boolean) {
      const player = ref();
      if (flag) {
        player.value[0].play();
      } else {
        player.value[0].pause();
      }
      data.playStatus = flag;
    }

    function formatTimes(timestamps: string) {
      const timestamp = parseFloat(timestamps);
      const hour = new Date(timestamp).getHours();
      const minute = new Date(timestamp).getMinutes();
      const second = new Date(timestamp).getSeconds();
      return `${hour > 9 ? hour : "0" + hour}:${
        minute > 9 ? minute : "0" + minute
      }:${second > 9 ? second : "0" + second}`;
    }

    function handleProgress(e: any) {
      if (!data.sTime || !data.eTime || !data.day) {
        ElMessage.error("");
        return;
      }

      innerBarRef.value[0].style.width = e.offsetX + "px";
      const duration = Math.floor(
        (e.offsetX / progressBarRef.value[0].offsetWidth) * allDuration.value
      );
      const time = data.sTime.toString().split(":");
      data.startTime = Number(
        data.day +
          Number(time[0]) * 3600000 +
          Number(time[1]) * 60000 +
          Number(time[2]) * 1000 +
          duration
      );
      initPlayer(data.playList);
    }

    function handleVideoProgress(e: any) {
      data.currentTime = Number(
        formatTimes(e.target.currentTime * 1000 + data.startTime + "")
      );
      innerBarRef.value[0].style.width =
        ((e.target.currentTime * 1000 + data.startTime - originTime.value) /
          (data.endTime - data.startTime)) *
          progressLength.value +
        "px";
    }

    function changeRate(rate: number) {
      data.currentRate = rate;
      playerRef.value[0].playbackRate = rate;
    }

    return {
      ...dataRef,
      handleProgress,
      getStyle,
      hidden,
      searchVideo,
      changePlayStatus,
      handleVideoProgress,
      changeRate
    };
  }
});
</script>
<style lang="scss" scoped>
.monitorPlayer-panel {
  position: fixed;
  /*right: -10px;*/
  /*top: 150px;*/
  z-index: 1;
}
.history-video {
  width: 100%;
  height: 100%;
  background: black;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  -moz-background-size: 100% 100%;
  background-image: url("/images/connecting.jpg");
}

.historical-mon {
  /* background: rgba(30, 42, 66, 0.88); */
  width: 100%;
  height: 100%;
  color: #ffffff;
}

.historical-mon .top-title {
  width: 100%;
  line-height: 30px;
  color: #fff;
  border-bottom: 1px solid rgba(178, 223, 255, 0.2);
  /* background: #274a7f; */
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 1;
}

.historical-mon .top-title span {
  margin-left: 10px;
}

.historical-mon .top-title button {
  width: 20px;
  height: 20px;
  border: 0;
  cursor: pointer;
  background: url(/images/x.png) no-repeat;
  background-size: 100% 100%;
  background-position: center;
}

.historical-mon ::-webkit-scrollbar {
  /**/
  width: 6px; /**/
}

.historical-mon ::-webkit-scrollbar-thumb {
  /**/
  border-radius: 3px;
  background: rgba(67, 97, 122, 1);
}

.video-controls {
  width: 100%;
  height: 3rem;
  display: flex;
  align-items: center;
  justify-content: center;

  .icon {
    font-size: 1.8rem;
  }

  .progress {
    width: 70%;
    height: 1rem;
    border-radius: 0.5rem;
    margin: 0 0.5rem;
    background: rgba(0, 0, 0, 0.6);

    .inner-bar {
      width: 0px;
      height: 100%;
      border-radius: 0.5rem;
      background: rgba(48, 163, 204, 0.5);
    }
  }
  .rate {
    margin-left: 1rem;
    position: relative;
    color: #fff;
    font-size: 1.8rem;
    width: 5rem;
    height: 100%;
    line-height: 3rem;
    text-align: center;

    &:hover > .rate-list {
      display: block;
    }

    .rate-list {
      position: absolute;
      bottom: 2rem;
      background: rgba(0, 0, 0, 0.5);
      border-radius: 0.4rem;
      display: none;

      li {
        height: 2rem;
        line-height: 2rem;
        width: 4rem;
        text-align: center;

        &:hover {
          background: rgba(0, 0, 0, 0.2);
        }
      }
    }
  }
  .time {
    margin: 0 0.5rem;
  }
}

.historical-mon .el-input__icon {
  padding-bottom: 1.3rem;
}

.historical-mon .check-bar > li .el-range-editor {
  display: flex;
  align-items: center;
}

.historical-mon.check-bar > li .el-range-editor i {
  display: inline-block;
  height: 3.2rem !important;
  line-height: 3.2rem;
  padding-bottom: 1.3rem;
  box-sizing: border-box;
}

.historical-mon.check-bar > li .el-range-editor span {
  height: 3.4rem;
  line-height: 3.4rem;
  box-sizing: border-box;
}

.historical-mon .el-pagination__total {
  color: #fff;
}

.historical-mon .el-checkbox__label {
  font-size: 1.4rem;
}

.historical-mon .el-date-editor .el-range-input {
  background-color: transparent;
  color: #eee;
  text-align: center;
  font-size: 1.2rem;
  padding-bottom: 0.2rem;
}

.historical-mon .el-date-editor .el-range-separator {
  color: #eee;
  font-size: 1.2rem;
  padding-bottom: 1.5rem;
}

.historical-mon .el-range-separator {
  padding-bottom: 1.4rem;
  font-size: 1.2rem;
  color: #eee;
}

.historical-mon .el-date-editor {
  border-color: #5789ff;
  background-color: #02163a;
  color: #eee;
  text-align: center;
  font-size: 1.2rem;
}
</style>
