<template>
  <!--  -->
  <div class="station">
    <el-form :model="params" label-width="80px" :inline="true" size="small">
      <el-form-item label="">
        <el-input v-model.trim="params.dnum" clearable @change="search" />
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="search"></el-button>
      </el-form-item>
    </el-form>

    <div class="tips">
      <div v-for="item in tips" :key="item.label">
        <span
          class="icon iconfont"
          v-html="item.icon"
          :style="{ color: item.color }"
        ></span>
        <span>{{ item.label }}</span>
      </div>
    </div>
    <div class="station-list clearfix" @touchstart="getFirstY" ref="listBar">
      <div
        v-for="item in tableData"
        :key="item.id"
        @click="handleDetail(item)"
        class="station-item"
        :class="item.isSelected ? 'checked' : 'item'"
      >
        <div class="direction-light">
          <span class="icon iconfont" :style="style[item.uwbt3checking || 0]"
            >&#xe628;</span
          >
          <span class="icon iconfont" :style="style[item.uwbr4checking || 0]"
            >&#xe628;</span
          >
          <span class="icon iconfont" :style="style[item.rfidchecking || 0]"
            >&#xe628;</span
          >
        </div>
        <div class="direction-light">
          <span class="icon iconfont" :style="style[item.uwbt3checking || 0]"
            >T3</span
          >
          <span class="icon iconfont" :style="style[item.uwbr4checking || 0]"
            >R4</span
          >
          <span class="icon iconfont" :style="style[item.rfidchecking || 0]"
            >RFID</span
          >
        </div>
        <span
          class="icon iconfont item-square"
          :style="timeStyle[styleFilter(item)]"
          >&#xe666;</span
        >
        <p :title="item.dnum">{{ item.dnum }}</p>
        <p :title="item.dtype">{{ item.dtype }}</p>
        <p :title="item.version">{{ item.version }}</p>
      </div>
    </div>

    <el-pagination
      v-show="total && tableData && tableData.length"
      background
      v-model:current-page="params.page"
      layout="total, prev, pager, next, jumper"
      :total="total"
      @current-change="changePage"
    />
  </div>
</template>

<script lang="ts">
import {
  defineComponent,
  reactive,
  ref,
  onMounted,
  shallowRef,
  toRefs
} from "vue";
import commonApi from "@/api/common";
import { TYPES } from "../constVariables";
import { IDeviceItem } from "../types";
import { getMapByNum } from "@/utils/auth";
const CLICKED = "clicked";

export default defineComponent({
  name: "BasestationList",
  emits: ["showEquipDetails", CLICKED],
  setup(props, { emit }) {
    const baseData = reactive({
      timeStyle: [
        {
          color: "#0066ff"
        },
        {
          color: "#707070"
        }
      ],
      style: [
        // {
        //   color: "#666600"
        // },
        {
          color: "#FF0000"
        },
        {
          color: "#00FF00"
        },
        {
          color: "#0000FF"
        },
        {
          color: "#FFFF00"
        },
        {
          color: "#00FFFF"
        },
        {
          color: "#FF00FF"
        }
      ],
      timeTipsStyle: [
        {
          color: "#0066ff"
        },
        {
          color: "#707070"
        }
      ]
    });
    const tips = ref([
      {
        icon: "&#xe628;",
        label: "",
        color: "#FF0000"
      },
      {
        icon: "&#xe628;",
        label: "ID",
        color: "#00FF00"
      },
      {
        icon: "&#xe628;",
        label: "",
        color: "#0000FF"
      },
      {
        icon: "&#xe628;",
        label: "",
        color: "#FFFF00"
      },
      {
        icon: "&#xe628;",
        label: "ID",
        color: "#00FFFF"
      },
      {
        icon: "&#xe666;",
        label: "",
        color: "#0066ff"
      },
      {
        icon: "&#xe666;",
        label: "",
        color: "#707070"
      }
    ]);
    const params = reactive({
      page: 1,
      pagesize: 10,
      dtype: "",
      dnum: ""
    });
    const loading = shallowRef<boolean>(false);
    const tableData = ref<IDeviceItem[]>([]);
    const total = shallowRef<number>(0);
    // 
    async function getList() {
      loading.value = true;
      const param = {
        act: "mKQQKGa49xhcOWaBjDI1612407197459",
        curpage: params.page,
        // eslint-disable-next-line @typescript-eslint/camelcase
        sys_showpagesize: params.pagesize,
        dtype: params.dtype,
        dnum: params.dnum
      };
      const res = await commonApi.getCommonApi(param);
      const { data } = res as any;

      data.content.forEach((item: any) => {
        if (item.eattribute) {
          const attrs = item.eattribute.split(",");
          attrs.forEach((elem: any) => {
            const attrName = elem.split("|");
            const arr = [
              "status", // 
              "hearttime", // 
              "softver", // 
              "hardwarever", // 
              "version", // 
              "starttype", // 
              "checkState", // 
              "maxstr", // 
              "uwbr4checking", // UWBR4
              "uwbt3checking", // UWBT3
              "rfidchecking", // RFID
              "pattern" // 
            ];
            arr.forEach(prop => {
              if (!item[prop] && attrName[0] === prop) {
                item[prop] = attrName[1].split(":")[1];
              }
            });
          });
        }
        let extinfo = item.extinfo;
        if (extinfo) {
          if (!extinfo.startsWith("{")) {
            extinfo = JSON.parse(JSON.stringify(extinfo));
          } else {
            extinfo = JSON.parse(extinfo);
          }

          item.x = extinfo.xcoordinate; // x
          item.y = extinfo.ycoordinate; // y
        }
      });
      tableData.value = data.content;
      console.log("tableData.value", tableData.value);
      total.value = data.count;
      loading.value = false;
    }
    // 
    function styleFilter(item: any) {
      return item.status == "" ? 0 : 1;
    }

    // 
    function handleDetail(row: IDeviceItem) {
      emit("showEquipDetails", row, TYPES.basestation);
      emit(CLICKED);
    }
    // 
    async function changePage(page: number) {
      params.page = page;
      getList();
    }
    // 
    async function search() {
      params.page = 1;
      getList();
    }

    onMounted(() => {
      getList();
    });

    return {
      ...toRefs(baseData),
      tips,
      params,
      loading,
      tableData,
      total,
      handleDetail,
      changePage,
      search,
      getMapByNum,
      styleFilter
    };
  }
});
</script>
<style lang="scss" scoped>
.station {
  border-radius: 0.5rem;
  .station-list {
    height: 37.3rem;
    padding: 0 1rem;
    overflow: auto;
    &::-webkit-scrollbar {
      /**/
      width: 0.5rem; /**/
    }
    &::-webkit-scrollbar-thumb {
      /**/
      /*!**!*/
      border-radius: 0.3rem;
      background: rgba(255, 255, 255, 0.3);
      height: 3rem;
    }
    & > div {
      float: left;
      width: 10%;
      height: 9rem;
      display: flex;
      justify-content: center;
    }
    .station-item {
      width: 14rem;
      height: 14rem;
      margin-top: 2rem;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      text-align: center;
      cursor: pointer;
      margin-left: 3rem;
      .direction-light {
        width: 100%;
        display: flex;
        justify-content: space-between;
        span {
          font-size: 1.4rem;
          margin: 0 1rem;
        }
      }

      .item-square {
        color: rgb(0, 102, 255);
      }
      span {
        font-size: 4rem;
        margin-top: 0.2rem;
      }
      p {
        text-align: center;
        font-size: 1.2rem;
        margin: 0.1rem 0;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        line-height: 2rem;
        height: 2rem;
      }
    }
    .item {
      background: url("../../../../assets/dashboard/equip_unchecked.png")
        no-repeat center;
      background-size: 100% 100%;
      &:hover {
        background: url("../../../../assets/dashboard/equip_checked.png")
          no-repeat center;
        background-size: 100% 100%;
      }
    }
    .checked {
      background: url("../../../../assets/dashboard/equip_checked.png")
        no-repeat center;
      background-size: 100% 100%;
    }
  }
  .clearfix:after {
    content: "."; /*    */
    display: block; /*  */
    height: 0; /* 0 */
    visibility: hidden; /*  */
    clear: both; /*  */
  }
}

.tips {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  padding: 10px 20px;
  background: rgba(49, 142, 255, 0.2);

  span + span {
    margin-left: 0.5rem;
  }
}
</style>
