
import { imgUrl } from "@/utils/urls";
import { defineComponent, PropType } from "vue";
import { IMapArrItem } from "../types";

const CHANGE_MAP = "changeMap";

export default defineComponent({
  name: "MapChoosen",
  props: {
    mapId: {
      type: String,
      required: true
    },
    mapList: {
      type: Object as PropType<IMapArrItem[]>,
      default: () => []
    }
  },
  emits: [CHANGE_MAP],
  setup(props, { emit }) {
    function changeMap(mapId: string) {
      if (mapId === props.mapId) return;
      emit(CHANGE_MAP, mapId);
    }
    return {
      imgUrl,
      changeMap
    };
  }
});
