
import { defineComponent, reactive, ref, onMounted, shallowRef } from "vue";
import { TYPES } from "../constVariables";
import { ICommonTableParams, IDeviceItem } from "../types";
import { getPagination, getPagintionCount } from "@/utils";
import { getAllDevice, getMapByNum } from "@/utils/auth";
const CLICKED = "clicked";

export default defineComponent({
  name: "CameraList",
  emits: ["showEquipDetails", "seeNowVideo", "seeHistoryVideo", CLICKED],
  setup(props, { emit }) {
    const params = reactive<ICommonTableParams>({
      page: 1,
      prop: "dnum",
      search: ""
    });
    const loading = shallowRef<boolean>(false);
    const tableData = ref<IDeviceItem[]>([]);
    const total = shallowRef<number>(0);
    // 
    async function getList() {
      loading.value = true;
      // localStorage
      const deviceAll = getAllDevice();
      const cameraAll = deviceAll.filter(
        item => item.dtypecode === "Camera_IP"
      );
      cameraAll.forEach((item: any) => {
        if (item.extinfo) {
          const extinfo = JSON.parse(item.extinfo);
          item.UserName = extinfo.UserName;
          item.Password = extinfo.Password;
          item.ChanelNum = extinfo.ChanelNum;
          item.IsYunTai = extinfo.IsYunTai;
          item.IsCameraPhoto = extinfo.IsCameraPhoto;
          item.x = extinfo.xcoordinate; // x
          item.y = extinfo.ycoordinate; // y
        }
      });
      tableData.value = getPagination(
        cameraAll,
        params.page,
        params.prop,
        params.search
      );
      total.value = getPagintionCount(cameraAll, params.prop, params.search);
      loading.value = false;
    }
    // 
    function handleDetail(row: IDeviceItem) {
      emit("showEquipDetails", row, TYPES.camera);
      emit(CLICKED);
    }
    // 
    async function changePage(page: number) {
      params.page = page;
      getList();
    }
    // 
    async function search() {
      params.page = 1;
      getList();
    }

    // 
    function seeNowVideo(row: IDeviceItem) {
      emit("seeNowVideo", row.dids);
    }
    // 
    function seeHistoryVideo(row: IDeviceItem) {
      emit("seeHistoryVideo", row.dids);
    }

    onMounted(() => {
      getList();
    });

    return {
      params,
      loading,
      tableData,
      total,
      changePage,
      search,
      getMapByNum,
      handleDetail,
      seeNowVideo,
      seeHistoryVideo
    };
  }
});
