<template>
  <div>
    <el-form :model="params" label-width="80px" :inline="true" size="small">
      <el-form-item label="IP">
        <el-input v-model.trim="params.search" clearable @change="search" />
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="search"></el-button>
      </el-form-item>
    </el-form>

    <hd-table v-loading="loading" :data="tableData">
      <hd-table-column label="IP" prop="dnum" width="110" />
      <hd-table-column label="" prop="dname" />
      <el-table-column label="" show-overflow-tooltip align="center">
        <template #default="{row}">
          {{ getMapByNum(row.snum).sname }}
        </template>
      </el-table-column>
      <el-table-column width="300" label="" align="center">
        <template #default="{row}">
          <el-button size="mini" type="primary" @click="handleDetail(row)"
            ></el-button
          >
          <el-button size="mini" type="primary" @click="seeNowVideo(row)"
            ></el-button
          >
          <el-button size="mini" type="primary" @click="seeHistoryVideo(row)"
            ></el-button
          >
        </template>
      </el-table-column>
    </hd-table>
    <el-pagination
      v-show="total && tableData && tableData.length"
      background
      v-model:current-page="params.page"
      layout="total, prev, pager, next, jumper"
      :total="total"
      @current-change="changePage"
    />
  </div>
</template>

<script lang="ts">
import { defineComponent, reactive, ref, onMounted, shallowRef } from "vue";
import { TYPES } from "../constVariables";
import { ICommonTableParams, IDeviceItem } from "../types";
import { getPagination, getPagintionCount } from "@/utils";
import { getAllDevice, getMapByNum } from "@/utils/auth";
const CLICKED = "clicked";

export default defineComponent({
  name: "CameraList",
  emits: ["showEquipDetails", "seeNowVideo", "seeHistoryVideo", CLICKED],
  setup(props, { emit }) {
    const params = reactive<ICommonTableParams>({
      page: 1,
      prop: "dnum",
      search: ""
    });
    const loading = shallowRef<boolean>(false);
    const tableData = ref<IDeviceItem[]>([]);
    const total = shallowRef<number>(0);
    // 
    async function getList() {
      loading.value = true;
      // localStorage
      const deviceAll = getAllDevice();
      const cameraAll = deviceAll.filter(
        item => item.dtypecode === "Camera_IP"
      );
      cameraAll.forEach((item: any) => {
        if (item.extinfo) {
          const extinfo = JSON.parse(item.extinfo);
          item.UserName = extinfo.UserName;
          item.Password = extinfo.Password;
          item.ChanelNum = extinfo.ChanelNum;
          item.IsYunTai = extinfo.IsYunTai;
          item.IsCameraPhoto = extinfo.IsCameraPhoto;
          item.x = extinfo.xcoordinate; // x
          item.y = extinfo.ycoordinate; // y
        }
      });
      tableData.value = getPagination(
        cameraAll,
        params.page,
        params.prop,
        params.search
      );
      total.value = getPagintionCount(cameraAll, params.prop, params.search);
      loading.value = false;
    }
    // 
    function handleDetail(row: IDeviceItem) {
      emit("showEquipDetails", row, TYPES.camera);
      emit(CLICKED);
    }
    // 
    async function changePage(page: number) {
      params.page = page;
      getList();
    }
    // 
    async function search() {
      params.page = 1;
      getList();
    }

    // 
    function seeNowVideo(row: IDeviceItem) {
      emit("seeNowVideo", row.dids);
    }
    // 
    function seeHistoryVideo(row: IDeviceItem) {
      emit("seeHistoryVideo", row.dids);
    }

    onMounted(() => {
      getList();
    });

    return {
      params,
      loading,
      tableData,
      total,
      changePage,
      search,
      getMapByNum,
      handleDetail,
      seeNowVideo,
      seeHistoryVideo
    };
  }
});
</script>
