<template>
  <header>
    <div class="header">
      <div class="side left">
        <i class="el-icon-time" />
        <span class="time">{{ now }}</span>
      </div>
      <div class="side center"></div>
      <div class="right">
        <header-bar />
      </div>
    </div>
  </header>
</template>

<script lang="ts">
import { defineComponent, onMounted, onUnmounted, shallowRef } from "vue";
import dayjs from "dayjs";
import HeaderBar from "./headerBar.vue";
import { timeFormateStr } from "../content/constVariables";

export default defineComponent({
  name: "Head",
  components: {
    HeaderBar
  },
  setup() {
    const now = shallowRef<string>(dayjs().format(timeFormateStr));
    let timer: null | number = null;
    onMounted(() => {
      timer = setInterval(() => {
        now.value = dayjs().format(timeFormateStr);
      });
    });
    onUnmounted(() => {
      timer && clearInterval(timer);
    });
    return {
      now
    };
  }
});
</script>

<style lang="scss" scoped>
header {
  position: absolute;
  width: 100%;
  height: 80px;
  z-index: 1500;
  font-size: 18px;
  color: #fff;
  background: url("/images/title.png") repeat center center/100% 100%;
  user-select: none;
  .header {
    position: relative;
    height: 60px;
    overflow: hidden;
  }
}
.side {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);

  &.left {
    color: #3dd1ff;
    left: 5%;
    font-size: 24px;
    display: flex;
    align-items: center;
    .time {
      margin-left: 5px;
      letter-spacing: 1px;
      color: #43b0de;
      font-size: 16px;
    }
  }

  &.center {
    left: 50%;
    bottom: 0;
    transform: translateX(-50%);
    font-size: 24px;
    font-weight: bold;
    letter-spacing: 2px;
  }
}
.right {
  height: 100%;
}
</style>
