<template>
  <div>
    <el-form :model="params" label-width="80px" :inline="true" size="small">
      <el-form-item label="">
        <el-input v-model.trim="params.search" clearable @change="search" />
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="search"></el-button>
      </el-form-item>
    </el-form>
    <hd-table v-loading="loading" :data="tableData">
      <hd-table-column label="" prop="DeviceName" />
      <hd-table-column label="" prop="Username" />
      <hd-table-column label="" prop="Position" />
      <hd-table-column width="80" label="" prop="Job_number" />
      <hd-table-column width="80" label="" prop="Room_number" />
      <el-table-column
        width="155"
        label=""
        show-overflow-tooltip
        align="center"
      >
        <template #default="{ row }">
          <span v-if="row.snum" style="color: #3dd1ff">
            {{ getMapByNum(row.snum).sname }}
          </span>
          <span v-if="!row.snum">
            
          </span>
        </template>
      </el-table-column>
      <el-table-column width="170" label="" align="center">
        <template #default="{ row }">
          <el-button size="mini" type="primary" @click="handleDetail(row)"
            ></el-button
          >
          <el-popconfirm title=" " effect="dark">
            <template #reference>
              <el-button type="primary" size="mini"></el-button>
            </template>
          </el-popconfirm>
        </template>
      </el-table-column>
    </hd-table>
    <el-pagination
      v-show="total && tableData && tableData.length"
      background
      v-model:current-page="params.page"
      layout="total, prev, pager, next, jumper"
      :total="total"
      @current-change="changePage"
    />
  </div>
</template>

<script lang="ts">
import {
  onMounted,
  reactive,
  ref,
  defineComponent,
  shallowRef,
  PropType
} from "vue";
import commonApi from "@/api/common";
import { TYPES } from "../constVariables";
import { ICommonTableParams, IPersonItem } from "../types";
import { getPagination, getPagintionCount } from "@/utils";
import { getMapByNum } from "@/utils/auth";
import { IResponseCommon } from "@/types";
import { IPersonArrItem } from "../types/index";
const CLICKED = "clicked";

export default defineComponent({
  name: "LabelList",
  props: {
    wsAllLabel: {
      type: Array as PropType<IPersonArrItem[]>
    }
  },
  emits: ["showEquipDetails", CLICKED],
  setup(props, { emit }) {
    const params = reactive<ICommonTableParams>({
      page: 1,
      prop: "Username",
      search: ""
    });
    let personAll: any[] = [];
    const loading = shallowRef<boolean>(false);

    const tableData = ref<IPersonItem[]>([]);
    const total = shallowRef<number>(0);

    // 
    function getList() {
      tableData.value = getPagination(
        personAll,
        params.page,
        params.prop,
        params.search
      );
      total.value = getPagintionCount(personAll, params.prop, params.search);
      loading.value = false;
    }
    // 
    async function getAllPersonList() {
      loading.value = true;
      const param = {
        act: "rYVwSSwhmDcZRL0yQbO1611304770982"
      };
      const result: unknown = await commonApi.getCommonApi(param);
      const { data } = result as IResponseCommon<IPersonItem[]>;
      data.forEach((item: any) => {
        if (props.wsAllLabel) {
          props.wsAllLabel.forEach(element => {
            if (element.name === item.Username) {
              item.snum = element.LocationSceneId;
            }
          });
        }
      });
      personAll = data;
      // 
      getList();
    }
    // 
    function handleDetail(row: IPersonItem) {
      emit("showEquipDetails", row, TYPES.person);
      emit(CLICKED);
    }

    // 
    async function changePage(page: number) {
      params.page = page;
      getList();
    }
    // 
    async function search() {
      params.page = 1;
      getList();
    }

    onMounted(() => {
      // 
      getAllPersonList();
    });

    return {
      params,
      loading,
      tableData,
      total,
      changePage,
      search,
      getMapByNum,
      handleDetail
    };
  }
});
</script>
