import { strToPos } from ".";
import { IPersonArrItem } from "../types";

export const generatePersonItem = (
  personItem: IPersonArrItem
): IPersonArrItem => {
  const { x, y } = strToPos(personItem.LocationPoint);
  return {
    ...personItem,
    x,
    y,
    name: personItem.ObjData.BindObjName,
    map: personItem.LocationSceneId,
    lids: personItem.TagId,
    ltype: personItem.ObjData.BindObjName
  };
};
