const sceneAll = "sceneAll";
// 
export function setAllScene(sceneall: any) {
  return localStorage.setItem(sceneAll, JSON.stringify(sceneall));
}
/**
 * @description 
 * @param snum 
 * @return map 
 */
export function getMapByNum(snum: string) {
  // localStorage
  const sceneAllStr = localStorage.getItem("sceneAll") as string;
  const sceneAll: [] = JSON.parse(sceneAllStr);
  const map = sceneAll[snum] ? sceneAll[snum] : "";
  return map;
}

const buildingAll = "buildingAll";
// 
export function setAllBuild(buildingall: IBuildItem[]) {
  return localStorage.setItem(buildingAll, JSON.stringify(buildingall));
}

// 
export function getBuildByNum(dnum: string) {
  // localStorage
  const buildAllStr = localStorage.getItem(buildingAll) as string;
  const buildAll: [] = JSON.parse(buildAllStr);
  const map = buildAll[dnum] ? buildAll[dnum] : "";
  return map;
}

/**
 * @description: 
 * @param {Router} router
 * @return {*}
 */
export async function logout(router: Router) {
  await removeToken();
  localStorage.clear();
  router.push("/login");
}
