<template>
  <ul class="fence-box">
    <li
      v-for="(item, index) in list"
      :key="index"
      :class="{ active: index === 0 }"
      @click="handleClick(item, index)"
    >
      {{ item.label }}
    </li>
  </ul>
</template>

<script lang="ts">
interface ILabel {
  label: string;
}

const emitObj = {
  1: "revoke",
  2: "redraw",
  3: "cancel",
  4: "finish"
};

import { defineComponent } from "vue";
export default defineComponent({
  name: "AlarmContent",
  emits: Object.keys(emitObj).map(key => emitObj[key]),
  setup(props, { emit }) {
    const list: ILabel[] = [
      {
        label: ""
      },
      {
        label: ""
      },
      {
        label: ""
      },
      {
        label: ""
      },
      {
        label: ""
      }
    ];
    function handleClick(item: ILabel, index: number) {
      emit(emitObj[index]);
    }
    return {
      list,
      handleClick
    };
  }
});
</script>
