import "normalize.css";
import { createApp } from "vue";
import router from "./router";
import "./permission";
// import store from "./store";
import App from "./App.vue";

import element from "./plugins/element-plus";

import "./style/index.scss";
import hd from "./plugins/hd-element";

// iconfont
import "@/assets/fonts/iconfont/iconfont.css";

// vue-svgicon
import { VueSvgIconPlugin } from "@yzfe/vue3-svgicon";
import "@yzfe/svgicon/lib/svgicon.css";

// directive
import debounceClick from "./directive/debounce-click";

const app = createApp(App);
element(app);
hd(app);
app.directive("debounce-click", debounceClick);
app
  .use(VueSvgIconPlugin, { tagName: "svg-icon" })
  // .use(store)
  .use(router)
  // .use(ElementPlus, { locale })
  .mount("#app");
