<template>
  <el-row>
    <el-col :span="12" :offset="0">
      <el-form-item label="">
        {{ dnum }}
      </el-form-item>
    </el-col>
    <el-col :span="12" :offset="0">
      <el-form-item label="">
        <el-tag v-if="status == ''"></el-tag>
        <el-tag v-if="status == '' || !status" type="info"></el-tag>
      </el-form-item>
    </el-col>
    <el-col :span="12" :offset="0">
      <el-form-item label="">
        {{ dname }}
      </el-form-item>
    </el-col>
    <el-col :span="12" :offset="0">
      <el-form-item label="">
        {{ dtype }}
      </el-form-item>
    </el-col>

    <el-col :span="12" :offset="0">
      <el-form-item label="">
        {{ hardwarever }}
      </el-form-item>
    </el-col>
    <el-col :span="12" :offset="0">
      <el-form-item label="">
        {{ version || "" }}
      </el-form-item>
    </el-col>
    <el-col :span="12" :offset="0">
      <el-form-item label="x">
        {{ x }}
      </el-form-item>
    </el-col>
    <el-col :span="12" :offset="0">
      <el-form-item label="y">
        {{ y }}
      </el-form-item>
    </el-col>
    <el-col :span="12" :offset="0">
      <el-form-item label="UWBR4">
        <span v-if="uwbr4checking == 0"></span>
        <span v-if="uwbr4checking == 1">ID</span>
        <span v-if="uwbr4checking == 2"></span>
        <span v-if="uwbr4checking == 3"></span>
        <span v-if="uwbr4checking == 4">ID</span>
      </el-form-item>
    </el-col>
    <el-col :span="12" :offset="0">
      <el-form-item label="UWBT3">
        <span v-if="uwbt3checking == 0"></span>
        <span v-if="uwbt3checking == 1">ID</span>
        <span v-if="uwbt3checking == 2"></span>
        <span v-if="uwbt3checking == 3"></span>
        <span v-if="uwbt3checking == 4">ID</span>
      </el-form-item>
    </el-col>
    <el-col :span="12" :offset="0">
      <el-form-item label="RFID">
        <span v-if="rfidchecking == 0"></span>
        <span v-if="rfidchecking == 1">ID</span>
        <span v-if="rfidchecking == 2"></span>
        <span v-if="rfidchecking == 3"></span>
        <span v-if="rfidchecking == 4">ID</span>
      </el-form-item>
    </el-col>
    <el-col :span="12" :offset="0">
      <el-form-item label="">
        {{ pattern }}
      </el-form-item>
    </el-col>
    <el-col :span="24" :offset="0">
      <el-form-item label="">
        <span v-if="checkState == 0"></span>
        <span v-if="checkState == 1"></span>
      </el-form-item>
    </el-col>
  </el-row>
</template>

<script lang="ts">
import { defineComponent, reactive, toRefs, watch } from "vue";
export default defineComponent({
  name: "BasestationItem",
  props: {
    deviceData: {
      type: Object
    }
  },
  setup(props) {
    const formData = reactive({
      dname: "",
      dnum: "",
      x: "",
      y: "",
      dtype: "",
      status: "",
      hardwarever: "",
      version: "",
      starttype: 0,
      checkState: 0,
      uwbr4checking: 0,
      uwbt3checking: 0,
      rfidchecking: 0,
      pattern: ""
    });
    watch(
      () => props.deviceData,
      newVal => {
        Object.assign(formData, newVal);
      },
      {
        immediate: true,
        deep: true
      }
    );
    return {
      ...toRefs(formData)
    };
  }
});
</script>
