<template>
  <div class="playback-control">
    <div class="playback-container">
      <span>{{ sTime }}</span>
      <div class="process-container" @click="handleClick" ref="processRef">
        <div
          class="process-bar"
          :style="{ width: `${(currentIndex / len) * 100}%` }"
        ></div>
        <div
          class="process-time"
          :style="{ left: `${(currentIndex / len) * 100}%` }"
        >
          {{ currentTime }}
        </div>
      </div>
      <span>{{ eTime }}</span>
      <span class="video-icon" @click="togglePlay">
        <i :class="[`el-icon-video-${isPlay ? 'pause' : 'play'}`]"></i>
      </span>
      <el-select
        v-model="playRateRef"
        @change="changeRate"
        size="mini"
        style="width: 70px; margin: 0 0.5rem"
      >
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        >
        </el-option>
      </el-select>

      <span class="end" @click="handleClose"></span>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, shallowRef } from "vue";
const CHANGE_RATE = "changeRate";
const TOGGLE_PLAY = "togglePlay";
const CLOSE = "close";
/**  */
const CHANGE_PROCESS = "changeProcess";
export default defineComponent({
  props: {
    sTime: {
      type: String,
      default: "12"
    },
    eTime: {
      type: String,
      default: "13"
    },
    currentTime: {
      type: String,
      default: "14"
    },
    currentIndex: {
      type: Number,
      default: 0
    },
    len: {
      type: Number,
      default: 10
    },
    /**  */

    isPlay: {
      type: Boolean,
      default: true
    }
  },
  emits: [CHANGE_RATE, TOGGLE_PLAY, CLOSE, CHANGE_PROCESS],
  setup(props, { emit }) {
    const playRateRef = shallowRef<string>("1.0");

    const options = ref([
      {
        label: "0.5",
        value: "0.5"
      },
      {
        label: "1.0",
        value: "1.0"
      },
      {
        label: "1.5",
        value: "1.5"
      },
      {
        label: "2.0",
        value: "2.0"
      },
      {
        label: "3.0",
        value: "3.0"
      },
      {
        label: "4.0",
        value: "4.0"
      },
      {
        label: "5.0",
        value: "5.0"
      }
    ]);

    const processRef = ref();
    function changeRate(e: string) {
      emit(CHANGE_RATE, e);
    }

    function togglePlay() {
      emit(TOGGLE_PLAY, !props.isPlay);
    }

    function handleClose() {
      emit(CLOSE);
    }

    function handleClick(e: MouseEvent) {
      let { offsetX } = e;
      // 
      if (offsetX < 0) {
        offsetX = 0;
      }
      if (offsetX === 0) {
        emit(CHANGE_PROCESS, 0);
      }
      // 
      const maxWidth = processRef.value.offsetWidth;
      if (offsetX > maxWidth) {
        offsetX = maxWidth;
      }
      if (offsetX === maxWidth) {
        emit(CHANGE_PROCESS, props.len - 1);
      }

      // 
      if (offsetX > 0 && offsetX < maxWidth) {
        const rate = offsetX / maxWidth;
        const index = Math.floor(props.len * rate);
        emit(CHANGE_PROCESS, index);
      }
    }

    return {
      processRef,
      playRateRef,
      options,
      changeRate,
      togglePlay,
      handleClose,
      handleClick
    };
  }
});
</script>

<style lang="scss" scoped>
.playback-control {
  position: absolute;
  left: 50%;
  bottom: 20%;
  transform: translate(-50%, -50%);
  width: 80rem;
  height: 4rem;
  font-size: 1.2rem;
  background: #1b3f84;
}
.playback-container {
  width: 100%;
  height: 100%;
  position: relative;
  padding: 8px;
  display: flex;
  align-items: center;
}
.process-container {
  position: relative;
  margin: 0 0.5rem;
  flex: 1;
  height: 100%;
  display: flex;
  background: rgba(71, 163, 226, 0.35);
  cursor: pointer;
  // overflow: auto;
}
.process-bar {
  background: rgba(134, 208, 255, 0.35);
}
.process-time {
  position: absolute;
  top: -32px;
  width: fit-content;
  height: 16px;
  padding: 4px 8px;
  display: flex;
  align-items: center;
  background: #4b93ff;
  box-sizing: content-box;
}
span + span {
  margin-left: 0.5rem;
}
.video-icon {
  color: #3ed2ff;
  font-size: 2rem;
  cursor: pointer;
}
.end {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding: 5px;
  height: 24px;
  border: 1px solid #3672c3;
  border-radius: 3px;
  cursor: pointer;
  &:hover {
    box-shadow: 0 0 2px 2px #3ed2ff;
  }
}
</style>
