
import {
  defineComponent,
  reactive,
  ref,
  toRefs,
  onMounted,
  watch,
  computed,
  nextTick,
  PropType
} from "vue";
import commonApi from "@/api/common";
import { ElMessage } from "element-plus";
// 
import dayjs from "dayjs";
import { getIntervalTime } from "@/utils";
import { timeFormateStr } from "../constVariables";
import { IGetAlarmItem } from "../types/ws";

export default defineComponent({
  name: "Handle",
  props: {
    alarmType: {
      required: false,
      default: ""
    },
    /** ws  */
    wsAlarmItem: {
      type: Object as PropType<IGetAlarmItem>
    },
    /** id */
    handleIds: {
      required: false,
      default: []
    },
    /** ws  id */
    cancelId: {
      type: String
    }
  },
  emits: [
    "showAlarmDetail",
    "showHandlePannel",
    "getAlarmTotal",
    "getAlarmUnHandle"
  ],
  setup(props, { emit }) {
    const params = reactive({
      status: ["", "", "", "active"],
      showAlarms: [] as any,
      alarms: [],
      styleArr: [
        { background: "#00A67B" },
        { background: "#FF7352" },
        { background: "#D9354E" }
      ],
      colorStyleArr: [
        { color: "#52f3c8" },
        { color: "#FF7352" },
        { color: "#D9354E" }
      ],
      flag: "",
      size: 10,
      page: 1,
      loading: false,
      oldY: undefined,
      disposeStatus: "", // 
      typeid: "", // id
      alarmLevel: "" as string | number,
      alarmTypeArr: [], // 
      alarmTypeName: "",
      total: 0,
      static: {
        total: 0,
        unHandle: 0
      },
      checkAll: false
    });

    const itemsRef = ref();
    const alarmBarRef = ref();
    const dataRef = toRefs(params);
    // 
    async function getAllAlarm() {
      const param = {
        act: "6Z6wTPGkfMVF9eLTOXA1611040899987",
        curpage: 1,
        // eslint-disable-next-line @typescript-eslint/camelcase
        sys_showpagesize: 10,
        ShowSceneNum: "",
        SourceType: "",
        alarmLevel: "",
        sTime: "",
        eTime: "",
        alarmProgress: ""
      };
      const res = await commonApi.getCommonApi(param);
      const { data } = res as any;
      params.static.total = data.count;
      emit("getAlarmTotal", params.static.total);
    }
    // 
    async function getAllUnHandleAlarm() {
      const param = {
        act: "6Z6wTPGkfMVF9eLTOXA1611040899987",
        curpage: 1,
        // eslint-disable-next-line @typescript-eslint/camelcase
        sys_showpagesize: 10,
        ShowSceneNum: "",
        SourceType: "",
        alarmLevel: "",
        sTime: "",
        eTime: "",
        alarmProgress: ""
      };
      const res = await commonApi.getCommonApi(param);
      const { data } = res as any;
      params.static.unHandle = data.count;
      emit("getAlarmUnHandle", params.static.unHandle);
    }
    // 
    async function getAlarmList() {
      const param = {
        act: "6Z6wTPGkfMVF9eLTOXA1611040899987",
        curpage: params.page,
        // eslint-disable-next-line @typescript-eslint/camelcase
        sys_showpagesize: params.size,
        ShowSceneNum: "",
        SourceType: props.alarmType,
        alarmLevel: params.alarmLevel,
        sTime: "",
        eTime: "",
        alarmProgress: params.disposeStatus
      };
      const res = await commonApi.getCommonApi(param);
      const { result, data } = res as any;

      if (result.success) {
        data.content.forEach((element: any) => {
          // 
          const createtime = dayjs(element.CreateTime).format(timeFormateStr);
          element.CreateTime = createtime;
          // 
          element.diffTime = getIntervalTime(createtime);
          // 
          const dealTime = dayjs(element.dealTime).format(timeFormateStr);
          element.dealTime = dealTime;
          // 
          element.isSelected = false;
        });
        params.showAlarms = [...params.showAlarms, ...data.content];
        params.total = data.count;
      }
      params.loading = false;
    }

    // 
    function alarmLevel(n: number) {
      const arr = ["", "", ""];
      return arr[n];
    }

    // 
    function handleStatus(status: string) {
      params.checkAll = false;
      params.loading = true;
      if (params.disposeStatus == status) {
        params.loading = false;
        return;
      }
      params.page = 1;
      params.flag = status;
      params.disposeStatus = status;
      params.showAlarms = [];
      getAlarmList();
    }
    // 
    function lazyLoad(e: any) {
      const itemLength = itemsRef.value && itemsRef.value.offsetHeight;
      const ulLength = alarmBarRef.value.offsetHeight;
      if (
        e.target.scrollTop >
        itemLength * params.page * params.size - ulLength
      ) {
        // if (params.showAlarms.length < params.page * params.size) {
        //   return;
        // }

        params.page += 1;
        params.loading = true;
        getAlarmList();
      }
    }
    // 
    function handleLevel(count: number) {
      params.checkAll = false;
      params.loading = true;
      if (
        params.alarmLevel === count ||
        (params.alarmLevel === "" && count === 3)
      ) {
        params.loading = false;
        return;
      }
      params.status = ["", "", "", ""];
      params.status[count] = "active";
      if (count === 3) {
        params.alarmLevel = "";
      } else {
        params.alarmLevel = count;
      }
      params.page = 1;
      params.showAlarms = [];
      getAlarmList();
    }

    // 
    function alarmDetail(row: any) {
      emit("showAlarmDetail", row);
    }

    //
    function handleAll() {
      const checkedAlarm = params.showAlarms.filter(
        (item: any) => item.isSelected
      );
      if (checkedAlarm.length === 0) {
        ElMessage.error("");
        return;
      }
      emit("showHandlePannel", checkedAlarm);
    }

    // 
    function showHandlePannel(row: any) {
      emit("showHandlePannel", row);
    }

    // 
    function checkAlarm(row: any) {
      params.showAlarms.forEach((item: any) => {
        if (item.Id === row.Id) {
          item.isSelected = !row.isSelected;
          return;
        }
      });
    }

    // /
    function handleCheckAllChange() {
      params.showAlarms.forEach((item: any) => {
        item.isSelected = params.checkAll;
      });
    }

    watch(
      () => props.wsAlarmItem,
      newVal => {
        console.log("newVal", newVal);
        if (newVal) {
          const wsAlarm = {
            isSelected: false,
            status: false,
            alarmLevel: newVal.AlarmLevel,
            CreateTime: dayjs(new Date()).format(timeFormateStr),
            diffTime: getIntervalTime(dayjs(new Date()).format(timeFormateStr)),
            SourceType: newVal.AlarmSourceType,
            alarmContent: newVal.AlarmContent,
            SourceName: newVal.AlarmSourceName,
            alarmProgress: ""
          };

          params.showAlarms = [wsAlarm, ...params.showAlarms];
          params.static.unHandle++;
          emit("getAlarmUnHandle", params.static.unHandle);
          params.static.total++;
          emit("getAlarmTotal", params.static.total);
        }
      }
    );
    // 
    watch(
      () => props.handleIds,
      newArr => {
        params.showAlarms.slice().forEach((item: any, index: number) => {
          newArr.forEach((id: string | number) => {
            if (item.IDS == id) {
              params.static.unHandle--;
              emit("getAlarmUnHandle", params.static.unHandle);
              // 
              if (params.showAlarms.length < params.size) {
                params.page = 1;
                params.showAlarms = [];
                getAlarmList();
              }
              params.showAlarms.splice(index, 1);
              params.total--;
            }
          });
        });
      }
    );
    const checkedNum: any = computed(() => {
      const num = params.showAlarms.filter((item: any) => item.isSelected);
      return num.length;
    });

    onMounted(() => {
      getAlarmList();
      // 
      getAllAlarm();
      // 
      getAllUnHandleAlarm();
    });

    watch(
      () => props.alarmType,
      () => {
        nextTick(() => {
          params.page = 1;
          params.showAlarms = [];
          getAlarmList();
        });
      }
    );
    return {
      ...dataRef,
      itemsRef,
      alarmBarRef,
      alarmLevel,
      handleStatus,
      lazyLoad,
      handleLevel,
      alarmDetail,
      handleAll,
      showHandlePannel,
      checkAlarm,
      handleCheckAllChange,
      checkedNum
    };
  }
});
