
import { defineComponent, reactive, toRefs, watch } from "vue";
export default defineComponent({
  name: "BasestationItem",
  props: {
    deviceData: {
      type: Object
    }
  },
  setup(props) {
    const formData = reactive({
      dname: "",
      dnum: "",
      x: "",
      y: "",
      dtype: "",
      status: "",
      hardwarever: "",
      version: "",
      starttype: 0,
      checkState: 0,
      uwbr4checking: 0,
      uwbt3checking: 0,
      rfidchecking: 0,
      pattern: ""
    });
    watch(
      () => props.deviceData,
      newVal => {
        Object.assign(formData, newVal);
      },
      {
        immediate: true,
        deep: true
      }
    );
    return {
      ...toRefs(formData)
    };
  }
});
