import type { ObjectDirective  } from "vue"
import { TITLE_HEIGHT as titleHeight } from "../../constVariables";
//  copydragdialog
const draggableDialog:ObjectDirective<any> = {
  mounted(el,binding) {
    const [header,customForm] = binding.value.split(",")
    
    const dialogHeaderEl: HTMLDivElement = el.querySelector("."+header);
    const dragDom = el.querySelector("."+customForm);
    dialogHeaderEl.style.cssText += ";cursor:move;";
    dragDom.style.cssText += ";top:5rem;";

    //  ie dom.currentStyle  window.getComputedStyle(dom, null);
    // ? ts 
    // const getStyle = (function() {
    //   if (window.document.currentStyle) {
    //     return (dom, attr) => dom.currentStyle[attr]
    //   } else {
    //     return (dom, attr) => getComputedStyle(dom, false)[attr]
    //   }
    // })()

    const getStyle = (function() {
      return (dom: HTMLElement, attr: string) =>
        getComputedStyle(dom, null)[attr];
    })();

    dialogHeaderEl.onmousedown = e => {
      // 
      const disX = e.clientX - dialogHeaderEl.offsetLeft;
      const disY = e.clientY - dialogHeaderEl.offsetTop;

      const dragDomWidth = dragDom.offsetWidth;
      const dragDomHeight = dragDom.offsetHeight;

      const screenWidth = document.body.clientWidth;
      const screenHeight = document.body.clientHeight - titleHeight+20;

      const minDragDomLeft = dragDom.offsetLeft;
      const maxDragDomLeft = screenWidth - dragDom.offsetLeft - dragDomWidth;

      const minDragDomTop = dragDom.offsetTop;
      const maxDragDomTop = screenHeight - dragDom.offsetTop - dragDomHeight;

      // px 
      let styL: string | number = getStyle(dragDom, "left");
      let styT: string | number = getStyle(dragDom, "top");

      if ((styL as string).includes("%")) {
        styL =
          +document.body.clientWidth *
          (+(styL as string).replace(/%/g, "") / 100);
        styT =
          +document.body.clientHeight *
          (+(styT as string).replace(/%/g, "") / 100);
      } else {
        styL = +(styL as string).replace(/\px/g, "");
        styT = +(styT as string).replace(/\px/g, "");
      }

      document.onmousemove = function(e) {
        // 
        let left = e.clientX - disX;
        let top = e.clientY - disY;

        // 
        if (-left > minDragDomLeft) {
          left = -minDragDomLeft;
        } else if (left > maxDragDomLeft) {
          left = maxDragDomLeft;
        }

        if (-top > minDragDomTop) {
          top = -minDragDomTop;
        } else if (top > maxDragDomTop) {
          top = maxDragDomTop;
        }

        // 
        dragDom.style.cssText += `;left:${left + (styL as number)}px;top:${top +
          (styT as number)}px;`;

        // emit onDrag event
        // vnode.child.$emit('dragDialog')
      };

      document.onmouseup = function() {
        document.onmousemove = null;
        document.onmouseup = null;
      };
    };
  },
};

export default draggableDialog;
