<template>
  <div v-if="visible" class="canvas-container">
    <canvas
      id="water-alarm-canvas"
      ref="waterAlarmCanvas"
      @click="clickHandler"
    />
    <i class="el-icon-close hidden-icon" @click="hiddenBox" />
  </div>
</template>

<script>
export default {
  name: "WaterAlarm",
  props: {
    visible: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      ctx: null,
      radarR: 60, // 
      radarR1: 150, // 1
      shipImg: null, // 
      shipDeg: 0, // 
      alpha: 0.1, // 
      personPosArr: [
        [100, 100],
        [400, 100]
      ] // canvas
    };
  },
  mounted() {
    this.visible && this.init();
  },
  methods: {
    init() {
      const { clientWidth, clientHeight } = this.$el;
      this.$refs.waterAlarmCanvas.width = clientWidth;
      this.$refs.waterAlarmCanvas.height = clientHeight;
      this.ctx = this.$refs.waterAlarmCanvas.getContext("2d");
      this.shipImg = new Image();
      this.shipImg.src = "/images/car.png";
      this.shipImg.onload = () => {
        const data = {
          ctx: this.ctx,
          shipImg: this.shipImg,
          shipWidth: 60,
          shipHeight: 60,
          shipDeg: this.shipDeg,
          radarR: this.radarR,
          radarR1: this.radarR1,
          alpha: this.alpha,
          personPosArr: this.personPosArr
        };
        this.drawScene(data);
        this.animate();
      };
    },
    // animate(shipImg) {
    animate() {
      if (!this.visible) {
        return;
      }
      requestAnimationFrame(this.animate);
      // const timer = requestAnimationFrame(this.animate);
      // this.$once("hook:beforeDestroy", () => {
      //   cancelAnimationFrame(timer);
      // });
      const data = {
        ctx: this.ctx,
        shipImg: this.shipImg,
        shipWidth: 60,
        shipHeight: 60,
        shipDeg: this.shipDeg,
        radarR: this.radarR,
        radarR1: this.radarR1,
        alpha: this.alpha,
        personPosArr: this.personPosArr
      };
      this.drawScene(data);
      // 
      this.shipDeg = Math.random() * Math.PI * 2;
      this.radarR = this.radarR > 300 ? 0 : ++this.radarR;
      this.radarR1 = this.radarR1 > 300 ? 0 : ++this.radarR1;
      this.alpha += 0.01;
      this.alpha = this.alpha > 1 ? 0.1 : this.alpha;
    },
    drawScene(data) {
      const {
        ctx,
        shipImg,
        shipWidth,
        shipHeight,
        shipDeg,
        radarR,
        radarR1,
        alpha,
        personPosArr
      } = data;
      const { width, height } = this.$refs.waterAlarmCanvas;
      ctx.clearRect(0, 0, width, height);
      // 
      this.drawBasicIcon(ctx, width, height);
      // 
      this.drawRadar({ ctx: ctx, pos: [width / 2, height / 2], r: radarR });
      this.drawRadar({ ctx: ctx, pos: [width / 2, height / 2], r: radarR1 });
      // 
      this.drawPerson(ctx, personPosArr, alpha);
      // 
      ctx.save();
      ctx.translate(width / 2, height / 2);
      ctx.rotate(shipDeg);
      ctx.drawImage(
        shipImg,
        -shipWidth / 2,
        -shipHeight / 2,
        shipWidth,
        shipHeight
      );
      ctx.restore();
    },
    /**
     * @description: 
     * @param {*}
     * @return {*}
     */
    drawBasicIcon(ctx, canvasWidth, canvasHeight) {
      // 
      ctx.beginPath();
      ctx.fillStyle = "#2CA3FF";
      ctx.arc(
        canvasWidth / 2,
        canvasHeight / 2,
        canvasWidth / 2,
        0,
        Math.PI * 2
      );
      ctx.fill();
      ctx.closePath();
      // 
      ctx.beginPath();
      ctx.lineCap = "round";
      ctx.strokeStyle = "#fff";
      ctx.lineWidth = 4;
      ctx.moveTo(0, canvasHeight / 2);
      ctx.lineTo(canvasWidth, canvasHeight / 2);
      ctx.moveTo(canvasWidth / 2, 0);
      ctx.lineTo(canvasWidth / 2, canvasHeight);
      ctx.stroke();
      ctx.closePath();
      // 
      ctx.beginPath();
      ctx.strokeStyle = "#ffffff";
      for (let i = 0; i < 6; i++) {
        ctx.arc(canvasWidth / 2, canvasHeight / 2, 50 * i, 0, Math.PI * 2);
      }
      ctx.lineWidth = 2;
      ctx.stroke();
      ctx.closePath();
    },
    /**
     * @description: 
     * @param {*}
     * @return {*}
     */
    drawRadar(data) {
      const { ctx, pos, r } = data;
      ctx.beginPath();
      const grd = ctx.createRadialGradient(
        pos[0],
        pos[1],
        0,
        pos[0],
        pos[1],
        r
      );
      grd.addColorStop(0, "rgba(200,0,0,1.0)");
      grd.addColorStop(1, "rgba(200,0,0,0.1)");
      ctx.fillStyle = grd;
      ctx.arc(pos[0], pos[1], r, 0, Math.PI * 2);
      ctx.fill();
      ctx.closePath();
    },
    /**
     * @description: 
     * @param {*}
     * @return {*}
     */
    drawPerson(ctx, posArr, alpha) {
      ctx.beginPath();
      ctx.fillStyle = `rgba(255,0,0,${alpha})`;
      posArr.forEach(value => {
        ctx.arc(value[0], value[1], 4, 0, Math.PI * 2);
      });
      ctx.fill();
      ctx.closePath();
    },
    hiddenBox() {
      this.$emit("update:visible", false);
    },
    /**
     * @description: canvas
     * @param {*}
     * @return {*}
     */
    clickHandler(e) {
      console.log(e);
      const arr = this.personPosArr;
      const x = e.offsetX;
      const y = e.offsetY;
      for (let i = 0, length = arr.length; i < length; i++) {
        if (
          x > arr[i][0] - 4 &&
          x < arr[i][0] + 4 &&
          y > arr[i][1] - 4 &&
          y < arr[i][1] + 4
        ) {
          this.$message("");
          return;
        }
      }
    }
  }
};
</script>
<style lang="scss" scoped>
.canvas-container {
  width: 100%;
  height: 100%;
  background: #fff;
  position: relative;
  & > #water-alarm-canvas {
    width: 100%;
    height: 100%;
  }
  .hidden-icon {
    position: absolute;
    top: 0;
    right: 0;
    font-size: 20px;
    color: #000;
    cursor: pointer;
    &:hover {
      background-color: rgba(0, 0, 0, 0.4);
      color: #fff;
    }
  }
}
</style>
