<template>
  <div
    v-show="contextmenuList.length"
    ref="contextMenuRef"
    class="context-menu"
    :style="styleAttrs ? styleAttrs : {}"
    :class="{ hidden: hiddenRef }"
  >
    <div
      v-for="menuItem in contextmenuList"
      :key="menuItem.label"
      class="menu-item"
      @click="handleClickContentmenu(menuItem.clickCode)"
    >
      {{ menuItem.label }}
    </div>
  </div>
</template>

<script lang="ts">
import {
  defineComponent,
  nextTick,
  onMounted,
  PropType,
  ref,
  shallowReactive,
  shallowRef,
  watch
} from "vue";
import { getCorrectPx } from "../utils";
import { TYPES } from "../constVariables";
import { IPos, IStyleLeftTop, TShapeType } from "../types";

const SHOW_DETAILS = "showDetails";
const TRACK_PLAYBACK = "trackPlayback";
const CLICKED = "clicked";
const SEE_NOWVIDEO = "seeNowVideo";
const SEE_HISTORYVIDEO = "seeHistoryVideo";
const FOLLOW = "follow";

const emits = [
  SHOW_DETAILS,
  TRACK_PLAYBACK,
  CLICKED,
  SEE_NOWVIDEO,
  SEE_HISTORYVIDEO,
  FOLLOW
];

export default defineComponent({
  props: {
    type: {
      type: String as PropType<TShapeType>,
      default: TYPES.basestation
    },
    // 
    contextmenuList: {
      type: Array,
      default: () => []
    },
    // 
    contxtmenuPosition: {
      type: Object as PropType<IStyleLeftTop>,
      default: () => ({})
    }
  },
  emits,
  setup(props, { emit }) {
    const styleAttrs = shallowReactive<IPos>({ x: 0, y: 0 });
    const hiddenRef = shallowRef<boolean>(true);

    const contextMenuRef = ref();

    function handleClickContentmenu(clickCode: string) {
      if (emits.includes(clickCode)) {
        emit(clickCode, props.type);
      }
      emit(CLICKED);
    }

    function getPx() {
      nextTick(() => {
        const res = getCorrectPx(
          contextMenuRef.value as HTMLElement,
          props.contxtmenuPosition
        );

        const positions = {};
        for (const key in res) {
          positions[key] = res[key] + "px";
        }

        Object.assign(styleAttrs, positions);
        hiddenRef.value = false;
      });
    }

    watch(
      () => props.contxtmenuPosition,
      () => {
        getPx();
      }
    );

    onMounted(() => {
      getPx();
    });

    return {
      contextMenuRef,
      styleAttrs,
      hiddenRef,
      handleClickContentmenu
    };
  }
});
</script>

<style lang="scss" scoped>
.context-menu {
  padding: 10px 0;
  position: absolute;
  z-index: 200;
  background: #fff;
  border-radius: 4px;
  border: 1px solid #ebeef5;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  left: 0px;
  top: 30px;

  &.hidden {
    visibility: hidden;
  }

  .menu-item {
    padding: 0 20px;
    font-size: 14px;
    color: #606266;
    line-height: 36px;
    outline: none;
    cursor: pointer;
    white-space: nowrap;

    &:hover {
      background: #ecf5ff;
      color: #66b1ff;
    }
  }
}
</style>
