
import { computed, defineComponent } from "vue";
import dragDialog from "../directive/drag-dialog";

const CLOSE = "close";
export default defineComponent({
  name: "DragableContainer",
  directives: { "drag-dialog": dragDialog },
  props: {
    title: {
      type: String,
      default: ""
    },
    modelValue: {
      type: Boolean,
      default: false
    },
    width: {
      type: Number
    }
  },
  emits: [CLOSE],
  setup(props, { emit }) {
    function handleClickHeaderClose() {
      emit(CLOSE);
    }

    const styles = computed(() => {
      if (props.width) {
        if (props.title === "" || props.title === "") {
          return { width: props.width + "px", left: "23.5%" };
        } else {
          return { width: props.width + "px", "z-index": 9001 };
        }
      } else {
        if (props.title === "" || props.title === "") {
          return { left: "23.5%" };
        } else {
          return { "z-index": 9001 };
        }
      }
    });

    return {
      handleClickHeaderClose,
      styles
    };
  }
});
