<template>
  <el-row>
    <el-col :span="12" :offset="0"
      ><el-form-item label="">
        {{ dnum }}
      </el-form-item></el-col
    >
    <el-col :span="12" :offset="0">
      <el-form-item label="">
        <el-tag v-if="status == ''"></el-tag>
        <el-tag v-if="status == '' || !status" type="info"></el-tag>
      </el-form-item>
    </el-col>
    <el-col :span="12" :offset="0">
      <el-form-item label="">
        {{ dname }}
      </el-form-item>
    </el-col>
    <el-col :span="12" :offset="0">
      <el-form-item label="">
        {{ dtype }}
      </el-form-item>
    </el-col>
    <el-col :span="12" :offset="0">
      <el-form-item label="">
        {{ getMapByNum(snum).sname || "" }}
      </el-form-item>
    </el-col>
    <el-col :span="12" :offset="0">
      <el-form-item label="">
        {{ hearttime }}
      </el-form-item>
    </el-col>
    <el-col :span="12" :offset="0">
      <el-form-item label="">
        {{ voltage }}
      </el-form-item>
    </el-col>
    <el-col :span="12" :offset="0">
      <el-form-item label="">
        {{ bracelet_voltage }}
      </el-form-item>
    </el-col>

    <el-col :span="24" :offset="0">
      <el-form-item label="">
        {{ currentalarm }}
      </el-form-item>
    </el-col>
  </el-row>
</template>

<script lang="ts">
import { defineComponent, reactive, toRefs, watch } from "vue";
import { getMapByNum } from "@/utils/auth";
export default defineComponent({
  name: "LabelItem",
  props: {
    deviceData: {
      type: Object
    }
  },
  setup(props) {
    const form = reactive({
      dname: "",
      dnum: "",
      dtype: "",
      status: "",
      hearttime: "",
      currentalarm: "", // 
      voltage: 0, // 
      // eslint-disable-next-line @typescript-eslint/camelcase
      bracelet_voltage: 0 // 
    });

    watch(
      () => props.deviceData,
      newVal => {
        Object.assign(form, newVal);
      },
      {
        immediate: true,
        deep: true
      }
    );

    return {
      ...toRefs(form),
      getMapByNum
    };
  }
});
</script>

<style lang="scss" scoped></style>
