<template>
  <ul class="fence-box">
    <li
      v-for="(item, index) in list"
      :key="index"
      :class="{ active: index === 0 }"
      @click="handleClick(item)"
    >
      {{ item.label }}
    </li>
  </ul>
</template>

<script lang="ts">
import { defineComponent, defineEmit } from "vue";
interface ILabel {
  label: string;
  code?: string;
}

type TEmitEvents = "revoke" | "redraw" | "cancel" | "finish";

// const emit = defineEmit(["revoke", "redraw", "cancel", "finish"]);

export default defineComponent({
  name: "fenceBox",
  emits: ["revoke", "redraw", "cancel", "finish"],
  setup(props, { emit }) {
    const list: ILabel[] = [
      {
        label: ""
      },
      {
        label: "",
        code: "revoke"
      },
      {
        label: "",
        code: "redraw"
      },
      {
        label: "",
        code: "cancel"
      },
      {
        label: "",
        code: "finish"
      }
    ];
    const handleClick = (item: ILabel) => {
      if (item.code) {
        emit(item.code as TEmitEvents);
      }
    };
    return {
      list,
      handleClick
    };
  }
});
</script>
