import axios from "axios";
import { ElMessage } from "element-plus";
import { baseUrl, alarmUrl } from "./urls";
import qs from "qs";
import { getToken } from "./auth";

const service = axios.create({
  baseURL: baseUrl,
  headers: {
    "Content-Type": "application/x-www-form-urlencoded"
  },
  timeout: 5000
});

const jsonList = ["/alarm/deal", "/alarm/dealall"];

service.interceptors.request.use(
  async config => {
    if (config.url !== "/tbs/dologin") {
      config.headers.visittoken = await getToken();
    }

    if (jsonList.includes(config.url as string)) {
      config.baseURL = alarmUrl;
      config.headers["Content-Type"] = "application/json";
    } else {
      config.data = qs.stringify(config.data);
    }

    return config;
  },
  error => {
    console.log(error);
    return Promise.reject(error);
  }
);

service.interceptors.response.use(
  response => {
    const res = response.data;
    if (res.result && res.result.success === false) {
      ElMessage({
        message: res.result.msg || "Error",
        type: "error",
        duration: 5 * 1000
      });

      return Promise.reject(new Error(res.result.msg || "Error"));
    } else {
      return res;
    }
  },
  error => {
    console.log("err" + error);
    ElMessage({
      message: error.message,
      type: "error",
      duration: 5 * 1000
    });
    return Promise.reject(error);
  }
);

export default service;
