<template>
  <div
    v-show="personList.length"
    ref="listRef"
    class="person-list-container"
    :style="styleAttrs ? styleAttrs : {}"
    :class="{ hidden: hiddenRef }"
  >
    <el-scrollbar style="max-height: 400px">
      <div
        v-for="item in personList"
        :key="item.TagId"
        class="person-item"
        @click="handleClick(item)"
      >
        {{ item.BindObjName }}
      </div>
    </el-scrollbar>
  </div>
</template>

<script lang="ts">
import {
  defineComponent,
  nextTick,
  onMounted,
  PropType,
  ref,
  shallowReactive,
  shallowRef,
  watch
} from "vue";
import { getCorrectPx } from "../utils";
import { IPos, IStyleLeftTop } from "../types";
import { ITagListItem } from "../types/cacheObj";

const CLICKED = "clicked";

export default defineComponent({
  props: {
    // 
    personList: {
      type: Array as PropType<ITagListItem[]>,
      default: () => []
    },
    // 
    mousePosition: {
      type: Object as PropType<IStyleLeftTop>,
      default: () => ({})
    }
  },
  emits: [CLICKED],
  setup(props, { emit }) {
    const styleAttrs = shallowReactive<IPos>({ x: 0, y: 0 });
    const hiddenRef = shallowRef<boolean>(true);

    const listRef = ref();

    function getPx() {
      nextTick(() => {
        const res = getCorrectPx(
          listRef.value as HTMLElement,
          props.mousePosition
        );

        const positions = {};
        for (const key in res) {
          positions[key] = res[key] + "px";
        }

        Object.assign(styleAttrs, positions);
        hiddenRef.value = false;
      });
    }

    watch(
      () => props.mousePosition,
      () => {
        getPx();
      }
    );

    onMounted(() => {
      getPx();
    });

    function handleClick(item: any) {
      emit(CLICKED, item);
    }

    return { styleAttrs, hiddenRef, listRef, handleClick };
  }
});
</script>

<style lang="scss" scoped>
.person-list-container {
  position: absolute;
  padding: 10px 0;
  background: #fff;
  color: #333;
  top: 0;
  z-index: 99999;
  border-radius: 4px;
  box-shadow: 0 2px 12px 0 rgb(0 0 0 / 10%);
  border: 1px solid #ebeef5;
  &.hidden {
    visibility: hidden;
  }
}
.person-item {
  padding: 0 20px;
  font-size: 14px;
  color: #606266;
  line-height: 36px;
  outline: none;
  cursor: pointer;
  white-space: nowrap;
  &:hover {
    background: #ecf5ff;
    color: #66b1ff;
  }
}
</style>
