
import { defineComponent, ref, shallowRef } from "vue";
const CHANGE_RATE = "changeRate";
const TOGGLE_PLAY = "togglePlay";
const CLOSE = "close";
/**  */
const CHANGE_PROCESS = "changeProcess";
export default defineComponent({
  props: {
    sTime: {
      type: String,
      default: "12"
    },
    eTime: {
      type: String,
      default: "13"
    },
    currentTime: {
      type: String,
      default: "14"
    },
    currentIndex: {
      type: Number,
      default: 0
    },
    len: {
      type: Number,
      default: 10
    },
    /**  */

    isPlay: {
      type: Boolean,
      default: true
    }
  },
  emits: [CHANGE_RATE, TOGGLE_PLAY, CLOSE, CHANGE_PROCESS],
  setup(props, { emit }) {
    const playRateRef = shallowRef<string>("1.0");

    const options = ref([
      {
        label: "0.5",
        value: "0.5"
      },
      {
        label: "1.0",
        value: "1.0"
      },
      {
        label: "1.5",
        value: "1.5"
      },
      {
        label: "2.0",
        value: "2.0"
      },
      {
        label: "3.0",
        value: "3.0"
      },
      {
        label: "4.0",
        value: "4.0"
      },
      {
        label: "5.0",
        value: "5.0"
      }
    ]);

    const processRef = ref();
    function changeRate(e: string) {
      emit(CHANGE_RATE, e);
    }

    function togglePlay() {
      emit(TOGGLE_PLAY, !props.isPlay);
    }

    function handleClose() {
      emit(CLOSE);
    }

    function handleClick(e: MouseEvent) {
      let { offsetX } = e;
      // 
      if (offsetX < 0) {
        offsetX = 0;
      }
      if (offsetX === 0) {
        emit(CHANGE_PROCESS, 0);
      }
      // 
      const maxWidth = processRef.value.offsetWidth;
      if (offsetX > maxWidth) {
        offsetX = maxWidth;
      }
      if (offsetX === maxWidth) {
        emit(CHANGE_PROCESS, props.len - 1);
      }

      // 
      if (offsetX > 0 && offsetX < maxWidth) {
        const rate = offsetX / maxWidth;
        const index = Math.floor(props.len * rate);
        emit(CHANGE_PROCESS, index);
      }
    }

    return {
      processRef,
      playRateRef,
      options,
      changeRate,
      togglePlay,
      handleClose,
      handleClick
    };
  }
});
