<template>
  <div class="map-content">
    <div
      v-for="mapItem in mapList"
      :key="mapItem.snum"
      class="map-item"
      :class="{ active: mapItem.snum === mapId }"
      @click="changeMap(mapItem.snum)"
    >
      <img :src="imgUrl + mapItem.surl" :alt="mapItem.sname" />
      <span class="o-h">
        {{ mapItem.sname }}
      </span>
      <div class="num">{{ mapItem.pnum || 0 }}</div>
    </div>
  </div>
</template>

<script lang="ts">
import { imgUrl } from "@/utils/urls";
import { defineComponent, PropType } from "vue";
import { IMapArrItem } from "../types";

const CHANGE_MAP = "changeMap";

export default defineComponent({
  name: "MapChoosen",
  props: {
    mapId: {
      type: String,
      required: true
    },
    mapList: {
      type: Object as PropType<IMapArrItem[]>,
      default: () => []
    }
  },
  emits: [CHANGE_MAP],
  setup(props, { emit }) {
    function changeMap(mapId: string) {
      if (mapId === props.mapId) return;
      emit(CHANGE_MAP, mapId);
    }
    return {
      imgUrl,
      changeMap
    };
  }
});
</script>

<style lang="scss" scoped>
.map-content {
  display: grid;
  grid-template-columns: repeat(4, calc((100% - 45px) / 4));
  grid-row-gap: 15px;
  grid-column-gap: 15px;
  padding: 15px;
  max-height: 600px;
  overflow-y: scroll;
}
.map-item {
  position: relative;
  display: flex;
  flex-direction: column;
  padding-bottom: 16px;
  height: 133px;
  background: rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  object-fit: fill;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  transition: color 0.3s ease-in-out;
  cursor: pointer;
  img {
    margin: 10px;
    width: calc(100% - 20px);
  }
  span {
    display: inline-block;
    font-size: 14px;
  }
  .num {
    position: absolute;
    top: 5px;
    right: 5px;
    background: rgba(0, 0, 0, 0.3);
    width: 40px;
    height: 40px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    transform: rotate(45deg);
  }
  &.active,
  &:hover {
    background: url("../../../../assets/dashboard/important_monitor.png") repeat
      center center/100% 100%;
    color: #3ed2ff;
    .num {
      background: rgba(255, 255, 255, 0.3);
    }
  }
}
</style>
