
import {
  defineComponent,
  nextTick,
  onMounted,
  PropType,
  ref,
  shallowReactive,
  shallowRef,
  watch
} from "vue";
import { getCorrectPx } from "../utils";
import { IPos, IStyleLeftTop } from "../types";
import { ITagListItem } from "../types/cacheObj";

const CLICKED = "clicked";

export default defineComponent({
  props: {
    // 
    personList: {
      type: Array as PropType<ITagListItem[]>,
      default: () => []
    },
    // 
    mousePosition: {
      type: Object as PropType<IStyleLeftTop>,
      default: () => ({})
    }
  },
  emits: [CLICKED],
  setup(props, { emit }) {
    const styleAttrs = shallowReactive<IPos>({ x: 0, y: 0 });
    const hiddenRef = shallowRef<boolean>(true);

    const listRef = ref();

    function getPx() {
      nextTick(() => {
        const res = getCorrectPx(
          listRef.value as HTMLElement,
          props.mousePosition
        );

        const positions = {};
        for (const key in res) {
          positions[key] = res[key] + "px";
        }

        Object.assign(styleAttrs, positions);
        hiddenRef.value = false;
      });
    }

    watch(
      () => props.mousePosition,
      () => {
        getPx();
      }
    );

    onMounted(() => {
      getPx();
    });

    function handleClick(item: any) {
      emit(CLICKED, item);
    }

    return { styleAttrs, hiddenRef, listRef, handleClick };
  }
});
