import { useRouter } from "vue-router";
import { getLoadSNum, logout } from "@/utils/auth";
import { getWsUrl } from "../utils";
import { computed, reactive, ref, shallowReactive, shallowRef } from "vue";
import {
  IActiveTarget,
  IAreaItem,
  ICanvasBasicState,
  ICanvasScale,
  IDetailsForm,
  IDrawFenceInfo,
  IListData,
  IMapArrItem,
  IMapData,
  IPersonArrItem,
  IShapeGroup,
  IStyleLeftTop,
  TLayerItem,
  TVectorgraph
} from "../types";
import { ElMessage } from "element-plus";
import { IAlarmInfo, IAlarmListInfo, TWarningListWidth } from "../types/alarm";
import {
  ALL_MENU,
  Hms,
  NAV_INFO,
  TYPES,
  warningListWidth,
  YMD
} from "../constVariables";
import {
  IFenceInfo,
  IFenceListInfo,
  IRuleItemDetail,
  IRuleItemDetailRule,
  IRuleListInfo
} from "../types/fence";
import { checkPositiveInteger, commonRuleItem, customFuncRule } from "@/utils";
import dayjs from "dayjs";
import Konva from "konva";
import {
  ICacheBubble,
  ICacheImage,
  ICachePerson,
  ITagListItem
} from "../types/cacheObj";
import { ISelectOption } from "@/types";
import { Circle } from "konva/types/shapes/Circle";

export default function initData() {
  const router = useRouter();
  const logOut = () => logout(router);
  const wsUrl = getWsUrl();
  const mapObj = reactive<IMapData>({}); //  obj
  const mapListRef = ref<IMapArrItem[]>([]); //  arr
  const mapIdRef = shallowRef<string>(getLoadSNum() || "");
  if (!mapIdRef.value) {
    ElMessage.info("");
    logOut();
  }
  const scaleBoundary: ICanvasScale = {
    minScale: 0,
    maxScale: 2
  };
  const state = reactive<ICanvasBasicState>({
    scale: 0,
    sx: 0,
    sy: 0
  });
  const status = shallowReactive({
    isPlayBack: false, // 
    isFollowing: false, // 
    draggable: false, // 
    isDrawFence: false // 
  });
  const showMenu = computed(() => {
    return !status.isDrawFence && !status.isFollowing && !status.isPlayBack;
  });
  const mapNameRef = computed(() => mapObj[mapIdRef.value]?.sname || "");
  const warningWidth = shallowRef<TWarningListWidth>(warningListWidth);
  const contextmenuTypeRef = shallowRef<string>(TYPES.basestation); //  TYPES
  const contxtmenuPosition = shallowReactive<IStyleLeftTop>({
    left: 0,
    top: 0
  }); //  
  const contextmenuList = computed(() => ALL_MENU[contextmenuTypeRef.value]);
  const detailsForm = reactive<IDetailsForm>({
    show: false,
    formTitle: "",
    formData: null, // form
    x: 0, // x
    y: 0
  });
  const visibleObj = shallowReactive({
    nav: true, // 
    waterAlarmPanel: false, // 
    contextmenu: false, // 
    detailsForm: false, // form
    monitorListFlag: false, //  lfq
    historyMonitorListFlag: false, //  lfq
    area: true, // 
    layerPerson: false, // layer 
    map: false, // 
    controlPannel: true // 
  });
  const monitorListInfo = reactive({
    //  lfq
    monitorInner: "monitor-inner-pannel", // class
    monitorHistoryInner: "monitor-inner-pannel", // class
    newMonitorList: [] as any[], // lfq
    newHistoryMonitorList: [] as any[], //  lfq
    cameraCount: 4, //  lfq
    liveCameraCount: 1, //  lfq
    historyCameraCount: 1, //  lfq
    livePlayCount: 1, //  lfq
    historyPlayCount: 1, //  lfq
    monitorListFlag: false, // lfq
    historyMonitorListFlag: false // lfq
  });

  const drawerInfo = reactive({
    showList: false, // 
    drawerTitle: NAV_INFO[TYPES.basestation].title as string,
    activeName: TYPES.basestation // 
  });
  const fenceInfo = reactive<IFenceInfo>({
    visible: false,
    dialogType: 0, // 0   1 
    form: {
      name: "",
      chosenCamera: []
    },
    allMapCamera: [], // 
    rules: {
      name: commonRuleItem("")
    }
  });
  const fenceListInfo = reactive<IFenceListInfo>({
    tableVisible: false,
    params: {
      curpage: 1
    },
    loading: false,
    allData: [],
    tableData: [],
    total: 0
  });
  const drawFenceInfo = reactive<IDrawFenceInfo>({
    message: null, // 
    points: [], // 
    clonePoints: []
  });
  //  table
  const ruleListInfo = reactive<IRuleListInfo>({
    visiable: false,
    loading: false,
    allData: [],
    tableData: [],
    total: 0,
    curpage: 1
  });
  //  
  const ruleFormRef = ref();
  const ruleItemDetail = reactive<IRuleItemDetail>({
    visiable: false,
    dialogType: 0, // 0  1 
    form: {
      name: "",
      memo: "",
      isLoop: false,
      limitedTime: "",
      startTime: "",
      endTime: "",
      permitType: ""
    }
  });
  //  > 
  const checkEndTime = (rule: any, value: Date, callback: Function) => {
    if (!ruleItemDetail.form.isLoop) {
      callback();
    } else {
      const ymd = dayjs(new Date()).format(YMD); // 
      const hms = dayjs(value).format(Hms); // value 
      const start = dayjs(ruleItemDetail.form.startTime).format(Hms); // 
      const startTime = new Date(`${ymd} ${start}`);
      const endTime = new Date(`${ymd} ${hms}`);
      if (startTime.getTime() > endTime.getTime()) {
        return callback(new Error(""));
      } else {
        callback();
      }
    }
  };
  const ruleItemDetailRule: IRuleItemDetailRule = {
    name: commonRuleItem(""),
    limitedTime: customFuncRule(checkPositiveInteger),
    permitType: commonRuleItem(""),
    startTime: commonRuleItem(""),
    endTime: [commonRuleItem(""), customFuncRule(checkEndTime)]
  };
  // 
  const ruleDisabledDate = computed(() => {
    return function(time: Date) {
      if (ruleItemDetail.form.startTime) {
        return (
          time.getTime() < (ruleItemDetail.form.startTime as Date).getTime()
        );
      }
      return false;
    };
  });
  const alarmInfo = reactive<IAlarmInfo>({
    visible: false,
    dialogType: 0, // 0   1 
    handleVisible: false, // 
    handleAlarmId: "", // id
    handleAlarmIdArr: "", // id
    handleExampleVisible: false,
    form: {
      CreateTime: "",
      IDS: "",
      Id: 0,
      SceneName: "",
      ShowSceneNum: "",
      SourceDbOrDeviceIdS: "",
      SourceName: "",
      SourcePointNum: "",
      SourceType: "",
      alarmContent: "",
      alarmLevel: 0,
      alarmProgress: "",
      dealAccount: "",
      dealMemo: "",
      dealTime: "",
      handleContent: "", // 
      handleType: "" // 
    },
    iconStyle: {
      left: "0",
      top: "0"
    },
    alarmTotal: 0, // 
    alarmUnHandle: 0, // 
    rules: {
      handleType: commonRuleItem(""),
      handleContent: commonRuleItem("")
    }
  });
  const alarmListInfo = reactive<IAlarmListInfo>({
    tableVisible: false,
    params: {
      curpage: 1,
      size: 10
    },
    loading: false,
    allData: [],
    tableData: [],
    total: 0,
    ShowSceneNum: "",
    SourceType: "",
    alarmLevel: "",
    sTime: "",
    eTime: "",
    timeRange: "",
    alarmProgress: "",
    handleIds: [] // id
  });
  const allGroup: IShapeGroup = {
    cameraGroup: new Konva.Group({ name: "cameraGroup" }),
    basestationGroup: new Konva.Group({ name: "basestationGroup" }),
    personGroup: new Konva.Group({ name: "personGroup" }),
    fenceGroup: new Konva.Group({ name: "fenceGroup" }),
    locateBubbleGroup: new Konva.Group({ name: "locateBubbleGroup" }),
    alarmGroup: new Konva.Group({ name: "alarmGroup" })
  };
  const activeTarget = reactive<IActiveTarget>({
    attrs: { x: 0, y: 0 } // 
  });
  const basestation: IListData<TVectorgraph> = {
    all: [],
    currentMap: []
  };
  const bubble: IListData<TVectorgraph> = {
    all: [],
    currentMap: []
  };
  const camera: IListData<TLayerItem> = {
    all: [],
    currentMap: []
  };
  const area: IListData<IAreaItem> = {
    all: [],
    currentMap: []
  };
  const person = reactive<IListData<IPersonArrItem>>({
    all: [],
    currentMap: []
  });
  const cacheAlarmObj: { [key: string]: Circle } = {};
  const cacheBasestationObj: ICacheImage = {};
  const cacheCameraObj: ICacheImage = {};
  const layerPerson = ref<ITagListItem[]>([]);
  const layerPosition = shallowReactive<IStyleLeftTop>({
    left: 0,
    top: 0
  });
  const cacheBubbleObj: ICacheBubble = {};
  const cacheAreaObj: ICachePerson = {};
  const cachePersonObj: ICachePerson = {};
  const transferProps = {
    label: "dname",
    key: "dids"
  };
  const allLayer = ref<TLayerItem[]>([]);
  const controlButtonList = reactive([
    {
      label: "",
      show: true,
      code: "basestation"
    },
    {
      label: "",
      show: true,
      code: "camera"
    },
    {
      label: "",
      show: visibleObj.map,
      code: "map"
    },
    {
      label: "",
      show: visibleObj.nav,
      code: "nav"
    }
  ]);
  const wsAlarmType = {
    getAlarm: 110,
    cancelAlarm: 111
  };
  const alarmType = ref<ISelectOption[]>([]);
  const alarmForm = reactive({
    type: ""
  });
  return {
    router,
    wsUrl,
    mapObj,
    mapListRef,
    mapIdRef,
    scaleBoundary,
    state,
    status,
    showMenu,
    mapNameRef,
    warningWidth,
    contextmenuTypeRef,
    contxtmenuPosition,
    contextmenuList,
    detailsForm,
    visibleObj,
    monitorListInfo,
    drawerInfo,
    fenceInfo,
    fenceListInfo,
    drawFenceInfo,
    ruleListInfo,
    ruleFormRef,
    ruleItemDetail,
    ruleItemDetailRule,
    ruleDisabledDate,
    alarmInfo,
    alarmListInfo,
    allGroup,
    activeTarget,
    basestation,
    bubble,
    camera,
    area,
    person,
    cacheAlarmObj,
    cacheBasestationObj,
    cacheCameraObj,
    layerPerson,
    layerPosition,
    cacheBubbleObj,
    cacheAreaObj,
    cachePersonObj,
    transferProps,
    allLayer,
    controlButtonList,
    wsAlarmType,
    alarmType,
    alarmForm
  };
}
