<template>
  <div class="right-menu">
    <!-- <header-search id="header-search" class="right-menu-item" /> -->

    <screenfull id="screenfull" class="right-menu-item hover-effect" />

    <el-dropdown
      class="avatar-container right-menu-item hover-effect"
      trigger="click"
    >
      <div class="avatar-wrapper">
        <i class="el-icon-user-solid user-avatar" />
        <i class="el-icon-caret-bottom" />
      </div>
      <template #dropdown>
        <el-dropdown-menu>
          <router-link to="/3d">
            <el-dropdown-item>3d</el-dropdown-item>
          </router-link>

          <el-dropdown-item divided @click="logOut">
            <span style="display: block"></span>
          </el-dropdown-item>
        </el-dropdown-menu>
      </template>
    </el-dropdown>
  </div>
</template>

<script lang="ts">
import Screenfull from "@/components/Screenfull/index.vue";
import { logout } from "@/utils/auth";
import { defineComponent } from "vue";
import { useRouter } from "vue-router";

export default defineComponent({
  name: "HeaderBar",
  components: {
    Screenfull
  },
  setup() {
    const router = useRouter();
    const logOut = () => logout(router);

    return {
      logOut
    };
  }
});
</script>

<style lang="scss" scoped>
.right-menu {
  float: right;
  height: 100%;
  margin-right: 20px;
  line-height: 60px;

  &:focus {
    outline: none;
  }

  .right-menu-item {
    display: inline-block;
    padding: 0 8px;
    height: 100%;
    font-size: 18px;
    color: #fff;
    vertical-align: text-bottom;

    &.hover-effect {
      cursor: pointer;
      transition: background 0.3s;

      &:hover {
        background: rgba(0, 0, 0, 0.025);
      }
    }
  }
}
</style>

<style lang="scss">
.avatar-container {
  margin-right: 30px;
  height: 60px;
  line-height: 60px;
  transform: translateY(-18px);
  text-align: center;
  padding: 0 8px;

  .avatar-wrapper {
    display: flex;
    align-items: center;
    height: 100%;
    position: relative;

    .user-avatar {
      cursor: pointer;
      font-size: 20px;
    }

    .el-icon-caret-bottom {
      cursor: pointer;
      position: absolute;
      right: -20px;
      bottom: 20px;
      font-size: 12px;
    }
  }
  &.el-dropdown {
    color: #fff;
  }
}
</style>
