/**
 * @description:
 * @param {string} type
 * @return {*}
 */
export const getFormTitle = (type: string) => {
  const title = {
    [TYPES.camera]: "",
    [TYPES.basestation]: "",
    [TYPES.labelList]: "",
    [TYPES.person]: ""
  };
  return title[type];
};
